import { _decorator, AudioClip, Event, AudioSource } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('CustomAudioClip')
export class CustomAudioClip {
    @property
    isMusic: boolean = false;
    @property
    isEffect: boolean = false;
    @property
    destroyedOnSwitchMode: boolean = false;
    @property
    soundId = ''
    @property
    maxCount = 1;
    @property
    ignoreCheckMaxChannel: boolean = false;
    @property({ type: AudioClip })
    audioFile: AudioClip = null;

    @property({ type: AudioSource, visible: false })
    audioSource: AudioSource = null;

    audioSourcePool: AudioSource[] = [];
}

@ccclass('CustomEvent')
export class CustomEvent extends Event {
    constructor(name: string, bubbles?: boolean, details?: any) {
        super(name, bubbles);
        this.details = details;
    }
    public details: any = null;
}

export type HowlerData = { clip: any /*Howl*/, id?: number };

