
import { _decorator, Node, Vec3, UITransform, ParticleSystem2D, tween } from 'cc';
import { GameObject } from '../core/GameObject';
import { PoolFactory } from './PoolFactory';
import { SlotGameMode } from './CustomEnum';
const { ccclass, property, requireComponent } = _decorator;

@ccclass('MovingParticleControl')
@requireComponent(UITransform)
export class MovingParticleControl extends GameObject {
    @property({type:SlotGameMode})
    currentGameMode : SlotGameMode = SlotGameMode.NormalGame;
    @property({type:Node})
    destination: Node = null;
    @property
    particleMovingPrefabName:string = '';
    @property
    particleExplodingPrefabName:string = '';

    protected poolFactory:PoolFactory = null;
    protected uiTransform:UITransform = null;
    onEnable(){
        if(this.eventManager){
            this.eventManager.on("MOVE_PARTICLES", this.moveParticle, this);
            this.eventManager.on('CLEAR_PARTICLES', this.clearParticles, this);
        }
        if(!this.poolFactory){
            this.poolFactory = this.mainDirector.poolFactory;
        }
        if(!this.uiTransform) this.uiTransform = this.node.getComponent(UITransform);
    }

    onDisable(){
        if(this.eventManager){
            this.eventManager.off("MOVE_PARTICLES", this.moveParticle, this);
            this.eventManager.off('CLEAR_PARTICLES', this.clearParticles, this);
        }
    }

    moveParticle(gameMode:SlotGameMode, fromWPos:Vec3, movingTime:number, callback:Function = null){
        if(gameMode === this.currentGameMode){
            const fLPos = this.uiTransform.convertToNodeSpaceAR(fromWPos);
            const movingpParticle = this.poolFactory.getObject(this.particleMovingPrefabName);
            const explodingParticle = this.poolFactory.getObject(this.particleExplodingPrefabName);
            if(movingpParticle){
                movingpParticle.parent = this.node;
                movingpParticle.active = true;
                movingpParticle.setPosition(fLPos);
                movingpParticle.getComponent(ParticleSystem2D).resetSystem();

                if(explodingParticle){
                    explodingParticle.parent = this.node;
                    explodingParticle.active = true;
                    explodingParticle.setPosition(this.destination.getPosition());
                    explodingParticle.getComponent(ParticleSystem2D).stopSystem();
                    explodingParticle.active = false;
                }
                
                const movingTween = movingpParticle["particleMoveTween"];
                if(movingTween) movingTween.stop();

                this.soundPlayer && this.soundPlayer.playSfx('PARTICAL_FLY');
                movingpParticle["particleMoveTween"] = tween(movingpParticle)
                .to(movingTime, {position: this.destination.getPosition()})
                .call(()=>{
                    this.soundPlayer && this.soundPlayer.playSfx('PARTICAL_HIT');
                    movingpParticle.getComponent(ParticleSystem2D).stopSystem();
                    if(explodingParticle){
                        explodingParticle.active = true;
                        explodingParticle.getComponent(ParticleSystem2D).resetSystem();
                    }
                    
                    callback&&callback();
                })
                .delay(1)
                .call(()=>{
                    movingpParticle.active = false;
                    this.poolFactory.removeObject(movingpParticle);
                    movingpParticle["particleMoveTween"] = null;

                    if(explodingParticle){
                        explodingParticle.getComponent(ParticleSystem2D).stopSystem();
                        explodingParticle.active = false;
                        this.poolFactory.removeObject(explodingParticle);
                    }
                });
                movingpParticle["particleMoveTween"].start();
            }
        }
    }

    clearParticles(){
        const paricles = this.node.children;
        for(let i = 0; i<paricles.length; i++){
            const particle = paricles[i];
            if(particle){
                const movingTween = particle["particleMoveTween"];
                if(movingTween) movingTween.stop();
                particle.getComponent(ParticleSystem2D).stopSystem();
                this.poolFactory.removeObject(particle);
            }
        }
        this.node.removeAllChildren();
    }
}