
import { _decorator, Label, Button, tween, CCFloat } from 'cc';
const { ccclass, property } = _decorator;
import {SlotUIScene} from '../component/SlotUIScene';
import {formatMoney} from '../../../cc-share/common/utils'

@ccclass('TotalWinPanel')
export class TotalWinPanel extends SlotUIScene {

    canClose : boolean = false;

    @property({type: Label})
    winAmount:Label = null;

    @property({type: Label})
    contentText:Label = null;

    @property({type: Button})
    closeBtn:Button = null;

    @property({type : CCFloat})
    showingTime = 3.5;

    enter(){
        this.canClose = false;
        const {winAmount, freeGameTotal} = this.content;
        this.winAmount.getComponent(Label).string = formatMoney(winAmount);

        if (this.contentText) {
            this.contentText.string = freeGameTotal > 0 ? 'Vi '+freeGameTotal+' lt quay min ph' : "";
        }
        this.playSoundTotalWin();
        tween(this.node)
            .delay(0.5)
            .call(()=>{
                this.canClose = true;
                this.closeBtn.interactable = true;
            })
            .delay(this.showingTime)
            .call(()=>{
                this.close();
            })
            .start();
    };

    close() {
        if (!this.canClose) return;
        this.canClose = false;
        this.closeBtn.interactable = false;
        this.fadeOut(this.node, 0.5, {
            onComplete: () => {
                this.callback && this.callback();
                this.callback = null;
                this.stopSoundTotalWin();
                this.exit();
            }
        })
    }

    skip(){
        this.close();
    }

    playSoundTotalWin(){
        this.soundPlayer && this.soundPlayer.playSfx("TOTAL_WIN");
    }
    stopSoundTotalWin(){
        this.soundPlayer && this.soundPlayer.stopSfx("TOTAL_WIN");
    }
}
