import { randRange } from "../../../cc-share/common/utils";

function processStackData(matrix, stackWilds = ["9;0", "9;0", "0;B3", "9;0", "9;0"], nudges = [6,6,6,6,6]) {
    const stackFormats = [];
    for (let col = 0; col < stackWilds.length; col++) {
        let type = stackWilds[col].split(";")[1];
        let isFake = Math.random() > 0.5;
        if (type === "0" && isFake) {
            stackFormats.push(getFakeStackData());
            continue;
        }
        const nudge = nudges[col];
        const nudgeStep = (6 - nudge) % 4;
        const lastIndex = parseInt(stackWilds[col].split(";")[0]);
        const size = getStackSize(type);
        const gongRow = matrix[col].indexOf("L");
        let firstIndex = lastIndex - nudgeStep;
        let format: any = { lastIndex, firstIndex, type, nudgeStep, gongRow, size, nudge };
        format.step = getStepToAddWild(format);
        stackFormats.push(format);
    }
    return stackFormats;
}

function getStackSize(type) {
    switch (type) {
        case "C": return 7;
        case "A1": case "B1": return 6;
        case "A2": case "B2": return 5;
        case "A3": case "B3": return 4;
    }
    return 0;
}

function getGongIndex(stackType) {
    switch (stackType) {
        case "A1": case "A2": case "A3": case "C": return 4;
        case "B1": return 3;
        case "B2": return 2;
        case "B3": return 1;
    }
    return 0;
}

function getStepToAddWild(format) {
    const { firstIndex, size, gongRow, type, nudgeStep } = format;
    const gongIndex = getGongIndex(type);

    if (gongRow > -1) return gongRow + gongIndex - nudgeStep;
    if (firstIndex === 9) return null;
    if (firstIndex > 0) return size + firstIndex;
    if (firstIndex < 0) return 3 + firstIndex;

    if (firstIndex === 0) {
        if (type.includes("B")) return size;
        if (type.includes("A")) return 3;
        if (type === "C") {
            const random = Math.random();
            return random > 0.5 ? 3 : size;
        }
    }
}

export function getFakeStackData() {
    const type = "FAKE", size = 4;
    const firstIndex = randRange(3, 6);
    const step = size + firstIndex;
    const lastIndex = firstIndex;
    return { type, size, step, firstIndex, lastIndex };
}


export {
    processStackData
}
