
import { _decorator, Node, Sprite } from 'cc';
import { JackpotType } from '../CustomType';
const { ccclass, property } = _decorator;

import { SlotBaseCellHistory } from './SlotBaseCellHistory'

@ccclass('SlotJackpotCellHistory')
export class SlotJackpotCellHistory extends SlotBaseCellHistory {

    @property({ type: Node })
    jackpotType = null;
    @property({ type: [JackpotType] })
    jackpotList = [];
    @property({ type: Node })
    iconWalletNormal = null;
    @property({ type: Node })
    iconWalletPromotion = null;

    jackpotData: any;

    onLoad() {
        super.onLoad();
    }

    onShowJackpotDetail() {
        if (this.eventManager) this.eventManager.emit('SHOW_JACKPOT_HISTORY_DETAIL', this.jackpotData);
        if (this.soundPlayer) this.soundPlayer.playSFXClick();
    }

    updateData(data) {
        const { exDt: extendData } = data;
        super.updateData(data);
        this.jackpotData = data;
        if (this.jackpotType) {
            const imageJP = this.findJackpotStaticData(data.jpType)
            if (imageJP) {
                this.jackpotType.getComponentInChildren(Sprite).spriteFrame = imageJP.image;
            }
        }
        if (extendData && this.dataStore.getWalletTypes().length > 1) {
            const { wo: walletOption } = extendData;
            if (this.iconWalletNormal && this.iconWalletPromotion) {
                if (typeof walletOption === "undefined") {
                    this.iconWalletNormal.active = false;
                    this.iconWalletPromotion.active = false;
                } else {
                    const isWalletNormal = walletOption === 0;
                    this.iconWalletNormal.active = isWalletNormal;
                    this.iconWalletPromotion.active = !isWalletNormal;
                }
            }
        }
    }

    findJackpotStaticData(jackpotType) {
        for (let i = 0; i < this.jackpotList.length; i++) {
            if (this.jackpotList[i].jpName == jackpotType)
                return this.jackpotList[i];
        }
        return null;
    }
}