
import { _decorator, Node, sys, Toggle, SpriteFrame } from 'cc';
import { Turbo } from '../../component/Turbo';
import { TutorialTriggerType } from "../../component/CustomEnum";
const { ccclass, property } = _decorator;


@ccclass('Turbov2')
export class Turbov2 extends Turbo {
    @property({ type: Node })
    button = null;

    @property({ type: SpriteFrame })
    iconSprites: SpriteFrame[] = [];

    @property({ type: SpriteFrame })
    textSprites: SpriteFrame[] = [];

    onLoad() {
        super.onLoad();
        this.node.on('ENABLE_TURBO', this.enableTurbo, this);
        (this.node as any).loadTuroConfig = this.loadTurboConfig.bind(this);
    }
    loadTurboConfig() {
        const gameId = this.config.GAME_ID;
        let turboValue = JSON.parse(sys.localStorage.getItem('turboValueWithGame') || '{}');
        let isTurbo = turboValue[gameId] || false;
        let toggle = this.button.getComponent(Toggle)
        toggle.isChecked = isTurbo;
        this.dataStore.setTurbo(isTurbo);
    }
    turboToggle() {
        if (this.soundPlayer)
            this.soundPlayer.playSFXClick();
        let isTurbo = !this.button.getComponent(Toggle).isChecked;
        this.saveTurboConfig(isTurbo);
        this.dataStore.setTurbo(isTurbo);
        this.showToolTip(isTurbo)
        this.guiMgr.onIngameEvent(TutorialTriggerType.TurboClick, "TURBO_CLICK");
    }
    showToolTip(isChecked) {
        if (!this.iconSprites || !this.textSprites) return;
        const index = isChecked ? 0 : 1
        let Frames = [this.iconSprites[index], this.textSprites[index]];
        (this.mainDirector.guiMgr as any).showSmallToolTip(Frames);
    }
    turnOnTurbo() {
        this.button.getComponent(Toggle).check();
        this.node.emit("TURBO_TOGGLE", true);
    }
    turnOffTurbo() {
        this.button.getComponent(Toggle).check();
        this.node.emit("TURBO_TOGGLE", false);
    }
    enableTurbo() {
        this.button.getComponent(Toggle).interactable = true;
    }

}
