import { _decorator } from 'cc';
import { GameObject } from "../../core/GameObject";
import { shuffleArray, removeElementByValue } from "../../../../cc-share/common/utils";
import { SlotSceneType } from '../CustomEnum';
const { ccclass } = _decorator;
@ccclass('GambleWriter')
export class GambleWriter extends GameObject {

    currentPick: number = null;
    allValues: number[] = [0, 0, 2, 2];

    onLoad() {
        this.node['writer'] = this;
    }

    makeScriptGambleGameStart() {
        return [
            { command: "_gambleGameStart" }
        ];
    }

    makeScriptGambleGameClick(boxIndex: number) {
        this.currentPick = boxIndex;
        return [
            {
                command: '_clearWinAmount'
            },
            {
                command: "_sendRequestPlayGamble",
                data: { openCell: boxIndex }
            }
        ];
    }

    makeScriptResultReceive() {
        let listScript = [];
        listScript.push({
            command: "_showResult",
        });
        return listScript;
    }

    makeScriptShowResult() {
        const listScript = [];
        const { winAmountPS, gambleRemain } = this.dataStore.playSession;

        const gambleValue = winAmountPS > 0 ? 2 : 0;

        listScript.push({
            command: "_openPickedBox",
            data: { index: this.currentPick, value: gambleValue }
        });

        let cloneValues = this.allValues.slice(); // copy
        let remainValues = shuffleArray(removeElementByValue(cloneValues, gambleValue));
        listScript.push({
            command: "_setRemainValues",
            data: remainValues
        });
        listScript.push({
            command: "_openAllBoxes",
        });

        if (winAmountPS > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: 300 }
            });
        }

        listScript.push({
            command: "_showCutscene",
            data: {
                name: SlotSceneType.TotalWinPanel,
                content: {
                    winAmount: winAmountPS,
                }
            }
        });

        if (gambleRemain > 0) {
            listScript.push({
                command: "_gambleGameRestart"
            });
            return listScript;
        } else {
            listScript.push({
                command: "_enableGambleButton",
                data: false
            });
            listScript.push({
                command: "_gameExit",
            });
            return listScript;
        }
    }


}

