
import { _decorator, Component, Node, tween } from 'cc';
import { setOpacity } from '../../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('RotateIcon')
export class RotateIcon extends Component {
    @property(Node)
    targetNode: Node;
    @property
    direction: number = 1;
    @property
    speed: number = 1;

    protected _onHover: () => void = null;
    protected _onMouseOut: () => void = null;
    protected tweenRotate: any = null;

    onLoad() {
        this._onHover = this.onHover.bind(this);
        this._onMouseOut = this.onMouseOut.bind(this);
        if (this.targetNode) {
            this.targetNode.on(Node.EventType.MOUSE_ENTER, this._onHover);
            this.targetNode.on(Node.EventType.MOUSE_LEAVE, this._onMouseOut);
            this.targetNode.on("BUTTON_SPIN_SHOW", this.showIcon, this);
            this.targetNode.on("BUTTON_SPIN_HIDE", this.hideIcon, this);
        }
    }

    start() {
        this.rotateIcon();
    }

    showIcon() {
        setOpacity(this.node, 255);
    }

    hideIcon() {
        setOpacity(this.node, 0);
    }

    onHover() {
        this.rotateIcon(1);
    }

    onMouseOut() {
        this.rotateIcon();
    }

    rotateIcon(timeStep = 2) {
        timeStep /= this.speed;
        this.tweenRotate && this.tweenRotate.stop();
        this.tweenRotate = tween(this.node);
        this.tweenRotate
            .by(timeStep, { angle: -360 * this.direction })
            .repeatForever()
            .start();
    }
    onEnable(){
        this.rotateIcon();
    }
    onDestroy() {
        this.tweenRotate && this.tweenRotate.stop();
    }
}
