
import { _decorator, Node, SpriteFrame, PageView, Button, Sprite, Mask } from 'cc';
import EventNames from '../core/EventNames';
import { SlotUIScene } from './SlotUIScene'
const { ccclass, property } = _decorator;

@ccclass('Info')
export class Info extends SlotUIScene {

    @property({type: Button})
    public nextBtn: Button;
    
    @property({type: Button})
    public preBtn: Button;

    @property({type: Node})
    public infoTitle: Node;

    @property({type: SpriteFrame})
    public titles = [];

    @property({type: PageView})
    public pageView: PageView;

    @property
    public isCircular: boolean = false;

    curInfoID : any = 0;

    maskComponent = null;

    viewNode = null;

    onLoad() {
        globalThis.info = this;
        super.onLoad();
        this.node.on("NEXT_GAME_INFO",this.next,this);
        this.node.on("PREVIOUS_GAME_INFO",this.previous,this);
        this.pageView.node.on('page-turning', this.pageViewEvent, this);
        this.activeButtons(this.curInfoID);
        this.preBtn.interactable = false;        
        this.maskComponent = this.pageView.getComponentInChildren(Mask);
        this.viewNode = this.maskComponent.node;
    };

    onEnable() {
        this.scheduleOnce(() => {
            this.pageView.node.setScale(1,1,1);
        },0.01);
    }

    enter() {
        this.resetInfo();
    }

    pageViewEvent() {
        let newIndex = this.pageView.getCurrentPageIndex();
        if (Math.abs(newIndex - this.curInfoID) !== 1) {
            this.pageView.scrollToPage(this.curInfoID, 0.1);
            return;
        }
        this.curInfoID = newIndex;
        this.activeButtons(this.curInfoID);
    };

    next() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.curInfoID++;
        this.activeButtons(this.curInfoID);
        this.pageView.scrollToPage(this.curInfoID);
        this.eventManager.emit(EventNames.OPEN_INFO, this.curInfoID);
    };

    previous() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.curInfoID--;
        this.activeButtons(this.curInfoID);
        this.pageView.scrollToPage(this.curInfoID);
        this.eventManager.emit(EventNames.OPEN_INFO, this.curInfoID);
    };

    activeButtons(id) {
        const { nextBtn, preBtn, infoTitle } = this;
        
        const totalInfo = this.pageView.content.children.length;
        if (this.isCircular) {
            nextBtn.interactable = true;
            preBtn.interactable = true;
            if (id > totalInfo - 1) {
                id = 0;
            }
            if (id < 0) {
                id = totalInfo - 1;
            }
        } else {
            if (id >= totalInfo - 1) {
                id = totalInfo - 1;
                nextBtn.interactable = false;
            } else {
                nextBtn.interactable = true;
            }

            if (id <= 0) {
                id = 0;
                preBtn.interactable = false;
            } else {
                preBtn.interactable = true;
            }
        }
        this.curInfoID = id;
        if (infoTitle) {
            if (infoTitle.getComponent(Sprite)) {
                infoTitle.getComponent(Sprite).spriteFrame = this.titles[this.curInfoID];
            }
        }
    };

    resetInfo(){ 
        this.pageView.scrollToPage(0, 0);
        this.curInfoID = 0;
        this.eventManager.emit(EventNames.OPEN_INFO, this.curInfoID);
        this.activeButtons(this.curInfoID);
    };

    onClose() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.exit();
    };
}

