
import { _decorator, Node, Label, ParticleSystem2D, SpriteFrame, Vec3, Sprite, tween, sp, Event } from 'cc';
const { ccclass, property, integer } = _decorator;
import { SlotUIScene } from "./../component/SlotUIScene";
import {formatMoney, fadeIn} from './../../../cc-share/common/utils';
import { PerformanceLevel, WinEffectLevel } from "../component/CustomEnum";
import EventNames from '../core/EventNames';

@ccclass('WinEffectv2')
export class WinEffectv2 extends SlotUIScene {
    isUpdating = false;
    speedUp = false;
    skippable = false;
    callback = null;
    label: any
    currentValue: number = 0
    currentTitle: number = 0;
    megaWinAmount: number = 0;
    superWinAmount: number = 0;

    nodeTween: any;
    winAmountTween: any;
    titleTween: any;
    content: any;
    canFinish: boolean = false;

    @property({ type: Node })
    public winAmount: Node | null = null;
    @property({ type: Node })
    public overlayNode: Node | null = null;
    @property({ type: Node })
    public title: Node | null = null;
    @property({ type: Node })
    public winInfo: Node | null = null;
    @property({ type: Node })
    public coinsEffect: Node | null = null;
    @property({ type: ParticleSystem2D })
    public fastParticle: ParticleSystem2D | null = null;
    @property({ type: SpriteFrame })
    public titleFrame: Array<SpriteFrame> | null = [];
    @property({ type: Node })
    public winAnim: Node | null = null;

    @integer public bigWinDur = 4;
    @integer public megaWinDur = 6;
    @integer public superWinDur = 6;
    @integer public megaRate = 20;
    @integer public superRate = 30;
    @integer public delayShowTime = 1;
    @integer public delayHideTime = 1;
    @integer public hideTime = 0.5;
    @property public bigWinSound: string = 'BIG_WIN';
    @property public megaWinSound: string = 'MEGA_WIN';
    @property public superWinSound: string = 'SUPER_WIN';
    @property public bigWinAnimIdle: string = '';
    @property public bigWinAnimStart: string = '';
    @property public megaWinAnimIdle: string = '';
    @property public megaWinAnimStart: string = '';
    @property public superWinAnimIdle: string = '';
    @property public superWinAnimStart: string = '';

    animationLevel:number = 2;
    winAnimSkeleton: any = null;
    isSkipEffect: boolean = false;
    winSound: string = 'BIG_WIN';

    onLoad() {
        super.onLoad();
        this.label = this.winAmount.getComponentInChildren(Label);
        if (this.winAnim) this.winAnimSkeleton = this.winAnim.getComponent(sp.Skeleton);
    }

    enter() {
        this.mainDirector.currentGameMode.emit('SPIN_DISABLE');
        this.eventManager.emit(EventNames.BIGWIN_START);
        const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
        this.animationLevel = this.config[performanceLvl].BIG_WIN_ANIMATION;
        if (this.coinsEffect) this.setOpacity(this.coinsEffect, 255);
        this.showEffectWin();
        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_OPEN_BIGWIN', true);
            updatedEvent.winAmount = this.content.winAmount;
            updatedEvent.winType = this.getWinType();
            updatedEvent.gameId = this.config.GAME_ID;
            this.node.dispatchEvent(updatedEvent);
        }
    }

    showEffectWin() {
        if (this.overlayNode) this.overlayNode.active = true;

        this.setOpacity(this.winInfo, 1);
        fadeIn(this.winInfo, this.delayShowTime);

        this.winAmountTween = tween(this.winAmount).repeatForever(
            tween()
                .to(0.2, { scale: new Vec3(1.2, 1.2, 1.2) })
                .to(0.2, { scale: new Vec3(1, 1, 1) })
        ).start();

        if (this.title) {
            this.titleTween = tween(this.title).repeatForever(
                tween()
                    .to(0.5, { scale: new Vec3(1.2, 1.2, 1.2) })
                    .to(0.5, { scale: new Vec3(1, 1, 1) })
            ).start();
        }

        this.initValue();
        this.playSoundStart();
        
        tween(this.node)
            .delay(this.delayShowTime)
            .call(() => {
                this.startParticle();
                this.playCoinSound();
                this.startUpdateWinAmount('BIG_WIN');
                this.setAnimation(this.bigWinAnimStart, this.bigWinAnimIdle);
            })
            .start();
    }

    initValue() {
        this.canFinish = false;
        this.winInfo.setScale(1, 1, 1);
        this.currentValue = 0;
        this.currentTitle = 0;
        this.label.string = '';
        if (this.title) this.title.getComponent(Sprite).spriteFrame = this.titleFrame[0];

        const { currentBetData, bigWinConfig } = this.content;
        let winRates = [];
        if (bigWinConfig) {
            winRates = bigWinConfig.split(',');
            this.megaRate = winRates[WinEffectLevel.MEGA] || this.megaRate;
            this.superRate = winRates[WinEffectLevel.SUPER] || this.superRate;
        }
        this.megaWinAmount = currentBetData * Number(this.megaRate);
        this.superWinAmount = currentBetData * Number(this.superRate);

        this.isUpdating = true;
        this.speedUp = false;
        this.bindQuickShow();
        this.isSkipEffect = this.content.winAmount >= this.superWinAmount * 2;
        this.coinsEffect.emit('RESET_COIN_EFFECT');
    }

    playSoundStart() {
        if (!this.soundPlayer) return;
        this.soundPlayer.stopAllAudio();

        this.winSound = this.bigWinSound;
        if (this.content.winAmount >= this.superWinAmount) {
            if (this.superWinSound) this.winSound = this.superWinSound;
        } else if (this.content.winAmount >= this.megaWinAmount) {
            if (this.megaWinSound) this.winSound = this.megaWinSound;
        }
        this.soundPlayer.playSfx(this.winSound);
    }

    playSoundEnd() {
        if (!this.soundPlayer) return;
        this.soundPlayer.stopAllEffects();
    }

    getWinType(){
        let winType = 'BIG';
        if (this.content.winAmount > this.superWinAmount) {
            winType = 'SUPER';
        }
        else if (this.content.winAmount > this.megaWinAmount) {
            winType = 'MEGA';
        }
        return winType;
    }

    playCoinSound() { }

    bindQuickShow() {
        this.skippable = false;
        tween(this.node)
            .delay(this.hideTime)
            .call(() => {
                this.skippable = true;
            })
            .delay(this.bigWinDur + this.megaWinDur + this.superWinDur)
            .call(() => {
                this.skippable = false;
            })
            .start();
    }

    startParticle() {
        if (!this.coinsEffect) return;
        if (this.animationLevel < Number(PerformanceLevel.Medium)) return;
        this.coinsEffect.emit('SHOW_COIN_EFFECT', 0, this.isSkipEffect);
        this.coinsEffect.emit('START_PARTICLE');
    }

    startUpdateWinAmount(winType) {
        let winAmount = this.content.winAmount;
        let animDuration = 0;
        if (this.canFinish) return;

        if (winType === 'BIG_WIN') {
            animDuration = this.bigWinDur;
            if (this.content.winAmount > this.megaWinAmount) {
                winAmount = this.megaWinAmount;
            } else {
                this.canFinish = true;
            }
        } else if (winType === 'MEGA_WIN') {
            animDuration = this.megaWinDur;
            if (this.content.winAmount > this.superWinAmount) {
                winAmount = this.superWinAmount;
            } else {
                this.canFinish = true;
            }
        } else {
            animDuration = this.superWinDur;
            this.canFinish = true;
        }

        const speedUpEasing = t => t * t;
        this.nodeTween && this.nodeTween.stop();
        this.nodeTween = tween(this);
        this.nodeTween
            .to(animDuration, { currentValue: winAmount }, { easing: speedUpEasing })
            .call(() => {
                if (this.canFinish) this.skippable = false;
            })

        if (this.canFinish) {
            this.nodeTween
                .delay(1)
                .call(() => {
                    this.skippable = false;
                    this.nodeTween = null;
                    this.finish();
                })
        }

        this.nodeTween.start();
    }

    update(dt) {
        if (!this.isUpdating) return;
        this.label.string = formatMoney(this.currentValue);
        if (this.currentValue >= (this.superWinAmount) && this.currentTitle == 1) {
            this.currentTitle = 2;
            if (this.animationLevel >= Number(PerformanceLevel.Medium)) {
                this.coinsEffect.emit('SHOW_COIN_EFFECT', 2, this.isSkipEffect);
            }
            this.setAnimation(this.superWinAnimStart, this.superWinAnimIdle);
            this.changeTitle(2);
            this.startUpdateWinAmount('SUPER_WIN');
        } else if (this.currentValue >= (this.megaWinAmount) && this.currentTitle == 0) {
            this.currentTitle = 1;
            if (this.animationLevel >= Number(PerformanceLevel.Medium)) {
                this.coinsEffect.emit('SHOW_COIN_EFFECT', 1, this.isSkipEffect);
            }
            this.setAnimation(this.megaWinAnimStart, this.megaWinAnimIdle);
            this.changeTitle(1);
            this.startUpdateWinAmount('MEGA_WIN');
        }
    }

    changeTitle(index: number) {
        this.currentTitle = index;
        if (!this.title || !this.titleFrame || !this.titleFrame.length) return;

        this.titleTween = tween(this.title)
            .to(0.2, { scale: new Vec3(1.5, 1.5, 1.5) })
            .call(() => {
                this.title.getComponent(Sprite).spriteFrame = this.titleFrame[index];
            })
            .to(0.2, { scale: new Vec3(1, 1, 1) })
            .call(() => {
                tween(this.title).repeatForever(
                    tween()
                        .to(0.5, { scale: new Vec3(1.2, 1.2, 1.2) })
                        .to(0.5, { scale: new Vec3(1, 1, 1) })
                ).start();
            })
            .start();
    }

    onClick() {
        if (!this.isUpdating || this.speedUp || this.canFinish || !this.skippable) return;
        this.canFinish = true;
        this.speedUp = true;
        this.winAmountTween && this.winAmountTween.stop();
        const scaleTime = 0.13;
        tween(this.winAmount).repeatForever(
            tween()
                .to(scaleTime, { scale: new Vec3(1.2, 1.2, 1.2) })
                .to(scaleTime, { scale: new Vec3(1, 1, 1) })
        ).start();

        this.nodeTween && this.nodeTween.stop();
        this.currentValue = this.content.winAmount;
        this.nodeTween = tween(this)
        this.nodeTween
            .to(1, { currentValue: this.content.winAmount })
            .call(() => {
                this.nodeTween = null;
                this.finish();
            })
            .start();
    }

    stopParticle() {
        if(!this.coinsEffect) return;
        if(this.animationLevel < Number(PerformanceLevel.Medium)) return;
        this.coinsEffect.emit('HIDE_COIN_EFFECT');
    }

    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmountTween && this.winAmountTween.stop();
        this.titleTween && this.titleTween.stop();
        this.nodeTween && this.nodeTween.stop();

        this.stopParticle();
        this.playSoundEnd();

        tween(this.winInfo)
            .delay(this.delayHideTime)
            .call(() => {
                this.fadeTo(this.winInfo, this.hideTime, 1);
                if (this.coinsEffect) this.setOpacity(this.coinsEffect, 0);
            })
            .delay(this.hideTime)
            .call(()=>{
                this.label.string = '';
                this.eventManager.emit(EventNames.BIGWIN_END);
                this.exit();
                this.soundPlayer && this.soundPlayer.stopAllAudio();
                this.soundPlayer && this.soundPlayer.playMainBGM();
            })
            .start();
    }

    onDisable() {
        if (this.nodeTween) {
            this.nodeTween.stop();
            this.nodeTween = null;
        }
    }

    setAnimation(current: string, next: string) {
        if (!current || !next || !this.winAnimSkeleton) return;
        this.winAnimSkeleton.setAnimation(0, current, false);
        this.winAnimSkeleton.setCompleteListener((listener) => {
            this.winAnimSkeleton.setCompleteListener(() => {});
            if (listener.animation.name === current) {
                this.winAnimSkeleton.setAnimation(0, next, true);
            };
        });
    }

    exit() {
        this.mainDirector.currentGameMode.emit('SPIN_ENABLE');
        this.eventManager.emit(EventNames.BIGWIN_END);
        super.exit();
    }

}
