
import { _decorator, Node } from 'cc';
const { ccclass, property } = _decorator;
import { TweenViewComponent } from './TweenViewComponent';

@ccclass('BottomBarMgr')
export class BottomBarMgr extends TweenViewComponent {
    @property({ type: Node })
    movingGroup = null;

    show(onStartCB = null, onCompleteCB = null) {
        if (this.movingGroup) {
            this.movingGroup.show();
        }
        super.show(onStartCB, onCompleteCB);
    }

    hide(onStartCB = null, onCompleteCB = null) {
        if (this.movingGroup) {
            this.movingGroup.hide();
        }
        super.show(onStartCB, onCompleteCB);
    }
}
