
import { _decorator, Node, Label, Event, tween, error, Vec3, UITransform, Vec2 } from 'cc';
import { script, BaseDirector } from "../../core/BaseDirector";
import { SlotGameMode, TutorialTriggerType } from "../CustomEnum";
import { PoolFactory } from '../PoolFactory';
import{BonusValueControl} from './BonusValueControl';
const { ccclass, property } = _decorator;

@ccclass('MiniDirector')
export class MiniDirector extends BaseDirector {

    @property(Node)
    table: Node;

    @property(Node)
    valueLabelHolder: Node = null;

    @property
    labelPrefabName:string = '';

    @property
    labelOffset:Vec2 = new Vec2(0,0);

    @property(Node)
    winAmount: Node;

    @property({type: Node})
    countDownNode = null;

    @property
    maxOpenBoxes: number = 3;

    @property
    timerCount: number = 15;
    @property
    defaultValue: number = 0;

    writer: any = null;
    isWaitingResult: boolean = false;
    isAutoTrigger: boolean = false;
    callbackStateUpdate: any = null;
    _callbackAutoTrigger: any = null;
    currentPick: number;
    listOpenedIndexes: number[] = [];
    repeatCountDown: any = null;
    _count: number = 20;
    _delayTimeCallback: any = null;
    countDownText = null;
    protected _labelHolderTransform:UITransform = null;
    protected _labelObjects:Node[] = [];
    protected _poolFactory:PoolFactory = null;

    onLoad() {
        super.onLoad();
        this.setOpacity(this.node, 0);
        this.node.on("GAME_UPDATE", this.stateUpdate, this);
        this.node.on("GAME_ENTER", this.ready, this);
        this.node.on("GAME_INIT", this.init, this);
        this.node.on('CLICK_ITEM', this.onClickItem, this);
        this.countDownText = this.countDownNode.getComponentInChildren(Label);
        if(this.valueLabelHolder){
            this._labelHolderTransform = this.valueLabelHolder.getComponent(UITransform);
            if(!this._labelHolderTransform) this._labelHolderTransform = this.valueLabelHolder.addComponent(UITransform);
        }
        
        this._bindProps();
        if(this.mainDirector) this.mainDirector.gameModes[SlotGameMode.BonusGame] = this.node;
    }

    onEnable(){
        if(this.eventManager){
            this.eventManager.on("SHOW_BONUS_VALUE", this.showBonusValue, this);
        }
        if(!this._poolFactory){
            this._poolFactory = this.mainDirector.poolFactory;
        }
    }

    onDisable(){
        if(this.eventManager){
            this.eventManager.off("SHOW_BONUS_VALUE", this.showBonusValue, this);
        }
    }

    _bindProps() {
        (this.node as any).init = this.init.bind(this);
    }

    /**
     * @receive_data
     * */
    stateUpdate(callback: void) {
        this.isWaitingResult = false;
        this.callbackStateUpdate = callback;
        this.runAction('ResultReceive');
    }

    runCallbackStateUpdate() {
        if (typeof this.callbackStateUpdate == "function") {
            this.callbackStateUpdate();
            this.callbackStateUpdate = null;
        }
    }

    ready(data) {
        if (data) {
            for (let index = 0; index < data.length; index++) {
                if (data[index] !== this.defaultValue)
                    this.listOpenedIndexes.push(index);
            }
            this.table.emit("RESUME_MINI_GAME", data, this.defaultValue);
        }
        this.isWaitingResult = false;
        this.runAction("MiniGameStart");
        this._count = this.timerCount;
        this.soundPlayer && this.soundPlayer.playMainBGM();
        const delayForTrigger = 1;
        this.scheduleOnce(()=>{
            this.guiMgr.onIngameEvent(TutorialTriggerType.EnterBonusGame, "ENTER_GAME_MODE");
        }, delayForTrigger)
    }

    init() {
        this.writer = this.node['writer'];
        this.isWaitingResult = false;
        this.table.emit("INIT_TABLE");
        this.node.active = false;
    }
    onClickItem(event: Event) {
        if (this.isWaitingResult) return;
        event.propagationStopped = true;
        this.stopAutoTrigger();
        this.soundClickItem();
        let box: any = event.target;
        if (this.canOpenBox(box)) {
            const index = box["boxIndex"];
            this.currentPick = index;
            this.listOpenedIndexes.push(index);
            this.runAction("MiniGameClick", this.currentPick);
            box.playAnimClick();
            box.disableClick();
            this.isAutoTrigger = box.isAutoTrigger;
        }
    }

    canOpenBox(box) {
        if (this.isFinishOpen()) return false;
        if (this.listOpenedIndexes.indexOf(box["boxIndex"]) > -1) return false;
        return true;
    }

    isFinishOpen() {
        return this.listOpenedIndexes.length >= this.maxOpenBoxes;
    }

    protected showBonusValue(bonusValue:number, itemWPosition:Vec3, isFast:boolean, isSelected:boolean){
        if(!this.labelPrefabName|| this.labelPrefabName ==='') return;
        for(let i = 0; i<this._labelObjects.length; i++){
            this._labelObjects[i].active = false;
        }
        const lPos:Vec3 = this._labelHolderTransform.convertToNodeSpaceAR(itemWPosition);
        const labelPos = new Vec3(lPos.x + this.labelOffset.x, lPos.y + this.labelOffset.y, lPos.z);
        const valueLabel = this._poolFactory.getObject(this.labelPrefabName);
        if(valueLabel && this.valueLabelHolder){
            valueLabel.parent = this.valueLabelHolder;
            valueLabel.active = true;
            valueLabel.setPosition(labelPos);
            valueLabel.getComponent(BonusValueControl).showValue(bonusValue, this.dataStore.betData.denom, isFast, isSelected);
            this._labelObjects.push(valueLabel);
        }
        for(let i = 0; i<this._labelObjects.length; i++){
            this._labelObjects[i].active = true;
        }
        this.soundPlayer && this.soundPlayer.playSfx("BONUS_PAY");
    }

    protected runAutoTrigger(delay: number) {
        this.stopAutoTrigger();
        this._callbackAutoTrigger = () => {
            this.table.emit("AUTO_OPEN_BOX");
            this._callbackAutoTrigger = null;
        }
        this.scheduleOnce(this._callbackAutoTrigger, delay);
        this.runCountDown(delay);
    }

    protected stopAutoTrigger() {
        this._callbackAutoTrigger && this.unschedule(this._callbackAutoTrigger);
    }

    protected runCountDown(delay: number) {
        if (!this.countDownText) return;
        this.stopCountDown();

        if (delay === this.timerCount) {
            this.repeatCountDown = tween(this);
            this.repeatCountDown
                .repeatForever(
                    tween(this.node)
                        .call(() => {
                            this.setOpacity(this.countDownNode, 255);
                            this.countDownText.string = `H thng s t chn sau: ${this._count}s`;
                        })
                        .delay(1)
                        .call(() => {
                            this._count--;
                            if (this._count <= 0) {
                                this.repeatCountDown.stop();
                                this.setOpacity(this.countDownNode, 0);
                            }
                        })
                )
                .start();
        } else {
            this.setOpacity(this.countDownNode, !this.isAutoTrigger ? 255 : 0);
        }
    }


    stopCountDown() {
        if (this.repeatCountDown) {
            this.repeatCountDown.stop();
            this._count = this.timerCount;
        }
    }

    resetMiniGame() {
        this.stopAutoTrigger();
        this.stopCountDown();
        this.listOpenedIndexes = [];
        this.currentPick = 0;
        this.table.emit("RESET_MINI_TABLE");
        this._count = this.timerCount;
        this.setOpacity(this.countDownNode, 0);
        this.clearValueLabels();
    }

    clearValueLabels(){
        if(!this._poolFactory){
            this._poolFactory = this.mainDirector.poolFactory;
        }
        for(let i = 0; i<this._labelObjects.length; i++){
            const labelObj = this._labelObjects[i];
            if(labelObj){
                labelObj.getComponent(Label).string = '';
                this._poolFactory.removeObject(labelObj);
            }
        }
        this._labelObjects = [];
    }

    /**
     * commands
     */
    _miniGameStart(scripts: script[]) {
        if(this._checkPauseTutorial('pauseBonusGame')){
            this.storeCurrentScripts = '_miniGameStart';
            this.storeNextScripts = {
                scripts,
                data: {},
            };
        }else{
            this.runAutoTrigger(this.timerCount);
            this.executeNextScript(scripts);
        }
    }
    _sendRequestPlayMiniGame(scripts: script[], { openCell }) {
        this.isWaitingResult = true;
        this.node["mainDirector"].gameStateManager.triggerMiniGame(openCell);
        this.executeNextScript(scripts);
    }
    _showResult(script: script[]) {
        this.isWaitingResult = false;
        this.runAction("ShowResult");
        this.executeNextScript(script);
    }

    _openPickedItem(scripts: script[], data) {
        this.table.emit("OPEN_BOX", data, () => {
            this.executeNextScript(scripts);
        });
        if (!this.isFinishOpen()) {
            const time = this.isAutoTrigger ? 0.5 : this.timerCount;
            this.runAutoTrigger(time);
        }
    }
    _openAllItems(scripts: script[], matrix) {
        this.setOpacity(this.countDownNode, 0);
        this.stopAutoTrigger();
        this.table.emit("OPEN_ALL_BOXES", matrix, () => {
            this.executeNextScript(scripts);
        });
        this.stopCountDown();
    }
    _updateWinningAmount(scripts: script[], { winAmount, time }) {
        this.guiMgr.updateWinAmount(winAmount, time / 1000);
        this.executeNextScript(scripts);
    }

    _miniGameRestart(scripts: script[]) {
        this.executeNextScript(scripts);
    };

    _delayTimeScript(script: script[], time) {
        this._delayTimeCallback = () => {
            this.executeNextScript(script);
        };
        this.scheduleOnce(this._delayTimeCallback, time);
    };

    _checkPauseTutorial(flag: string) {
        return this.guiMgr.checkPauseTutorial(flag);
    }

    _showCutscene(script:script[], {name, content}){
        if(this._checkPauseTutorial('pauseFreeGame') || this._checkPauseTutorial('pauseBonusGame')){
            this.storeCurrentScripts = '_showCutscene';
            let data = { name, content };
            this.storeNextScripts = {
                script,
                data
            };
        }else{
            this.guiMgr.showCutScene(name, content, ()=>{
                this.executeNextScript(script);
            });
        }
    }

    _showCutscene_2(script:script[]){
        this.executeNextScript(script);
    }

    _hideCutscene(script:script[], {name} ){
        this.guiMgr.hideCutScene(name);
        this.executeNextScript(script);
    }

    _showUnskippedCutscene(script: script[], { name, content }) {
        this.guiMgr.showCutScene(name, content, () => {
            this.executeNextScript(script);
        });
    };

    _updateValueJP(script, data) {
        this.guiMgr.updateValueJP(data);
        this.executeNextScript(script);
    };

    _pauseUpdateJP(script: script[]) {
        this.guiMgr.pauseJackpot();
        this.executeNextScript(script);
    }

    _resumeUpdateJP(script: script[]) {
        this.guiMgr.resumeJackpot();
        this.executeNextScript(script);
    }

    _gameExit(scripts: script[]) {
        if (scripts.length > 0) {
            error("have tp finish all scripts when exit game");
        }
        this.resetMiniGame();
        this.guiMgr.onIngameEvent(TutorialTriggerType.ExitBonusGame, "EXIT_GAME_MODE");
        (this.node as any).exit(() => {
            this.executeNextScript(scripts);
        });
    }

    soundClickItem(){
        //to do play sound
    }
}
