
import { _decorator, Node, tween, Tween, Label, Vec3, Event } from 'cc';
import { SlotUIScene } from './SlotUIScene';
import {formatMoney} from '../../../cc-share/common/utils';
import { PerformanceLevel, TutorialTriggerType } from './CustomEnum';
import EventNames from '../core/EventNames';

const { ccclass, property } = _decorator;

@ccclass('JackpotWin')
export class JackpotWin extends SlotUIScene {

    label: Label = null;
    currentValue:number = 0;
    currentTitle = 0;
    isUpdating = false;
    speedUp = false;
    skippable = false;
    currentTween: Tween<any> = null;
    tweenWinAmount: Tween<any> = null;
    tweenTitle: Tween<any> = null;
    tweenExit: Tween<any> = null;

    @property({type: Node})
    winAmount = null;

    @property({type: Node})
    title = null;

    @property({type: Node})
    winInfo = null;

    @property({type: Node})
    coinsEffect = null;

    @property
    delayShowTime = 2;

    @property
    hideTime = 0.5;

    @property
    animDuration = 10;

    @property
    extendFinishDelayTime = 15;

    protected animationLevel:number = 2;

    onLoad() {
        super.onLoad();
        let labels = this.winAmount.getComponentsInChildren(Label);
        this.label = labels[labels.length-1];
    }

    enter() {
        this.eventManager.emit(EventNames.JACKPOT_START);

        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_OPEN_JACKPOT', true);
            updatedEvent.winAmount = this.content.winAmount;
            updatedEvent.gameId = this.config.GAME_ID;
            this.node.dispatchEvent(updatedEvent);
        }

        const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
        this.animationLevel = this.config[performanceLvl].JP_WIN_ANIMATION;
        this.playSoundStart();

        this.playTweenWinAmount();

        this.playTweenTitle();

        this.initValue();
        this.startParticle();
        this.startUpdateWinAmount();
    }

    playSoundStart() {
        this.soundPlayer && this.soundPlayer.stopAllAudio();
        this.soundPlayer && this.soundPlayer.playSfx('JACKPOT_START', true);
    }

    playSoundEnd() {
        this.soundPlayer && this.soundPlayer.playSfx('JACKPOT_END');
    }

    initValue() {
        this.winInfo.scale = new Vec3(1,1,1);
        this.currentValue = 0;
        this.currentTitle = 0;
        this.label.string = '';
        this.isUpdating = true;
        this.speedUp = false;
        this.bindQuickShow();
    }

    bindQuickShow() {
        this.skippable = false;
        tween(this.node).delay(this.hideTime).call(()=>{
            this.skippable = true;
        }).start();
    }

    playTweenWinAmount() {
        if (!this.winAmount) return;
        this.tweenWinAmount = tween(this.winAmount).repeatForever(
            tween()
            .to(0.2, {scale: new Vec3(1.2, 1.2, 1.2)})
            .to(0.2, {scale: new Vec3(1, 1, 1)}));
        this.tweenWinAmount.start();
    }

    stopTweenWinAmount() {
        if (this.tweenWinAmount) this.tweenWinAmount.stop();
        if (this.winAmount) {
            this.winAmount.scale = Vec3.ONE;
        }
    }

    playTweenTitle() {
        if (!this.title) return;
        this.tweenTitle = tween(this.title).repeatForever(
            tween()
            .to(0.2, {scale: new Vec3(1.2, 1.2, 1.2)})
            .to(0.2, {scale: new Vec3(1, 1, 1)}));
        this.tweenTitle.start();
    }

    stopTweenTitle() {
        if (this.tweenTitle) this.tweenTitle.stop();
        if (this.title) {
            this.title.scale = Vec3.ONE;
        }
    }

    startParticle() {
        if(!this.coinsEffect) return;
        if(this.animationLevel< Number(PerformanceLevel.Medium)){
            return;
        }
        this.coinsEffect.emit('START_PARTICLE');
        this.coinsEffect.emit('DROP_MONEY');
    }

    startUpdateWinAmount() {
        const halfAmount = 0.5 * Number(this.content.winAmount);
        const extendFinishDelayTime = this.dataStore && this.dataStore.isAutoSpin ? this.extendFinishDelayTime : 0;
        this.currentTween = tween(this);
        this.currentTween
            .to(0.5*this.animDuration, {currentValue: halfAmount})
            .to(0.5*this.animDuration, {currentValue: Number(this.content.winAmount)})
            .delay(extendFinishDelayTime)
            .call(() => {
                if (this.dataStore && this.dataStore.isAutoSpin) {
                    this.playSoundEnd();
                    this.skippable = false;
                    this.currentTween = null;
                    this.finish();
                }
            });
        this.currentTween.start();
    }

    update() {
        if (!this.isUpdating) return;
        this.label.string = formatMoney(this.currentValue);
    }

    onClick() {
        if (!this.isUpdating || this.speedUp) return;
        if (!this.skippable) return;

        this.speedUp = true;


        this.playSoundEnd();

        if (this.currentTween) {
            this.currentTween.stop();
            this.currentTween = null;
        }
        this.currentTween = tween(this);

        this.currentTween
            .to(1, {currentValue: 1*this.content.winAmount})
            .delay(0.1)
            .call(() => {
                this.finish();
            })
            .start();
    }

    stopParticle() {
        if(!this.coinsEffect) return;
        if(this.animationLevel< Number(PerformanceLevel.Medium)){
            return;
        }
        this.coinsEffect.emit('STOP_PARTICLE');
    }

    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.stopParticle();
        this.stopTweenTitle();
        this.stopTweenWinAmount();
        this.tweenExit = tween(this.node)
            .delay(this.delayShowTime)
            .call(() => {
                this.label.string = '';
                this.exit();
                this.soundPlayer && this.soundPlayer.stopAllAudio();
                this.soundPlayer && this.soundPlayer.playMainBGM();
            }).start();
    }

    onDisable() {
        this.currentTween && this.currentTween.stop();
        this.currentTween = null;
        this.tweenTitle && this.tweenTitle.stop();
        this.tweenTitle = null;
        this.tweenWinAmount && this.tweenWinAmount.stop();
        this.tweenWinAmount = null;
        this.tweenExit && this.tweenExit.stop();
        this.tweenExit = null;
    }

    exit(){
        this.soundPlayer && this.soundPlayer.stopAllEffects();
        this.eventManager.emit(EventNames.JACKPOT_END);
        this.guiMgr.onIngameEvent(TutorialTriggerType.OnJackpotCutsceneClose, 'ON_JACKPOT_CUTSCENE_CLOSE');
        super.exit();
    }
}
