
import { _decorator, Component, Color, Label, Tween, tween, Vec3 } from 'cc';
import { formatMoney, formatWalletMoney } from '../../../../cc-share/common/utils';
const { ccclass, property, requireComponent } = _decorator;
@ccclass('BonusValueControl')
@requireComponent(Label)
export class BonusValueControl extends Component {
    @property
    dimColor:Color = new Color(0,0,0,0);
    onLoad(){
        this._label = this.getComponent(Label);
    }

    _label:Label = null;
    _currentCreditValue:number = 0;
    _currentDenomValue:number = 1;
    _appearingTween:Tween<any> = null;
    showValue(creditValue:number, denomRate:number, isFast:boolean = false, isSelected:boolean = false){
        this._currentCreditValue = creditValue;
        this._currentDenomValue = denomRate;
        const money:number = this._currentDenomValue*this._currentCreditValue;
        if(isFast){
            this.node.scale = new Vec3(1,1,1);
            if(this._label){
                if(money>=100000){
                    this._label.string = (formatWalletMoney(money)).toString();
                }else{
                    this._label.string = formatMoney(money);
                }
            }
        }else{
            if(this._label){
                this._label.string = 'X'+creditValue;
            }
            this.node.scale = new Vec3(0.01,0.01,0.01);
            if(this._appearingTween) this._appearingTween.stop();
            this._appearingTween = tween(this.node)
            .to(0.5, {angle:-360, scale:new Vec3(1,1,1)})
            .delay(0.5)
            .call(()=>{
                if(this._label){
                    if(money>=100000){
                        this._label.string = (formatWalletMoney(money)).toString();
                    }else{
                        this._label.string = formatMoney(money);
                    }
                }
                this._appearingTween = null;
            });
            this._appearingTween.start();
        }
        this._label.color = isSelected?Color.WHITE:this.dimColor;

    }

    reset(){
        if(this._label){
            this._label.string = '';
        }
        if(this._appearingTween) this._appearingTween.stop();
        this.node.scale = new Vec3(0.01,0.01,0.01);
        this.node.angle = 0;
    }
}
