
import { _decorator, Node, SpriteFrame, warn, Sprite, Color, Button } from 'cc';
import { GameObject } from '../../core/GameObject';
import { CustomEvent } from '../../component/CustomType';
import { setOpacity } from '../../../../cc-share/common/utils';
const { ccclass, property } = _decorator;


 
@ccclass('AutoSpinPanelController')
export class AutoSpinPanelController extends GameObject {
    @property({type: Node})
    autoSpinButton: Node = null;
    @property({type: SpriteFrame})
    normalFrame: SpriteFrame = null;
    @property({type: SpriteFrame})
    selectedFrame: SpriteFrame = null;
    @property({type: Node})
    numberButtons: Node[] = [];
    @property({type: Node})
    overlay: Node = null;
    spinNumber: any;
    setAutoSpinEvent: any=null;

    onLoad() {
        super.onLoad();
        (this.node as any).showOverlay = this.showOverlay.bind(this);
    }

    showOverlay(isShow = false) {
        this.overlay.active = isShow;
    }

    getSpinNumber(evt, number) {
        if(!evt || !number) {
            warn('Missing event or number of spins');
            return;
        }
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.spinNumber = number;
        this.numberButtons.forEach(it => {
            it.getComponent(Sprite).spriteFrame = this.normalFrame;
            it.getChildByName('Label').getComponent(Sprite).color = new Color().fromHEX('#ffffff');
        });
        evt.target.getComponent(Sprite).spriteFrame = this.selectedFrame;
        evt.target.getChildByName('Label').color = new Color().fromHEX('#f3d598');
        this.autoSpinButton.getComponent(Button).interactable = true;
    }

    startAutoSpinning() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.setAutoSpinEvent = new CustomEvent('SET_AUTO_SPIN_EVENT', true,{spinNumber: this.spinNumber});
        
        this.node.dispatchEvent(this.setAutoSpinEvent);
        this.node.emit('HIDE', 0, ()=>{ setOpacity(this.node,1)});
        this.resetButtonStatus();
        this.overlay.active = false;
    }

    resetButtonStatus() {
        this.autoSpinButton.getComponent(Button).interactable = false;
        this.numberButtons.forEach(it => {
            it.getComponent(Sprite).spriteFrame = this.normalFrame;
            it.getChildByName('Label').getComponent(Sprite).color = new Color().fromHEX('#ffffff');
        });
    }

    clickBtnClose() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.resetButtonStatus();
        this.node.emit('HIDE', 0, ()=>{ setOpacity(this.node,1)});
        this.overlay.active = false;
    }
}


