
import { _decorator, log, error, } from 'cc';
import { GameObject } from "../core/GameObject";
import { SlotGameMode } from '../component/CustomEnum';
const { ccclass, property } = _decorator;

export interface script {
    command: string,
    data: any
}
export interface asyncScript {
    callback: () => {},
    name: string,
    isSkippable: boolean
}

@ccclass('BaseDirector')
export class BaseDirector extends GameObject {
    writer: any = null;
    exitScript: script[];
    scripts: script[];
    listScriptAsync: asyncScript[] = [];
    forceToExitMode: boolean = false;
    storeNextScripts: any = null;
    callBackWhenHide: any = null;
    storeCurrentScripts: any = {
        script: [],
        data: {}
    };
    isResetAsyncScript: boolean = false;

    onLoad() {
        super.onLoad();
        this.node["director"] = this;
        this.node.on("RUN_CONTINUE_SCRIPT", this.runContinueScript, this);
        this.node.on("FORCE_TO_EXIT", this.forceToExit, this);
        this.onExtendedLoad();
    };

    start() {
        this.writer = this.node['writer'];
    }

    onExtendedLoad() {
        // let it empty. just override this func to call custom onLoad 
    }

    runAction(actionName: string, data?: any) {
        if (!this.writer || typeof this.writer['makeScript' + actionName] !== 'function') return;
        let script = this.writer['makeScript' + actionName](data);
        this.executeNextScript(script);
    }

    executeNextScript(scripts: script[] = []) {
        if (!this.writer || !scripts || scripts.length == 0) return;
        this.scripts = scripts;
        if (this.forceToExitMode && this.exitScript && this.exitScript.length > 0) {
            this.scripts = this.exitScript;
        }
        const nextScript: script = this.scripts.shift();
        let { command, data } = nextScript;
        let executeFunc: any = this.getCommandName(command)
        if (typeof this[executeFunc] === 'function') {
            log(this.name + " run command", executeFunc, data);
            this[executeFunc](this.scripts, data);
        } else {
            error(`No command ${executeFunc} in ${this.name}`);
            this.executeNextScript(this.scripts);
        }
    }

    getCommandName(command: string): string {
        let gameSpeed = this.dataStore.gameSpeed || "";
        let commandWithSpeed: string = command + "_" + gameSpeed;
        if (typeof this[commandWithSpeed] === 'function') {
            return commandWithSpeed;
        } else {
            return command;
        }
    }

    resetGameSpeed() {
        log('resetGameSpeed');
        this.dataStore.gameSpeed = 0;
    }

    setGameSpeed(gameSpeed) {
        log('setGameSpeed ' + gameSpeed);
        this.dataStore.gameSpeed = gameSpeed || this.getDefaultGameSpeed();
    }

    setGameSpeedMode(mode = 'NORMAL') {
        log('setGameSpeedMode ' + mode);
        const GAME_SPEED = this.getGameSpeedConfig();
        this.dataStore.gameSpeed = GAME_SPEED[mode] || GAME_SPEED.NORMAL;
    }

    getGameSpeed() {
        return this.dataStore.gameSpeed || this.getDefaultGameSpeed();
    }

    getDefaultGameSpeed() {
        const GAME_SPEED = this.getGameSpeedConfig();
        return this.dataStore.modeTurbo ? GAME_SPEED.TURBO : GAME_SPEED.NORMAL;
    }

    getGameSpeedConfig() { // remove after all games updated
        if (this.config && this.config.GAME_SPEED) {
            return this.config.GAME_SPEED;
        } else {
            return {
                NORMAL: 0,
                TURBO: 1,
                INSTANTLY: 2,
            };
        }
    }

    runContinueScript() {
        if (!this.storeNextScripts) return;
        const { data, script } = this.storeNextScripts;
        this[this.storeCurrentScripts] && this[this.storeCurrentScripts](script, data);
        this._resetStoreScript();
    }

    forceToExit(scripts: script[]) {
        this.forceToExitMode = true;
        this.exitScript = scripts;
    };

    _resetStoreScript() {
        this.storeCurrentScripts = '';
        this.storeNextScripts = {
            script: [],
            data: {}
        };
    }

    hide(){
        
    };

    //-------------- Async Script ---------------
    /**@AsyncScript */
    canStoreAsyncScript() {
        let isNormalGame = this.dataStore.currentGameMode === SlotGameMode.NormalGame;
        const { isFinished } = this.dataStore.playSession;
        const { isAutoSpin } = this.dataStore;
        const isValid = isFinished === true && !isAutoSpin && isNormalGame;
        return isValid;
    };

    storeAsyncScript(script, data) {
        this.listScriptAsync.push(data);
        this.executeNextScript(script);
    };

    _runAsyncScript(script) {
        this.executeNextScript(script);
        this.runAsyncScript();
    };

    runAsyncScript() {
        if (!this.listScriptAsync.length || this.isResetAsyncScript) return;
        const command = this.listScriptAsync.shift();
        if (command) {
            const {callback, name} = command;
            if (name) {
                log(this.name +' run AsyncScript: ', name);
            }
            callback && callback();
        }
    };

    resetAsyncScript() {
        if (!this.listScriptAsync.length) return;
        this.isResetAsyncScript = true;
        while (this.listScriptAsync.length > 0) {
            const command = this.listScriptAsync.shift();
            if (command) {
                const {callback, isSkippable, name} = command;
                if (!isSkippable) {
                    if (name) {
                        log(this.name +' run resetAsyncScript: ', name);
                    }
                    callback && callback();
                }
            }
        }
        this.isResetAsyncScript = false;
    };

    clearAsyncScript(){
        this.listScriptAsync = [];
    }

    exit() {
        if (typeof this.callBackWhenHide == "function") {
            this.callBackWhenHide();
            this.callBackWhenHide = null;
        }
        this.hide();
        this.node.emit('GAME_EXIT');
        this.node.active = false;
    }

}