
import { _decorator, sys } from 'cc';
import { fadeIn, setOpacity } from "../../../cc-share/common/utils";
import { SlotSceneType, TutorialTriggerType } from './CustomEnum';
import EventNames from '../core/EventNames';
import { GameObject } from '../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('SlotUIScene')
export class SlotUIScene extends GameObject {

    content: any;

    callback: any;

    @property({type: SlotSceneType})
    sceneType = SlotSceneType.Other;

    onLoad() {
        super.onLoad();
        this.node.on("PLAY", this.play, this);
        this.node.on("HIDE", this.exit, this);
        this.node.on("INIT", this.init, this);
        this.node.on("SKIP", this.skip, this);
        setOpacity(this.node, 0);
        this.setUpNodeInHierachy();
        this.node.active = false;
    }

    start(): void {
        
    }

    setUpNodeInHierachy(){
        if(this.guiMgr && !this.guiMgr.getCutscene(this.sceneType)){
            this.guiMgr.sceneComp.push(this);
        }
    }

    play(content: any, callback: Function) {
        this.content = content;
        this.callback = () => {
            this.callback = null;
            callback && callback();
        };
        this.show();
        this.enter();
    }

    show() {
        this.node.active = true;
        setOpacity(this.node, 0);
        fadeIn(this.node, 0.2);
    }

    init() {

    }

    enter() {
        //Overwrite this when extends
    }

    skip(){
        
    }

    getType() {
        return this.sceneType;
    }

    exit(callBack : any = null) {
        if (callBack && typeof callBack == "function")  this.callback = callBack;
        this.guiMgr.onIngameEvent(TutorialTriggerType.OnCutsceneClose, 'ON_CUTSCENE_CLOSE');
        if (this.callback) {
            this.node.emit("STOP");
            this.callback();
        }
        if(sys.isNative){
            this.eventManager && this.eventManager.emit(EventNames.REMOVE_ON_DEMAND_OBJECT, Number(this.sceneType), true, ()=>{
                const index = this.guiMgr.sceneComp.indexOf(this);
                if(index>-1){
                    this.guiMgr.sceneComp.splice(index, 1);
                }
            });
        }
        this.hideScene();
    }
    
    hideScene() {
        this.node.active = false;
    }

    playSoundClick(){
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }
}