
import { _decorator, Node, Label } from 'cc';
import {formatMoney, formatUserName} from '../../../../cc-share/common/utils';
import { GameObject } from '../../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('SlotBaseCellHistory')
export class SlotBaseCellHistory extends GameObject {

    @property({type: Node})
    time = null;

    @property({type: Node})
    account = null;

    @property({type: Node})
    bet = null;

    @property({type: Node})
    winAmount = null;

    @property addSecond = true;

    onLoad() {
        super.onLoad();
        this.node["updateData"] = this.updateData.bind(this);
    }

    updateData(data) {
        if (!data) return;
        this.time.getComponent(Label).string = this.formatTimeStamp(data.time);
        this.account.getComponent(Label).string = formatUserName(data.dn);
        this.bet.getComponent(Label).string = formatMoney(data.betAmt);
        this.winAmount.getComponent(Label).string = formatMoney(data.jpAmt);
    }

    formatTimeStamp(ts, hasYear?:boolean) {
        const d = new Date(ts);
        const h = addZero(d.getHours());
        const m = addZero(d.getMinutes());
        const s = this.addSecond ?  ':' + addZero(d.getSeconds()) : '';
        const t = addZero(d.getDate()) + '/' + addZero(d.getMonth() + 1) + (hasYear?('/' +  d.getFullYear()):'') + ' ' + h + ':' + m + s;
        return t;
    }

    formatTimeStampNew(data) {
        const date = new Date(data);
        let year = date.getFullYear() + 1900;
        let month = "0" + date.getMonth() + 1;
        let day = "0" + date.getUTCDate()
        let hours = date.getHours();
        let minutes = "0" + date.getMinutes();
        let seconds = "0" + date.getSeconds();
        let formattedTime = day.substr(-2) + "/" + month.substr(-2) + "/" + year + " " + hours + ':' + minutes.substr(-2) + ':' + seconds.substr(-2);
        return formattedTime;
    }
}

function addZero(i) {
    if (i < 10) {
        i = '0' + i;
    }
    return i;
}
