/* global cc, Sentry */

import { log, warn, sys } from 'cc';
import loadConfigAsync from '../cc-share/shareServices/loadConfigAsync';

import * as gameNetwork from './game-network.js';
const network = globalThis.GameNetwork || gameNetwork.default;
const { MessageManager, ServiceRest } = network;
const { logger} = network.lib;

logger && logger.updateLogger(log, log, warn);

const messageManager = MessageManager.getInstance();

class GlobalNetwork {
    gamesData: object = {};
    token: any = null;
    refreshToken: any = null;
    socket: any = null;
    expectedEvent: string = '';
    expectedCommandId: string = '';

    init = (token: any, refreshToken = '', envId = 'portal', gameIdSocket = 'all') => {
        log("Network using V3");
        this.token = token;
        this.refreshToken = refreshToken;
        const { SOCKET_URL, API_URL } = loadConfigAsync.getConfig();
        const deviceInfo = {
            os: sys.os,
            osVersion: sys.osVersion,
            platform: sys.platform,
            browser: sys.browserType,
            browserVersion: sys.browserVersion,
            language: sys.language
        };
        messageManager.initSocket({
            socketUrl: SOCKET_URL,
            apiUrl: API_URL,
            urlVerifyToken: 'auth/token/login',
            env: envId,
            games: gameIdSocket,
            token,
            serviceRest: ServiceRest.getInstance('cc'),
            device: deviceInfo,
        });
        loadConfigAsync.setUpSentry();
    };

    getToken = () => {
        return this.token;
    };

    setToken = (token) => {
        this.token = token;
    }

    registerGame = (gameData: any) => {
        const { gameId, gameState } = gameData;
        if (typeof Sentry !== 'undefined') {
            Sentry.configureScope(function (scope) {
                scope.setExtra("gameId", gameId);
            });
        }
        if (!gameState) return;
        return new gameState({ gameData });
    };

    initGame = (gameData: any = {}) => {
        gameData.token = this.getToken();
        if (typeof Sentry !== 'undefined') {
            Sentry.configureScope(function (scope) {
                scope.setExtra("gameId", gameData.gameId);
            });
        }
        return gameData;
    }

    triggerUserLogout = () => {
        messageManager.closeAndCleanUp();
    };

    outGame = () => { };
}

const globalNetwork = new GlobalNetwork();
export default globalNetwork;
