
import { _decorator } from 'cc';
const { ccclass, property } = _decorator;
import { SlotUIScene } from "./../component/SlotUIScene";

@ccclass('FreeGameOption')
export class FreeGameOption extends SlotUIScene {

    exit(callback : any = null) {
        if (callback) this.callback = callback;
        if (this.callback) {
            this.node.emit("STOP");
            this.callback();
        }
        this.node.active = false;
    }
}
