
import { _decorator, ScrollView } from 'cc';
import { setOpacity } from '../../../../cc-share/common/utils';
import { SlotUIScene } from '../../component/SlotUIScene';
const { ccclass, property } = _decorator;
 
@ccclass('InfoView')
export class InfoView extends SlotUIScene {
    @property({ type: ScrollView })
    scrollView: ScrollView = null;

    show() {
        this.node.active = true;
        setOpacity(this.node, 255);
        (this.node as any).show();
    }

    enter() {
        super.enter();
        if(this.scrollView){
            this.scrollView.scrollToTop();
        }
    }

    exit() {
        // overide exit
        // must have TweenView component

        let startCB = () => {};
        let endCB = () =>{
            // this.node.opacity = 0;
            setOpacity(this.node, 1);
            this.node.active = false;
        };
        (this.node as any).hide(startCB, endCB);
    }

}


