
import { _decorator, Node, tween, Vec3, Sprite, Button, SpriteFrame, Event } from 'cc';
import { GameObject } from "../../core/GameObject";
const { ccclass, property } = _decorator;

@ccclass('MiniBox')
export class MiniBox extends GameObject {

    @property({ type: Node, override: true })
    staticNode: Node;

    @property({ type: Node, override: true })
    symbolNode: Node;

    @property({ type: SpriteFrame, override: true })
    spriteSymbols: SpriteFrame[] = [];

    @property
    soundBoxClick: string = "";

    @property
    soundBoxOpen: string = "";

    isAutoTrigger: boolean = false;
    tweenClick: any;
    tweenOpen: any;
    mapValue: any = {};
    boxPosition: Vec3 = null;
    isSelected: boolean = false;
    protected animationLevel: number = 2;

    onLoad() {
        super.onLoad();
        this.node.on("INIT", this.init, this);
        this.node.on("SHOW", this.show, this);
        this.node.on("HIDE", this.hide, this);
        this.node.on("DIM", this.dim, this);

        this.node.on("BOX_OPEN", this.playAnimOpen, this);
        this.node.on("BOX_RESET", this.resetBox, this);
        (this.node as any).isOpen = false;
        (this.node as any).disableClick = this.disableClick.bind(this);
        (this.node as any).enableClick = this.enableClick.bind(this);
        (this.node as any).showScore = this.showScore.bind(this);
        (this.node as any).playAnimClick = this.playAnimClick.bind(this);
        (this.node as any).onClickItem = this.onClickItem.bind(this);
        (this.node as any).stopAnimClick = this.stopAnimClick.bind(this);
    };

    init(config) {
        this.mapValue = config.BOX_MAP_VALUES;
        this.boxPosition = this.node.position.clone();
    };

    show() {
        this.node.active = true;
    }

    hide() {
        this.node.active = false;
    }

    onClickItem(e: any, isAutoTrigger: boolean = false) {
        if ((this.node as any).isOpen) return;
        this.node["isAutoTrigger"] = isAutoTrigger;
        let event = new Event('CLICK_ITEM', true);
        this.node.dispatchEvent(event);
    };

    playAnimClick() {
        this.soundBoxClick && this.soundPlayer && this.soundPlayer.playSfx(this.soundBoxClick);
        const time: number = 0.02;
        this.stopAnimClick();
        this.tweenClick = tween(this.node)
            .repeatForever(
                tween(this.node)
                    .by(time, { position: new Vec3(-10, 0, 0) })
                    .by(time, { position: new Vec3(10, 0, 0) })
                    .by(time, { position: new Vec3(10, 0, 0) })
                    .by(time, { position: new Vec3(-10, 0, 0) })
            );
        this.tweenClick.start();
    };

    stopAnimClick() {
        if (this.tweenClick) this.tweenClick.stop();
        this.tweenClick = null;
    }

    playAnimOpen(value: number, isFast: boolean, isSelected: boolean, callback?: any) {
        (this.node as any).isOpen = true;
        this.isSelected = isSelected;
        this.disableClick();
        this.soundBoxOpen && this.soundPlayer && this.soundPlayer.playSfx(this.soundBoxOpen);
        if(this.tweenOpen) this.tweenOpen.stop();
        this.tweenOpen = tween(this.node)
            .delay(0.5)
            .call(() => {
                this.stopAnimClick();
                //Todo get the current performance level from data store to play exact animation
                //Example:
                // const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
                // this.animationLevel = this.config[performanceLvl].BONUS_ANIMATION;
                // const showStaticAnim = this.animationLevel< Number(PerformanceLevel.Medium);
                this.node.setPosition(this.boxPosition);
                this.symbolNode.active = true;
                this.setOpacity(this.staticNode, 0);
                let indexSprite = this.mapValue[value];
                this.symbolNode.getComponent(Sprite).spriteFrame = this.spriteSymbols[indexSprite];
                this.setOpacity(this.staticNode, 255);
            })
            .call(() => {
                this.showScore(value, isFast);
            })
            .delay(0.5)
            .call(() => {
                if (callback && typeof callback === 'function') {
                    callback();
                }
                this.tweenOpen = null;
            });
        this.tweenOpen.start();
            
    }

    dim() {
        this.setOpacity(this.node, 150);
    }

    showScore(value: number, isFast: boolean = false) {
        if (this.eventManager) {
            this.eventManager.emit("SHOW_BONUS_VALUE", value, this.node.getWorldPosition(), isFast, this.isSelected);
        }
    };

    setScore(value: number) {
        this.symbolNode.active = false;
        // this.labelScore.getComponent(Label).string = 'x' + (value || 0);
        // this.labelScore._uiProps.opacity = 1;
    };

    enableClick() {
        this.node.getComponent(Button).interactable = true;
    };

    disableClick() {
        this.node.getComponent(Button).interactable = false;
    };

    resetBox() {
        this.boxPosition && this.node.setPosition(this.boxPosition);
        this.stopAnimClick();
        this.symbolNode.active = false;
        this.setOpacity(this.staticNode, 255);
        this.setOpacity(this.node, 255);
        (this.node as any).isOpen = false;
        this.enableClick();
        this.isSelected = false;
    }

}
