import { _decorator, Component, Node, SpriteFrame, Prefab, Sprite, Label, instantiate, warn } from 'cc';
import { formatWalletMoney } from '../../../../cc-common/cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('BonusRollSymbol')
export class BonusRollSymbol extends Component {
    @property({ type: Node })
    Bg: Node = null;
    @property({ type: SpriteFrame })
    sprites: SpriteFrame[] = [];
    @property
    rate1: number = 500;
    @property
    rate2: number = 1000;
    @property({ type: Prefab })
    numberPrefab: Prefab = null;
    @property({ type: Sprite })
    spriteBg: Sprite = null;
    isFirst: boolean = false;
    creditValue: any;
    betDenom: any;
    numberIntro: any = null;

    onLoad() {
        (this.node as any).changeSymbol = this.changeSymbol.bind(this);
        (this.node as any).upgradeCredit = this.upgradeCredit.bind(this);
        (this.node as any).disableLabel = this.disableLabel.bind(this);
        (this.node as any).isFirstStringLabel = this.isFirstStringLabel.bind(this);

        if (this.Bg) this.spriteBg = this.Bg.getComponent(Sprite);

        this.node.on("UPDATE_BET_DENOM", this.updateBetDenom, this);
        this.node.on("ADD_NUMBER_INTRO", this.addNumberIntro, this);
        this.node.on("HIDE_NUMBER_INTRO", this.hideNumberIntro, this);
        this.isFirst = true;
    }

    isFirstStringLabel(isFirst) {
        this.isFirst = isFirst;
    }

    changeSymbol(symbolValue, betDenom = 1) {
        const label = this.node.getComponentInChildren(Label);
        this.creditValue = null;
        if (label) {
            label.node.active = false;
        }
        if (symbolValue === 'JP') {
            this.spriteBg.spriteFrame = this.sprites[0];
        } else if (symbolValue === 'FR') {
            this.spriteBg.spriteFrame = this.sprites[1];
        } else if (symbolValue && symbolValue !== '0') {
            if (symbolValue < this.rate1) {
                this.spriteBg.spriteFrame = this.sprites[2];
            } else if (symbolValue < this.rate2) {
                this.spriteBg.spriteFrame = this.sprites[3];
            } else {
                this.spriteBg.spriteFrame = this.sprites[4];
            }
            if (label) {
                label.node.active = true;
                this.creditValue = parseInt(symbolValue);
                if (this.creditValue) {
                    label.string = formatWalletMoney(this.creditValue * betDenom);
                } else {
                    warn("mini symbol value: " + symbolValue);
                }
                // if (this.isFirst) {
                //     label.string = "";
                // }
            }
        } else {
            this.spriteBg.spriteFrame = this.sprites[2];
            warn("mini symbol value: " + symbolValue);
        }
    }

    upgradeCredit(multiplier) {
        if (this.creditValue) {
            const label = this.node.getComponentInChildren(Label);
            if (label) {
                label.string = formatWalletMoney(this.creditValue * multiplier);
            }
        }
    }

    updateBetDenom(betDenom) {
        this.betDenom = betDenom;
        if (this.creditValue && this.isFirst) {
            const label = this.node.getComponentInChildren(Label);
            if (label) {
                label.string = formatWalletMoney(this.creditValue * betDenom);
            }
        }
        this.isFirst = false;
    }

    disableLabel() {
        const label = this.node.getComponentInChildren(Label);
        if (label) {
            label.node.active = false;
        }
    }

    addNumberIntro(num) {
        if (!this.numberIntro) {
            this.numberIntro = instantiate(this.numberPrefab);
            this.node.addChild(this.numberIntro);
        }
        this.disableLabel();
        this.numberIntro.getComponent(Label).string = formatWalletMoney(num);
    }

    hideNumberIntro() {
        if (this.numberIntro) this.numberIntro.active = false;
    }

}
