

import * as cc from 'cc';
import loadConfigAsync from '../cc-share/shareServices/loadConfigAsync';
import serviceRest from '../cc-share/shareServices/serviceRest';
import gameCommonUtils from '../cc-share/common/gameCommonUtils';
import globalNetwork from './globalNetwork';
import { mock } from '../../mock/mock';

class ConnectNetwork {
    gameId: string = '';
    getToken() {
        const {LOGIN_IFRAME, URL_TOKEN, USER_TOKEN} = loadConfigAsync.getConfig();
        let token: any = '';
        if (LOGIN_IFRAME) {
            const {getUrlParam} = gameCommonUtils;
            token = getUrlParam(URL_TOKEN);
            cc.sys.localStorage.setItem(USER_TOKEN, token);
        }
        else if (typeof window !== 'undefined' && 
                    typeof window["__Game_Bridge"] !== 'undefined' && 
                    typeof window["__Game_Bridge"].getUSS === 'function') {
            token = window["__Game_Bridge"].getUSS();
        }
        else {
            token = cc.sys.localStorage.getItem(USER_TOKEN);
        }
        return "bedau21";
    };

    getRefreshToken() {
        const {LOGIN_IFRAME, URL_REFRESH_TOKEN = 'refresh-token', USER_REFRESH_TOKEN = 'user-refresh-token'} = loadConfigAsync.getConfig();
        let refreshToken = '';
        if (LOGIN_IFRAME) {
            const {getUrlParam} = gameCommonUtils;
            refreshToken = getUrlParam(URL_REFRESH_TOKEN);
            cc.sys.localStorage.setItem(USER_REFRESH_TOKEN, refreshToken);
        } else {
            refreshToken = cc.sys.localStorage.getItem(USER_REFRESH_TOKEN);
        }
        return refreshToken;
    };

    loginScene({callback = () => {}, gameId = '', callbackAuthFailed = () => {}}) {
        cc.log("Login using V3");
        const {IS_FINISHED_REMOTE, DEV_ENV, USER_TOKEN} = loadConfigAsync.getConfig();
        if (!IS_FINISHED_REMOTE) {
            setTimeout(() => {
                this.loginScene({callback, gameId, callbackAuthFailed});
            }, 100);
            return;
        }
        this.gameId = gameId;
        let token = this.getToken();
        const refreshToken = this.getRefreshToken();
        const {LOGIN_IFRAME, API_URL } = loadConfigAsync.getConfig();
        const envId = LOGIN_IFRAME ? 'iframe' : 'portal';
        const gameIdSocket = LOGIN_IFRAME ? gameId : 'all';
        const {getUrlParam} = gameCommonUtils;
        const history = getUrlParam('history');
        if (LOGIN_IFRAME && history && history === 'true') return;

        if (token || !DEV_ENV) {
            if (token) {
                globalNetwork.init(token, refreshToken, envId, gameIdSocket);
                callback();
            }  else {
                callbackAuthFailed();
            }
        } else {
            let dataPost = {
                userName: mock.userText,
                password: mock.pwText,
                fingerPrint: 'test'
            };
            serviceRest.post({
                url: 'auth/login', 
                data: dataPost, 
                callback: ({data}) => {
                    if (data.data.token) {
                        cc.sys.localStorage.setItem(USER_TOKEN, data.data.token);
                        globalNetwork.init(data.data.token, '', envId, gameIdSocket);
                        callback();
                    } else {
                        callbackAuthFailed && callbackAuthFailed();
                    }
                }, 
                callbackErr: ()=>{
                    callbackAuthFailed && callbackAuthFailed();
                },
                apiUrl: API_URL
            });
        }
    };
}
const connectNetwork = new ConnectNetwork();
export default connectNetwork;