
import { tween, Vec3, _decorator, Node, Component, warn, error, instantiate } from 'cc';
import { CustomEvent } from '../../component/CustomType';
const { ccclass } = _decorator;

@ccclass('SlotReelv2')
export class SlotReelv2 extends Component {

    protected _higherHolder: Node = null;
    protected _topSymbol: Node = null;
    protected _botSymbol: Node = null;

    showSymbols: Node[] = [];
    symbols: Node[] = [];
    // constants
    config: any = {};
    symbolHeight: number = 0;
    reelIndex: number = 0;
    symbolList: string[] = [];
    // 
    showNumber: number = 0;
    totalSymbols: number = 5;
    symbolStartY: number = 0;
    symbolPos: number[] = [];
    // 
    reelConfig: any = {};
    currentSpeed: number = 0;
    currentIndex: number = 0;
    stop: number = 0;
    extendTime: number = 0;
    step: number;
    result: any[] = [];
    // flag
    isSpinning: boolean = false;
    isShowingResult: boolean = false;
    isNearWin: boolean = false;
    isFastToResult: boolean = false;
    isFreeMode: boolean = false;

    callbackStop: () => void;
    callBackDelayStop: () => void;

    MAX_STEP: number = Number.MAX_SAFE_INTEGER;

    onLoad() {
        this.node.on("INIT", this.init, this);
        this.node.on("START_SPIN", this.startSpinning, this);
        this.node.on("STOP_SPIN", this.stopSpinningWithDelay, this);
        this.node.on("UPDATE_SYMBOLS", this.updateSymbols, this);
        this.node.on("FAST_STOP", this.fastStopSpinning, this);
        this.node.on("HIDE_SYMBOL", this.hideSymbol, this);
        this.node.on("EXTEND_TIME_STOP", this.extendTimeToStop, this);
        this.node.on("UPDATE_NEAR_WIN_SPEED", this.updateNearWinSpeed, this);
        this.node.on("RANDOM_SYMBOLS", this.randomSymbols, this);
        this.node.on("ADJUST_REEL_SPEED", this.adjustReelSpeed, this);
        this.node.getShowSymbols = this.getShowSymbols.bind(this);
    }
    public init(tableFormat = [], gameConfig: any = {}, col = 0, symbolPrefab = null, isFreeMode = false) {
        this.config = gameConfig;
        const { NORMAL_SYMBOLS, FREE_SYMBOLS, TABLE_BUFFER, SYMBOL_HEIGHT, SYMBOL_HEIGHT_FREE_GAME, EXCEPTION_SYMBOLS } = gameConfig;
        this.symbolHeight = isFreeMode && SYMBOL_HEIGHT_FREE_GAME ? SYMBOL_HEIGHT_FREE_GAME : SYMBOL_HEIGHT;
        this.reelIndex = col;
        this.symbolList = isFreeMode ? FREE_SYMBOLS[col] : NORMAL_SYMBOLS[col];
        this.isFreeMode = isFreeMode;
        this.showNumber = tableFormat[col];
        this.totalSymbols = this.showNumber + TABLE_BUFFER.TOP + TABLE_BUFFER.BOT;
        this.symbolStartY = -(this.showNumber / 2 + TABLE_BUFFER.BOT - 0.5) * this.symbolHeight;
        this.showSymbols = [];
        this.currentIndex = 0;
        for (let index = 0; index < this.totalSymbols; index++) {
            const symbol = instantiate(symbolPrefab);
            symbol.setPosition(0, this.symbolStartY + index * this.symbolHeight);
            this.node.addChild(symbol);
            symbol.emit('SET_SYMBOL_MODE', isFreeMode);
            let randomSymbol = this.getRandomSymbolNameWithExceptions(EXCEPTION_SYMBOLS || ["A", "R", "K"]);
            symbol.emit('CHANGE_TO_SYMBOL', randomSymbol);
            this.symbols.push(symbol);
            this.symbolPos.push(symbol.position.y);
            if (index >= TABLE_BUFFER.BOT && index < this.totalSymbols - TABLE_BUFFER.TOP) {
                this.showSymbols.unshift(symbol);
            }
        }
        this.reset();
    }
    public startSpinning(reelIndex: number, reelConfig = {}) {
        this.step = this.MAX_STEP - 1;
        this.reelConfig = reelConfig;
        this.reelIndex = reelIndex;
        this.setUpConfig();
        const { DELAY_START } = this.reelConfig;
        const currentSpeed = this.reelConfig.TIME;

        tween(this.node)
            .delay(DELAY_START * reelIndex)
            .by(currentSpeed, { position: new Vec3(0, 25) })
            .by(currentSpeed, { position: new Vec3(0, -25) })
            .call(() => { this.runSpinning(); })
            .start();
    };
    public stopSpinningWithDelay(data: string[], callbackStop) {
        this.showSymbols = [];
        this.callbackStop = callbackStop;
        this.result = [...data];
        this.isNearWin = false;
        const { DELAY_STOP, EASING_TIME, IS_TURBO } = this.reelConfig;
        const delayTime = IS_TURBO ? DELAY_STOP : (this.reelIndex + 1) * EASING_TIME + DELAY_STOP;

        this.unschedule(this.setStepToStop);
        this.scheduleOnce(this.setStepToStop, delayTime + this.extendTime);

        // buffer bot = 1, unshift one more
        this.result.unshift(this.getRandomSymbolNameWithExceptions('A'));
        // buffer top = 1, push one more
        this.result.push(this.getRandomSymbolNameWithExceptions('R'));
    };
    public updateSymbols(symbolNames) {
        this.showSymbols.forEach((symbol, index) => {
            symbol.emit('CHANGE_TO_SYMBOL', symbolNames[index]);
        });
    };
    public fastStopSpinning(remainStep?: number) {
        //check step is reset will not do anything.
        if (this.step === this.MAX_STEP) return;
        if (this.isFastToResult) return;
        this.isFastToResult = true;
        this.unschedule(this.setStepToStop);
        this.isShowingResult = true;
        this.currentSpeed = this.currentSpeed / 3;
    };
    public hideSymbol(row, symbol, isShow) {
        if (this.showSymbols[row]) {
            this.showSymbols[row].emit(isShow ? 'SHOW_SYMBOL' : 'HIDE_SYMBOL', symbol);
        }
    };
    public extendTimeToStop() {
        const { NORMAL_TABLE_FORMAT, FREE_TABLE_FORMAT } = this.config;
        const tableFormat = this.isFreeMode ? FREE_TABLE_FORMAT : NORMAL_TABLE_FORMAT;
        this.extendTime = 0;
        if (this.reelIndex === tableFormat.length - 1) {
            this.extendTime = this.reelConfig.NEAR_WIN_DELAY_LAST_REEL * this.reelIndex;
        } else {
            this.extendTime = this.reelConfig.NEAR_WIN_DELAY * this.reelIndex;
        }
        this.unschedule(this.setStepToStop);
        this.scheduleOnce(this.setStepToStop, this.extendTime);
    };
    public updateNearWinSpeed() {
        this.currentSpeed = this.config.SUPER_TURBO;
    };
    public randomSymbols() {
        const { EXCEPTION_SYMBOLS } = this.config;
        this.symbols.forEach(sym => {
            sym.emit('CHANGE_TO_SYMBOL', this.getRandomSymbolNameWithExceptions(EXCEPTION_SYMBOLS || ["A", "R", "K"]));
        });
    };
    public getShowSymbols(): Node[] {
        return this.showSymbols;
    };
    public adjustReelSpeed(speed) {
        this.currentSpeed = speed;
    }

    runSpinning() {
        this.runSpinningAnimation(() => {
            if (this.step > this.showNumber) {
                this.runSpinning();
                this.step--;
                if (this.step < this.totalSymbols) {
                    this.isShowingResult = true;
                }
            } else if (this.step == this.showNumber) {
                const { EASING_DISTANCE } = this.reelConfig;
                this.runStopAnimation(EASING_DISTANCE, 0.08);
            }
        });
    }

    setUpConfig() {
        this.currentSpeed = this.reelConfig.TIME;
        this.isSpinning = true;
        this.isShowingResult = false;
        this.isFastToResult = false;
        this.extendTime = 0;
    };
    runSpinningAnimation(callback) {
        let time = this.currentSpeed + this.currentSpeed * this.stop / 4;
        tween(this.node)
            .by(time, { position: new Vec3(0, -1 * this.symbolHeight, 0) })
            .call(() => {
                this.circulateSymbols();
                callback && callback();
            })
            .start();
    }
    circulateSymbols() {
        let lastSymbol = this.symbols[this.currentIndex % this.totalSymbols];
        let symbolValue = this.getRandomSymbolName();

        if (!this.isShowingResult) {
            lastSymbol.emit('CHANGE_TO_BLUR_SYMBOL', symbolValue);
        } else if (this.stop < this.totalSymbols) {
            let isRealSymbol = this._isRealSymbol();
            symbolValue = this.result[this.stop];
            this.step = this.totalSymbols + this.showNumber - (this.stop + this.config.TABLE_BUFFER.BOT);
            if (isRealSymbol) {
                lastSymbol.emit('CHANGE_TO_SYMBOL', symbolValue);
                this.showSymbols.unshift(lastSymbol);
            } else {
                lastSymbol.emit('CHANGE_TO_BLUR_SYMBOL', symbolValue);
            }
            this.stop++;
        }
        let posY = lastSymbol.position.y;
        posY += (this.symbolHeight * this.totalSymbols);
        lastSymbol.setPosition(new Vec3(lastSymbol.position.x, posY, 0));
        this.currentIndex++;
    }
    _isRealSymbol() {
        const { TOP, BOT } = this.config.TABLE_BUFFER;
        return (this.stop >= BOT) && (this.stop < this.showNumber + TOP);
    }
    runStopAnimation(indexNearWin, time) {
        const timer = time ? time : this.reelConfig.TIME;
        this.onReelStop();
        this.notifyReelGoingToStop();
        tween(this.node)
            .by(timer, { position: new Vec3(0, -indexNearWin) })
            .by(timer, { position: new Vec3(0, indexNearWin) })
            .call(() => {
                this.reset();
                this.callbackStop();
                // stop schedule when reel is stopped
                this.unschedule(this.setStepToStop);
                this.currentSpeed = this.reelConfig.TIME;
            })
            .start();
    }

    setStepToStop() {
        this.step = this.reelConfig.STEP_STOP * 2 - this.totalSymbols;
    };

    onReelStop() {
        this.symbols.forEach((child) => {
            child.emit("CHANGE_TO_SYMBOL", child['symbol']);
        });
        // this.testResult();
    }

    notifyReelGoingToStop() {
        const reelResult = this.getReelResult();
        const isTurbo = this.reelConfig.IS_TURBO;
        const reelStopEvt = new CustomEvent('REEL_GOING_TO_STOP', true, { reelIndex: this.reelIndex, reelResult, isTurbo, isF2R: this.isFastToResult });
        this.node.dispatchEvent(reelStopEvt)
    }

    getReelResult() {
        if(!this.showSymbols) return [];
        let realResult = [];
        for(let i = 0; i < this.showSymbols.length; i++) {
            const symbolName = this.showSymbols[i]['symbol'];
            realResult.push(symbolName);
        }
        return realResult;
    }

    getRandomSymbolNameWithExceptions(exceptionSymbols): string {
        let symbol = this.getRandomSymbolName();
        return exceptionSymbols.includes(symbol) ? this.getRandomSymbolNameWithExceptions(exceptionSymbols) : symbol;
    }
    getRandomSymbolName() {
        return this.symbolList[Math.floor(Math.random() * this.symbolList.length)];
    }
    reset() {
        // reset currentIndex
        this.symbols.sort((a, b) => a.position.y - b.position.y);
        this.currentIndex = 0;
        // reset reel position
        this.symbols.forEach((symbol, index) => symbol.setPosition(symbol.position.x, this.symbolPos[index]));
        this.node.setPosition(this.node.position.x, 0);

        this.stop = 0;
        this.step = this.MAX_STEP;
        this.isShowingResult = false;
        this.result = [];
        this.isSpinning = false;
    }
    testResult() {
        if (!this.result) {
            warn("do not have result");
            return;
        }
        let _realResult = this.result.slice().reverse();
        let _showingResult = [];
        this.showSymbols.forEach(sb => _showingResult.push(sb['symbol']))
        if (JSON.stringify(_realResult) !== JSON.stringify(_showingResult)) {
            error("wrong matrix: ", { realResult: _realResult, showingResult: _showingResult })
        }
    }
}

