import { _decorator, Node, Label, Sprite, SpriteFrame } from 'cc';
import { SlotSoundPlayer } from '../sound/SlotSoundPlayer';
import { SlotUIScene } from './SlotUIScene';
const { ccclass, property } = _decorator;

@ccclass('SlotDialogAction')
export class SlotDialogAction extends SlotUIScene {
    actionOK: any;
    actionCancel: any;
    actionClose: any;
    
    @property({type: Node})
    gradient = null;
    @property({type: SpriteFrame})
    titleSpriteFrames = [];
    @property({type: Sprite})
    title = null;
    @property({type: Node})
    buttonOK = null;
    @property({type: Node})
    buttonCancel = null;
    @property({type: Node})
    buttonClose = null;

    @property({type: Label})
    message = null;

    @property({ type: SlotSoundPlayer })
    slotSoundPlayer = null;

    show() {
        if (!this.node.active)
            super.show();
        const {strText, dialogType, actionOK = null, actionCancel = null, actionClose = null} = this.content;
        this.showMessage(strText, dialogType, actionOK, actionCancel, actionClose);
    }

    showMessage(strText: string, dialogType: number = 0, actionOK: null, actionCancel: null, actionClose: null) {
        if (this.message.string.replace(/\n|\r/g, "") !== strText.replace(/\n|\r/g, "")) {
            this.message.string = strText;
        }
        this.title.spriteFrame = this.titleSpriteFrames[dialogType];
        this.actionOK = actionOK;
        this.actionCancel = actionCancel;
        this.actionClose = actionClose;
        if (!actionCancel) {
            this.buttonCancel.active = false;
        }
        else {
            this.buttonCancel.active = true;
        }
        if (!actionOK) {
            this.buttonOK.active = false;
        }
        else {
            this.buttonOK.active = true;
        }
        if (!actionClose) {
            this.buttonClose.active = false;
        }
        else {
            this.buttonClose.active = true;
        }
    }

    isDisplay() {
        return this.node.active;
    }

    onActionOK() {
        this.slotSoundPlayer.playSfx('CLICK');
        this.actionOK();
        this.hideScene();
    }

    onActionClose() {
        this.slotSoundPlayer.playSfx('CLICK');
        this.actionClose();
        this.hideScene();
    }

    onActionCancel() {
        this.slotSoundPlayer.playSfx('CLICK');
        this.actionCancel();
        this.hideScene();
    }

    hidePopup() {
        this.slotSoundPlayer.playSfx('CLICK');
        this.hideScene();
    }
}