
import { _decorator, Component, Node } from 'cc';
const { ccclass, property } = _decorator;


 
@ccclass('GlobalNodeSharing')
export class GlobalNodeSharing extends Component {
    @property
    nodeId: string = '';
    @property
    getNode: boolean = false;

    @property
    setNode: boolean = false;

    onLoad () {
       if(this.getNode){
           this.getNodeSharing();
       }
       if(this.setNode){
           this.setNodeSharing();
       }
    }

    setNodeSharing(){
        if(this.node.scene){
            this.node.scene[this.nodeId] = this.node;
        }
    }

    getNodeSharing(){
        if(this.node.scene && this.node.scene[this.nodeId]){
            this.node[this.nodeId] = this.node.scene[this.nodeId];
        }
    }
}

