
import { _decorator, Node, Prefab, instantiate, Tween, v3, tween } from 'cc';
import { CustomEvent } from '../component/CustomType';
import { SlotReelv2 } from '../portrait/table/SlotReelv2';
const { ccclass, property } = _decorator;

@ccclass('GongReel')
export class GongReel extends SlotReelv2 {
   @property(Prefab)
   stackWildPrefab: Prefab = null;

   protected _stackWild: Node = null;

   protected _isSpeedUp: boolean = false;
   protected _remainStep: number = this.MAX_STEP;
   protected _stackData: any = {};
   protected _tweenNudge: Tween<any> = null;
   protected _isFinished: boolean = true;

   onLoad() {
      super.onLoad();
      this.node.on('SET_STACK_DATA', this.setStackData, this);
      this.node.on('SHOW_STACK_WILD', this.showStackWild, this);
      this.node.on('CLEAR_STACK_WILD', this.clearStackWild, this);
      this.node.on('RUN_NUDGE', this.runAnimNudge, this);
      this.node.on('SKIP_NUDGE', this.skipAnimNudge, this);
      this.node["getShowSymbols"] = this.getShowSymbols.bind(this);
   }

   init(...params) {
      super.init(...params);
      this._stackWild = instantiate(this.stackWildPrefab);
      this.node.addChild(this._stackWild);
      this._stackWild.emit('INIT', this.symbolHeight);
   }

   setHigherHolder(node: Node) {
      this._higherHolder = node;
   }

   startSpinning(reelIndex: number, reelConfig = {}) {
      this._isFinished = false;
      this._isSpeedUp = false;
      this._stackWild.emit("SHOW_PARTICLE", false);
      this._stackWild.emit("SHOW_GONG_EFFECT", false);
      this._resetReelPosition();
      super.startSpinning(reelIndex, reelConfig);
      this._remainStep = this.step;
   }

   setStackData(stackFormat) {
      this._stackData = Object.assign({}, stackFormat);
   }

   public fastStopSpinning(remainStep) {
      //check step is reset will not do anything.
      if (this.step === this.MAX_STEP) return;
      if (this.isFastToResult) return;
      this.isFastToResult = true;
      this.unschedule(this.setStepToStop);
      this.currentSpeed = this.currentSpeed / 3;
      if (this.isShowingResult) return;
      this.scheduleOnce(() => {
         if (remainStep < this.totalSymbols) {
            this.step = this.totalSymbols;
         } else if (this.step > remainStep) {
            this.step = remainStep;
         }
         this._remainStep = this.step;
      }, 0.04);
   };

   public speedUp() {
      this._isSpeedUp = true;
      this.adjustReelSpeed(this.currentSpeed / 1.5);
   }

   public showStackWild() {
      const { nudgeStep, step, type, size } = this._stackData;
      if (type === "0") return this.clearStackWild();
      this._addStackWild();
      this._stackWild.emit("SHOW_PARTICLE", false);
      this._stackWild.emit("SHOW_GONG_EFFECT", true);
      this._stackWild.setPosition(0, (size + 1.5 - step - nudgeStep) * this.symbolHeight);
      this._setupShowSymbols();
   }

   public clearStackWild() {
      this._stackWild.emit("CLEAR_STACK_WILD");
      this.symbols.forEach(sb => sb.opacity = 255);
      this.showSymbols = this.symbols.slice(1, 4).reverse();
   }

   public runAnimNudge() {
      const { nudgeStep, type, nudge } = this._stackData;
      if (type === "0") return this._finishNudge(false);
      if (nudge === 6) return this._finishNudge();
      if (nudge === 2) return this._runFakeNudge();

      const step = Math.abs(nudgeStep);
      const direction = nudgeStep / step; // 1: down -1: up */
      let dur = 1;

      this._clearTweenNudge();
      this._tweenNudge = tween(this.node).delay(0.2);
      for (let index = 0; index < step; index++) {
         this._tweenNudge
            .call(() => {
               this._stackWild.emit("SHAKING_WILDS");
               this._playSoundWildShake();
            })
            .delay(dur)
            .call(() => { this._playSoundWildMove(); })
            .by(dur, { position: v3(0, - this.symbolHeight * direction) }, { easing: "cubicInOut" })
            .call(() => {
               const lastIndex = direction === 1 ? 0 : this.totalSymbols - 1;
               const lastSymbol = this.symbols[lastIndex];
               lastSymbol.setPosition(0, lastSymbol.position.y + this.symbolHeight * this.totalSymbols * direction);
               this._resetReelPosition();
            })
      }
      this._tweenNudge
         .call(() => {
            this._tweenNudge = null;
            this._stackWild.emit("RESET_ANIM_WILDS");
            this._finishNudge()
         })
         .start();
   }

   public skipAnimNudge() {
      if (this._isFinished) return;
      if (this._tweenNudge) {
         this._clearTweenNudge();
         this._resetReelPosition();
         this.symbols.forEach((symbol, index) => { symbol.setPosition(0, this.symbolPos[index]) });
      }
      this.showStackWild();
      this._finishNudge();
   }

   /**@private */
   circulateSymbols() {
      const { step, nudgeStep } = this._stackData;
      const lastSymbol = this.symbols[0];
      let posY = lastSymbol.position.y + (this.symbolHeight * this.totalSymbols)
      lastSymbol.setPosition(lastSymbol.position.x, posY);

      this._resetReelPosition();
      if (!this.isShowingResult) {
         this._remainStep = this.step;
         lastSymbol.emit('CHANGE_TO_BLUR_SYMBOL', this.getRandomSymbolName());
      } else if (this.stop < this.totalSymbols) {
         this._remainStep = this.totalSymbols - 1 - this.stop;
         let isRealSymbol = this._isRealSymbol();
         let symbolName = this.result[this.stop];
         this.step = this.totalSymbols + this.showNumber - (this.stop + this.config.TABLE_BUFFER.BOT);
         if (isRealSymbol) {
            if ('KL'.includes(symbolName) && nudgeStep !== 0) {
               symbolName = this.getRandomSymbolNameWithExceptions(["2", "4"]);
            } // fake for nudge
            lastSymbol.emit('CHANGE_TO_SYMBOL', symbolName);
            this.showSymbols.unshift(lastSymbol);
         } else {
            lastSymbol.emit('CHANGE_TO_BLUR_SYMBOL', symbolName);
         }
         this.stop++;
      }
      if (this._remainStep === step) {
         this._addStackWild();
      }
   }

   _resetReelPosition() {
      // reset currentIndex
      this.symbols.sort((a, b) => a.position.y - b.position.y);
      this.currentIndex = 0;
      // reset reel position
      this.node.children.forEach((child) => child.setPosition(child.position.x, child.position.y + this.node.position.y));
      this.node.setPosition(this.node.position.x, 0);

      if (this._stackWild.position.y < -1.5 * this.symbolHeight) { // out of table
         this._stackWild.emit("RESET_STACK_WILD");
      }
   }

   _addStackWild() {
      const { size, type } = this._stackData;
      if ((type === "FAKE") && !this._canFakeStackWild()) return;
      this._stackWild.emit("SET_STACK_WILD", this._stackData);
      this._stackWild.setPosition(0, (size + 1.5) * this.symbolHeight);
      this._stackWild.emit("SHOW_PARTICLE", true);
   }

   _isUnderStackWild() {
      const { step, size } = this._stackData;
      return (this._remainStep <= step) && (this._remainStep > Math.abs(step - size));
   }

   onReelStop() {
      this._stackWild.emit("SHOW_PARTICLE", false);
      this._stackWild.emit("SHOW_GONG_EFFECT", true);
   }

   _runFakeNudge() {
      const isFake = true;
      this._stackWild.emit("SHAKING_WILDS", isFake);
      this.node.soundPlayer.playSFXWildShake();
      this._clearTweenNudge();
      this._tweenNudge = tween(this.node)
         .delay(0.5)
         .call(() => {
            this._finishNudge(true);
            this._tweenNudge = null;
         });

      this._tweenNudge.start();
   }

   _clearTweenNudge() {
      if (this._tweenNudge) {
         this._tweenNudge.stop();
         this._tweenNudge = null;
      }
   }

   _finishNudge(hasStackWild = true) {
      if (this._isFinished) return;
      this._isFinished = true;
      if (hasStackWild) this._setupShowSymbols();
      const event = new CustomEvent('FINISH_NUDGE', true);
      this.node.dispatchEvent(event);
   }

   _setupShowSymbols() {
      if (this._stackData.type === "0") return;
      this.showSymbols = this.symbols.slice(1, 4).reverse();
      const wildSymbols = this._stackWild.getShowWilds();
      for (let row = 0; row < 3; row++) {
         this.showSymbols[row] = wildSymbols[row] || this.showSymbols[row];
      }
   }

   _canFakeStackWild() {
      if (this.isFreeMode) return true;
      if ([1, 3].includes(this.reelIndex)) return false;
      return true;
   }

   /**@sound */
   _playSoundWildShake() { }
   _playSoundWildMove() { }

}
