import { _decorator, Node, tween, Tween, Vec3, warn } from 'cc';
import { PoolFactory } from '../component/PoolFactory';
import { GameObject } from '../core/GameObject';
const { ccclass, property } = _decorator;
@ccclass('BonusRollReel')
export class BonusRollReel extends GameObject {
    @property
    showSymbolIndex: number = 1;
    @property
    topBuffer: number = 1;
    @property
    botBuffer: number = 1;
    @property
    unitDistance: number = 100;
    @property
    fastTotalCircular: number = 10;
    @property
    turboTotalCicular: number = 5;
    @property
    stopEasingDistance: number = 10;
    @property
    isBlock: boolean = false;
    @property
    autoUpdate: boolean = false;
    @property
    miniReel: boolean = true;
    @property
    showNumber: number = 1;
    @property
    f2rFactor: number = 8;

    @property({ type: Node })
    reelNode: Node = null;
    @property({ type: Node })
    blockNode: Node = null
    @property({ type: Node })
    unblockNode: Node = null;
    @property({ type: Node })
    reelEffect: Node = null;

    totalSymbol: number = 4;
    offsetToStop: number = 2;
    totalScrolling: number = 0;
    scrollingSpeed: number = 0;
    betDenom: number = 0;

    currentIndex: number = 0;
    previousY: number = 0;
    _symbols: any = [];
    symbolPos: number[] = [];
    symbolNameList: any = [];
    mode: string = '';
    reelIndex: number = -1;
    totalCircular: number = 0;
    isWaitingResult: boolean = false;
    isRolling: boolean = false;
    reelStopped: boolean = false;
    canF2R: boolean = false;
    symbolValue: any = null;
    circularCount: number = 0;
    callbackFinish: any = null;
    spinAction: Tween<any> = null;
    targetY: number = 0;
    tweenTime: number;
    isShowingResult: boolean = false;
    callBackDelayStop: any = null;
    multiplierFactor: number = 0;
    symbolPrefabName: string = '';
    poolFactory: PoolFactory = null;
    creditLabelPrefabName: string = '';

    onLoad() {
        super.onLoad();
        this.node.on("INIT", this.init, this);
        this.node.on("START_SPINNING", this.startSpinning, this);
        this.node.on("STOP_SPINNING", this.stopSpinning, this);
        this.node.on("UPGRADE_CREDIT", this.upgradeCredit, this);
        this.node.on("FAST_TO_RESULT", this.fastToResult, this);
        this.node.on("SET_MODE", this.setMode, this);
        this.node.on("SET_UP_BONUS_ROLL_RESUME", this.setupBonusRollResume, this);
        this.node.on("UPDATE_BET_DENOM", this.updateBetDenom, this);
        this.node.on("UNBLOCK_JACKPOT", this.unBlockJackpot, this);
        this.node.on("UPDATE_ALL_SYMBOLS", this.updateAllSymbols, this);
        this.node.on("HIDE_INTRO", this.hideIntro, this);

        (this.node as any).updatePosition = this.updatePosition.bind(this);
        (this.node as any).reelIndex = this.updateReelIndex.bind(this);
        this.betDenom = 1;
        this.setMode('FAST');
        this.totalSymbol = this.showNumber + this.topBuffer + this.botBuffer;
    }

    start() {
        const { MIN_SPEED } = this.config;

        this.scrollingSpeed = MIN_SPEED;
        this.currentIndex = 0;
        this.previousY = 0;
        this.isRolling = false;
        this.circularCount = 0;
        this.canF2R = false;
    }

    init(poolFactory: PoolFactory, symbolNameList: [], symbolPrefabName: string, creditLabelPrefabName: string, isBlock: boolean, config: any) {
        if (poolFactory) {
            this.poolFactory = poolFactory;
        }
        if (symbolPrefabName) {
            this.symbolPrefabName = symbolPrefabName;
        }
        if (creditLabelPrefabName) {
            this.creditLabelPrefabName = creditLabelPrefabName;
        }
        this.symbolNameList = symbolNameList;
        this.isBlock = isBlock;
        if (this.blockNode) {
            this.blockNode.active = this.isBlock;
        }
        this._symbols = [];
        this.createSymbols();
    }

    updateReelIndex(col: number) {
        this.reelIndex = col;
    }

    startSpinning() {
        if (this.isWaitingResult || this.isBlock)
            return;
        const { MIN_SPEED } = this.config;
        this.isRolling = true;
        this.reelStopped = false;
        this.isWaitingResult = true;
        this.scrollingSpeed = MIN_SPEED;
        this.canF2R = false;
        this.symbolValue = null;
        this.isShowingResult = false;
        this.totalScrolling = 0;
        this.previousY = 0;
        this.circularCount = 0;
    }

    stopSpinning(symbolValue, callback) {
        if (this.isBlock) return;
        this.callbackFinish = callback;
        const delayTime = 0.5;
        this.canF2R = true;
        this.callBackDelayStop = () => {
            this.symbolValue = symbolValue;
            this.isShowingResult = true;
            let distanceMove = this.totalCircular * this.unitDistance;
            this.targetY = this.reelNode.position.y - distanceMove + this.totalScrolling;
            this.stopSpinningWithSteps();
        }
        this.unschedule(this.callBackDelayStop);
        this.scheduleOnce(this.callBackDelayStop, delayTime);
    }

    stopSpinningWithSteps() {
        if (this.spinAction) {
            this.spinAction.stop();
        }
        this.tweenTime = Math.abs((this.targetY - this.reelNode.position.y)) * 1.0 / (this.scrollingSpeed * this.multiplierFactor);
        this.spinAction = tween(this.reelNode)
            .to(this.tweenTime, { position: new Vec3(0, this.targetY - this.stopEasingDistance, 0) },
            {
                easing: 'sineOut',
                onUpdate: () => {
                    this.circulateSymbols();
                }
            })
            .call(() => {
                this.canF2R = false;
            })
            .to(0.05, { position: new Vec3(0, this.targetY, 0)},
            {
                onUpdate: () => {
                    this.circulateSymbols();
                }
            })
            .call(() => {
                this.onFinishSpin();
            })
            .start();
    }

    onFinishSpin() {
        this.reelNode.setPosition(this.reelNode.position.x, 0);
        this._symbols.sort((a, b) => { return a.position.y - b.position.y });
        this.spinAction = null;
        this.isWaitingResult = false;
        this.isRolling = false;
        this.callbackFinish && this.callbackFinish();
        for (let i = 0; i < this._symbols.length; i++) {
            const symbol = this._symbols[i];
            if (symbol) {
                symbol.setPosition(new Vec3(symbol.position.x, this.symbolPos[i], 0));
                if (i === this.showSymbolIndex && this.symbolValue !== undefined && this.symbolValue !== null) {
                    symbol.changeSymbol(this.symbolValue, this.betDenom);
                } else {
                    symbol.disableLabel && symbol.disableLabel();
                }
            }
        }
        this.currentIndex = 0;
    }

    upgradeCredit(symbolValue) {
        this.symbolValue = symbolValue;
        this.reelNode.children.forEach(item => {
            if (item) {
                (item as any).changeSymbol(this.symbolValue || this.getRandomSymbolValue(), this.betDenom);
            }
        });
    }

    fastToResult() {
        if (!this.canF2R || this.isBlock) return;
        if (this.circularCount >= this.totalCircular - 1) {
            return;
        }
        this.canF2R = false;
        this.scrollingSpeed = this.config.MAX_SPEED * this.f2rFactor;;
        if (this.callBackDelayStop) {
            this.unschedule(this.callBackDelayStop);
            this.callBackDelayStop();
        } else
            if (this.spinAction) {
                this.stopSpinningWithSteps();
            }
    }

    setMode(mode) {
        this.mode = mode;
        this.multiplierFactor = this.mode === 'FAST' ? 1 : 5;
        this.totalCircular = this.mode === 'FAST' ? this.fastTotalCircular : this.turboTotalCicular;
    }

    createSymbols() {
        if (this.poolFactory && !this.isBlock) {
            const startY = -(this.showNumber / 2 + this.botBuffer - 0.5) * this.unitDistance;
            for (let i = 0; i < this.totalSymbol; i++) {
                const symbol = this.poolFactory.getObject(this.symbolPrefabName);
                if (symbol) {
                    symbol.active = true;
                    symbol.parent = this.reelNode;
                    symbol.setSiblingIndex(i);
                    const x = 0;
                    const y = startY + i * this.unitDistance;
                    symbol.position = new Vec3(x, y, 0);
                    if (this.creditLabelPrefabName && this.miniReel) {
                        const label = this.poolFactory.getObject(this.creditLabelPrefabName);
                        if (label) {
                            symbol.addChild(label);
                            label.position = new Vec3(0, 0, 0);
                        }
                    }
                    const symbolValue = this.getRandomSymbolValue();
                    (symbol as any).changeSymbol(symbolValue, this.betDenom);
                    if (i === this.showSymbolIndex && Number(symbolValue) > 0) {
                        this.symbolValue = symbolValue;
                    } else {
                        (symbol as any).disableLabel && (symbol as any).disableLabel();
                    }
                    this._symbols.push(symbol);
                    this.symbolPos.push(symbol.position.y);
                }
            }
        }
    }

    getRandomSymbolValue() {
        const { MINI_SYMBOL_NAME_LIST, LIST_JACKPOTS } = this.config;
        if (this.symbolNameList && this.symbolNameList.length > 0) {
            return this.symbolNameList[Math.floor(Math.random() * this.symbolNameList.length)];
        } else {
            this.symbolNameList = this.miniReel ? MINI_SYMBOL_NAME_LIST : LIST_JACKPOTS;
            return this.getRandomSymbolValue();
        }
    }

    setupBonusRollResume(symbolValue) {
        if (this.isBlock) return;
        this.symbolValue = symbolValue;
        for (let i = 0; i < this.reelNode.children.length; i++) {
            let botItem = this.reelNode.children[i];
            if (this.symbolValue) {
                (botItem as any).changeSymbol(this.symbolValue, this.betDenom);
            } else {
                (botItem as any).changeSymbol(this.getRandomSymbolValue(), this.betDenom);
            }
        }

    }

    update(dt) {
        if (this.autoUpdate) {
            this.updatePosition(dt);
        }
    }

    updateBetDenom(betDenom) {
        this.betDenom = betDenom;
        this.reelNode.children.forEach(item => {
            if (item) {
                item.emit("UPDATE_BET_DENOM", this.betDenom);
            }
        });
    }

    unBlockJackpot(active = false, callback = null) {
        this.isBlock = true;
        this.unblockNode.active = active;
        if (active) {
            //this.unblockNode.getComponent(cc.Animation).play('VFX_Jackpot_Unlock_Animation');
        }
        if (callback) {
            this.scheduleOnce(() => {
                callback && callback();
                callback = null;
            }, .5);
        }
    }

    updateAllSymbols(symbolName) {
        let rs = isNaN(symbolName) ? symbolName : parseInt(symbolName);
        warn("updateAllSymbols " + rs);
        this.reelNode.children.forEach((item, index) => {
            (item as any).changeSymbol(symbolName);
            if (isNaN(symbolName) == false) item.emit("ADD_NUMBER_INTRO", symbolName);
        });
    }

    hideIntro() {
        this.reelNode.children.forEach(item => {
            item.emit("HIDE_NUMBER_INTRO");
        });
    }

    updatePosition(dt) {
        if (this.isBlock) return;
        if (this.isRolling) {
            if (this.scrollingSpeed < this.config.MAX_SPEED) {
                this.scrollingSpeed += this.config.ACCEL * dt
            } else if (this.canF2R) {
                this.scrollingSpeed = this.config.MAX_SPEED;
            }

            if (this.scrollingSpeed > this.config.MAX_SPEED) this.scrollingSpeed = this.config.MAX_SPEED;            
            if (!this.isShowingResult) {
                let y = this.reelNode.position.y;
                y -= this.scrollingSpeed * dt;
                this.reelNode.position = new Vec3(this.reelNode.position.x, y, 0);
            }
            if (this.previousY != this.reelNode.position.y) {
                this.totalScrolling += Math.abs((this.previousY - this.reelNode.position.y));
                this.previousY = this.reelNode.position.y;
            }
        }

        if (!this.isShowingResult) {
            this.circulateSymbols();
        }
    }

    circulateSymbols() {
        while (this.totalScrolling >= this.unitDistance) {
            let botItem = this._symbols[this.currentIndex];
            (botItem as any).isRealSymbol = false;
            let y = botItem.position.y;
            y += this.unitDistance * this.totalSymbol;
            botItem.position = new Vec3(botItem.position.x, y, 0);
            this.currentIndex = (this.currentIndex + 1) % this.totalSymbol;
            this.totalScrolling = this.totalScrolling - this.unitDistance;
            let symbolName: any = this.getRandomSymbolValue();
            if (this.isShowingResult) {
                this.circularCount += 1;
                const offsetToStop = this.showNumber + this.topBuffer - 1;
                const stopCount = this.totalCircular - offsetToStop;
                if (this.circularCount >= stopCount
                    && this.circularCount < stopCount + this.showNumber
                    && this.symbolValue !== undefined
                    && this.symbolValue != null) {
                    (botItem as any).changeSymbol(this.symbolValue, this.betDenom);
                    (botItem as any).isRealSymbol = true;
                }
                else {
                    (botItem as any).changeSymbol(symbolName, this.betDenom);
                }
            } else {
                (botItem as any).changeSymbol(symbolName, this.betDenom);
            }
        }
    }
}
