
import { _decorator, Node,Tween,tween } from 'cc';
const { ccclass, property } = _decorator;
import { GameObject } from '../../core/GameObject';
 
@ccclass('BetSelectionMgr')
export class BetSelectionMgr extends GameObject {
    @property(Node)
    overlay = null;
    @property(Node)
    betSelectionPanel = null;
    isShowing = false;

    tweenShow: Tween<any> = null;

    onLoad() {
        super.onLoad();
        this.node.on("SHOW", this.show, this);
        this.node.on("HIDE", this.hide, this);
        
        this.node['SHOW']=this.show.bind(this);
        this.node['HIDE']=this.hide.bind(this);

    }
    start(){
        this.overlay.active = false;
        this.betSelectionPanel.active = false
        this.isShowing = false;
    }
    show() {
        if (this.isShowing) return;
        if (this.tweenShow) this.tweenShow.stop();
        this.tweenShow = tween(this.node)
            .call(() => {
                this.isShowing = true;
                this.overlay.active = true;
                this.setOpacity(this.overlay, 0);
                this.fadeTo(this.overlay, 0.1, 120, { easing: 'sineIn' });
            })
            .delay(0.1)
            .call(() => {
                this.betSelectionPanel.active = true;
                this.betSelectionPanel.emit("UPDATE_VALUE");
                this.betSelectionPanel.emit("SHOW");
                this.tweenShow = null;
            })
        this.tweenShow.start();
    }
    hide() {
        if (this.tweenShow) this.tweenShow.stop();
        this.tweenShow = tween(this.node)
            .call(() => {
                this.isShowing = false;
                this.betSelectionPanel.emit("HIDE");
            })
            .delay(0.22)
            .call(() => {
                this.betSelectionPanel.emit("CLEAR_ALL_BET");
                this.betSelectionPanel.active = false;
                this.fadeOut(this.overlay, 0.1, { easing: 'sineIn' });
            })
            .delay(0.1)
            .call(() => {
                this.overlay.active = false;
                this.node.active = false;
                this.tweenShow = null;
            })
        this.tweenShow.start();
    }
}