
import { _decorator, Component, sys } from 'cc';
import { setOpacity } from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('GameMode')
export class GameMode extends Component {

    @property
    garbageCollectWhenExit: boolean = true;

    protected callBackWhenHide: any;

    onLoad() {
        (this.node as any).init = this.init.bind(this);
        (this.node as any).enter = this.enter.bind(this);
        (this.node as any).exit = this.exit.bind(this);
        (this.node as any).hide = this.hide.bind(this);
        (this.node as any).show = this.show.bind(this);
        (this.node as any).reset = this.reset.bind(this);
        (this.node as any).stateUpdate = this.stateUpdate.bind(this);
        (this.node as any).stateResume = this.stateResume.bind(this);
        (this.node as any).resetCallbackWhenHide = this.resetCallbackWhenHide.bind(this);
    }
    init(isActive = false) {
        this.node.emit('GAME_INIT');
        setOpacity(this.node, 1);
        this.node.active = false;
        if (isActive) {
            setOpacity(this.node, 255);
            this.node.active = true;
        }
    }

    stateResume(callback) {
        this.node.emit('GAME_RESUME');

        if (callback && typeof callback == "function") {
            callback();
        }
    }

    stateUpdate(callback) {
        this.node.emit('GAME_UPDATE');

        if (callback && typeof callback == "function") {
            callback();
        }
    }

    show(callback = null) {
        this.node.active = true;
        setOpacity(this.node, 255);
        this.node.emit('GAME_SHOW');
        if (callback && typeof callback == "function") {
            callback();
        }
    }

    exit() {
        if (this.callBackWhenHide && typeof this.callBackWhenHide == "function") {
            this.callBackWhenHide();
            this.callBackWhenHide = null;
        }
        this.hide();
        this.node.emit('GAME_EXIT');
        this.node.active = false;
        if(sys.isNative && this.garbageCollectWhenExit){
            sys.garbageCollect();
        }
    }

    hide(callback = null) {
        setOpacity(this.node, 1);
        this.node.emit('GAME_HIDE');
        if (callback && typeof callback == "function") {
            callback();
        }
        this.node.active = false;
    }

    enter(data, callback) {
        this.node.active = true;
        this.show();
        this.callBackWhenHide = callback;
        this.node.emit('GAME_ENTER', data);
    }

    reset(callback) {
        this.node.emit('GAME_RESET');
        if (callback && typeof callback == "function") {
            callback();
        }
    }
    resetCallbackWhenHide() {
        if (this.callBackWhenHide && typeof this.callBackWhenHide == "function") {
            this.callBackWhenHide = null;
        }
    }
}