
import { _decorator, CCFloat } from 'cc';
const { ccclass, property } = _decorator;
import { SlotUIScene } from "./../component/SlotUIScene";

@ccclass('IntroFree')
export class IntroFree extends SlotUIScene {
    @property({ type: CCFloat })
    timeShow: number = 2;

    onLoad() {
        super.onLoad();
        window["introFree"] = this;
    }

    show() {
        super.show();
        this.scheduleOnce(this.exit, this.timeShow);
    }
}