
import { _decorator, Label } from 'cc';
import {BaseTableHistoryDetail} from '../BaseTableHistoryDetail';
import { formatMoney } from '../../../../cc-share/common/utils';

const { ccclass, property } = _decorator;

@ccclass('SlotJackpotHistoryDetail')
export class SlotJackpotHistoryDetail extends BaseTableHistoryDetail {
    @property({ type: Label }) 
    session: Label = null;
    
    updateDetailView(data, pages) {
        super.updateDetailView(data, pages);
        this.session.string = "#" + data.psId.substring(data.psId.length - 8, data.psId.length);
        const {matrix, mformat, subsym} = data.exDt;
        const format = mformat || this.config.NORMAL_TABLE_FORMAT;
        this.renderTable(matrix, format, subsym);
    }

    updateTotalWinAmount(data: any){
        this.totalWinAmount.string = '0';
        if(data && data.jpAmt){
            this.totalWinAmount.string = formatMoney(parseInt(data.jpAmt));
        }
    }
}

