import * as cc from 'cc';
import loadConfigAsync from '../shareServices/loadConfigAsync';
import {setDeviceOrientation, setOpacity} from "./utils";
const lodash = globalThis._;

class GameCommonUtils {
    gameCanvasSize: any = {};
    getUrlParam = (name: any) => {
        if (cc.sys.isNative) return null;
        const url = new URL(window.location);
        return url.searchParams.get(name);
    };

    isLoginIframe() {
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        return LOGIN_IFRAME === true;
    };

    handleBackToLobby() {
        const {IS_PRODUCTION, LOBBY_SCENE_NAME, IS_LOBBY_PORTRAIT} = loadConfigAsync.getConfig();
        if (IS_PRODUCTION) {
            cc.director.emit("INTEGRATION_BACK_TO_LOBBY");
        } else {
            const scene: any = cc.director.getScene();
            const nodePersist = scene.getChildByName('OverlayPersistent');
            if (nodePersist) {
                cc.game.removePersistRootNode(nodePersist);
            }
            setDeviceOrientation(IS_LOBBY_PORTRAIT ? true : false);
            cc.director.preloadScene(LOBBY_SCENE_NAME, () => {
                cc.director.loadScene(LOBBY_SCENE_NAME);
            });
        }
    }

    handleReleaseBundle() {
    };

    checkConditionCloseGameIframe = () => {
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if  (!LOGIN_IFRAME) {
            return true;
        }
        const returnUrl = this.getUrlParam('ru');
        return (returnUrl && LOGIN_IFRAME);
    };

    handleCloseGameIframe = () => {
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if (!LOGIN_IFRAME) {
            this.handleBackToLobby();
            return;
        }

        const returnUrl = this.getUrlParam('ru');
        if (returnUrl && LOGIN_IFRAME) {
            if (returnUrl.trim() === 'close') {
                window.close();
            } else {
                window.location.href = returnUrl;
            }
        } else {
            location.reload();
        }
    };

    handleFlowOutGame = () => {
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if (LOGIN_IFRAME) {
            if (window && window.location) {
                window.location.reload();
            }
        } else {
            this.handleBackToLobby();
        }
    };

    handleBackLogin = () => {
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if (LOGIN_IFRAME) {
            if (window && window.location) {
                window.location.reload();
            }
        } else {
            this.handleBackToLobby();
        }
    };

    getMessageSlot = (mess = {}) => {
        const { LOGIN_IFRAME } = loadConfigAsync.getConfig();
        let language = LOGIN_IFRAME ? this.getUrlParam('l') : cc.sys.localStorage.getItem('l');
        let result = {};
        if (globalThis.slotLanguageData) { 
            result = new globalThis.slotLanguageData(language).getMessageSlot(mess);
        }
        return result;
    };

    getBetValueWithGame = (gameId: any, listBet: any) => {
        if (!gameId) return '';

        let betValue = cc.sys.localStorage.getItem('betValueWithGame');
        if (lodash.isEmpty(betValue)) {
            const newObj: any = {};
            newObj[gameId] = '';
            cc.sys.localStorage.setItem('betValueWithGame', JSON.stringify(newObj));
        } else {
            betValue = JSON.parse(betValue);
            if (lodash.isEmpty(listBet)) {
                return betValue[gameId];
            } else {
                if (lodash.isArray(listBet) && listBet.includes(betValue[gameId])) {
                    return betValue[gameId];
                } else {
                    let isExist = false;
                    Object.keys(listBet).map((betId) => {
                        if (listBet[betId] === betValue[gameId]) {
                            isExist = true;
                        }
                    });
                    if (isExist) {
                        return betValue[gameId];
                    }
                }
            }
        }
        return '';
    };

    setBetValueWithGame = (gameId: any, betId: any)  => {
        let betValue: any = cc.sys.localStorage.getItem('betValueWithGame');
        if (lodash.isEmpty(betValue)) {
            const newObj: any = {};
            newObj[gameId] = betId;
            cc.sys.localStorage.setItem('betValueWithGame', JSON.stringify(newObj));
        } else {
            betValue = JSON.parse(betValue);
            betValue[gameId] = betId;
            cc.sys.localStorage.setItem('betValueWithGame', JSON.stringify(betValue));
        }
    };

    getKeyWithGame = (gameId: any, key: any, value = '') => {
        if (!gameId || !key) return '';

        let betLinesValue = cc.sys.localStorage.getItem(key);
        if (lodash.isEmpty(betLinesValue)) {
            const newObj: any = {};
            newObj[gameId] = value;
            cc.sys.localStorage.setItem(key, JSON.stringify(newObj));
        } else {
            betLinesValue = JSON.parse(betLinesValue);
            return betLinesValue[gameId] ? betLinesValue[gameId] : value;
        }
        return value;
    };

    setKeyWithGame = (gameId: any, key: any, value = '')  => {
        if (!gameId || !key) return '';

        let betLinesValue: any = cc.sys.localStorage.getItem(key);
        if (lodash.isEmpty(betLinesValue)) {
            const newObj: any = {};
            newObj[gameId] = value;
            cc.sys.localStorage.setItem(key, JSON.stringify(newObj));
        } else {
            betLinesValue = JSON.parse(betLinesValue);
            betLinesValue[gameId] = value;
            cc.sys.localStorage.setItem(key, JSON.stringify(betLinesValue));
        }
    };

    optimizeScrollView = (listView: any) => {
        let view = listView.parent;
        let viewRect = cc.rect(- view.width / 2, - listView.y - view.height, view.width, view.height);
        for (let i = 0; i < listView.children.length; i++) {
            const node = listView.children[i];
            if (viewRect.intersects(node.getBoundingBox())) {
                setOpacity(node, 255);
            }
            else {
                setOpacity(node, 0);
            }
        }
    };
}

const gameCommonUtils = new GameCommonUtils();
export default gameCommonUtils;
