
import { _decorator, Node, Prefab, instantiate, sys } from 'cc';
import { BaseDynamicLoading } from './BaseDynamicLoading';
const { ccclass } = _decorator;

@ccclass('DynamicLoadingPrefabControl')
export class DynamicLoadingPrefabControl extends BaseDynamicLoading {
    protected _isHighPerformance: boolean = false;
    protected _usingObjects: Node[] = [];
    protected _isLoaded: boolean = false;

    onLoad() {
        super.onLoad();
    }

    //dnvuanh remove causing bad performance
    /*onEnable(){
        if(this.eventManager){
            this.eventManager.on(EventNames.ON_CHANGE_PERFORMANCE, this.onChangePerformance, this);
        }
    };

    onDisable(){
        if(this.eventManager){
            this.eventManager.off(EventNames.ON_CHANGE_PERFORMANCE, this.onChangePerformance, this);
        }
    }*/

    onChangePerformance(isHighPer:boolean){
        this._isHighPerformance = isHighPer;
        if(isHighPer){
            if(this._isLoaded) return;
            this.createObjectsAsyncByPaths(()=>{
                if(!this._isHighPerformance){
                    this.destroyDynamicLoadingObjects();
                }
            });
        }else{
            this.destroyDynamicLoadingObjects();
        }
    }

    createObjectsAsyncByPaths(completeCB:Function = null){
        if (!this._localBundle) return;
        let count = 0;
        for(let i = 0; i<this.assetPaths.length; i++){
            const assetPathConfig = this.assetPaths[i];
            if(assetPathConfig){
                this._dynamicLoadByPath(assetPathConfig.path, assetPathConfig.id, assetPathConfig.parentNode, (prefab)=>{
                    if(this._isHighPerformance){
                        const node:Node = instantiate(prefab as Prefab);
                        node.active = true;
                        node.parent = assetPathConfig.parentNode;
                        this._usingObjects.push(node);
                    }
                    count++;
                    if(count>=this.assetPaths.length){
                        completeCB&&completeCB();
                    }
                });
            }
        }
        this._isLoaded = true;
    };

    onAssetBundleReady() {
        if(!this._isLoaded){
            this._isHighPerformance = true;
            this.createObjectsAsyncByPaths(()=>{
                if(!this._isHighPerformance){
                    this.destroyDynamicLoadingObjects();
                }
            });
        }
    };

    destroyDynamicLoadingObjects(){
        for(let i = 0; i<this._usingObjects.length; i++){
            const node = this._usingObjects[i];
            if(node){
                node.destroy();
            }
        }
        this._usingObjects = [];
        // this.clearResourcesLoaded();
        this._isLoaded = false;
        if(sys.isNative){
            sys.garbageCollect();
        }
    }
}