import { _decorator, Component, Node, sys, PageView, Button, tween, Tween } from 'cc';
import { fadeOut, setOpacity } from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('IntroGame')
export class IntroGame extends Component {
    @property(PageView)
    pageView: PageView = null;
    @property(Button)
    btnPrev: Button = null;
    @property(Button)
    btnNext: Button = null;
    @property(Node)
    introNodes: Node[] = [];
    @property(Node)
    anchorPages: Node[] = [];
    @property(Button)
    btnJoinGame: Button = null;
    @property(Node)
    doNotShowNode: Node = null;
    @property(Node)
    progressBar: Node = null;
    @property
    isLoading: boolean = false;
    @property
    gameId: string = "";

    currentIndex: number = 0;
    isHideIntroGame: boolean = false;

    start() {
        this.node.on("SHOW_BUTTON_JOIN_GAME", this.showButtonJoinGame, this);
        this.pageView.node.on('scroll-ended', this._onPageTurning, this);
        this.init();
    }
    init() {
        this.currentIndex = globalThis.currentIntroPage || 0;
        if (this.isLoading) this.currentIndex = 0;
        this.showPageIntro();
        this.pageView.scrollToPage(this.currentIndex, 0);
        if (!this.isLoading) {
            const cacheValue = JSON.parse(sys.localStorage.getItem("HideIntroGame")) || {};
            this.isHideIntroGame = !!cacheValue[this.gameId];
            this.node.active = !this.isHideIntroGame;
            setOpacity(this.node, 255);
            this.showButtonJoinGame(false);
        }
    }
    _onPageTurning() {
        this.currentIndex = this.pageView.getCurrentPageIndex();
        this.showPageIntro();
    }
    onNextIntro() {
        this.currentIndex++;
        this.showPageIntro();
        this.pageView.scrollToPage(this.currentIndex, 0.3);
    }
    onPrevIntro() {
        this.currentIndex--;
        this.showPageIntro();
        this.pageView.scrollToPage(this.currentIndex, 0.3);
    }
    showPageIntro() {
        globalThis.currentIntroPage = this.currentIndex;
        this.btnPrev.interactable = (this.currentIndex !== 0);
        this.btnNext.interactable = (this.currentIndex !== this.introNodes.length - 1);

        this.introNodes.forEach((item, index) => {
            const isShow = index == this.currentIndex;
            item.active = isShow;
            this.anchorPages[index].active = isShow;
        });
    }
    onClickJoinGame() {
        this.cacheHideIntroGame();
        tween(this.node)
            .call(() => fadeOut(this.node, 0.2))
            .delay(0.2)
            .call(() => this.node.active = false)
            .start();
    }
    cacheHideIntroGame() {
        const cacheValue = {};
        cacheValue[this.gameId] = this.isHideIntroGame;
        sys.localStorage.setItem("HideIntroGame", JSON.stringify(cacheValue));
    }
    onCheckDoNotShowAgain() {
        this.isHideIntroGame = !this.isHideIntroGame;
    }
    enableButtonJoinGame(isEnable) {
        this.btnJoinGame && (this.btnJoinGame.interactable = isEnable);
    }
    showButtonJoinGame(isShow) {
        this.progressBar && (this.progressBar.active = !isShow);
        this.doNotShowNode && (this.doNotShowNode.active = isShow);
        this.btnJoinGame && (this.btnJoinGame.node.active = isShow);
    }
    onDisable() {
        Tween.stopAllByTarget(this.node);
    }
}

