
import { _decorator, Label, Tween, Node, log, error} from 'cc';
import { GameObject } from '../core/GameObject';
import {formatMoney, tweenMoney } from '../../../cc-share/common/utils';
import { fadeOut } from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('WinAmount')
export class WinAmount extends GameObject {
    _currentValue: number = 0;
    _winValue: number;
    _callBackOnComplete: Function;
    _tweenValue: any;
    _tweenFade: Tween<Node>;

    @property(Label)
    labelWinAmount: Label;
    protected isDebug: boolean = true;

    get currentValue(): number {
        return this._currentValue;
    }

    set currentValue(value: number) {
        if (this._currentValue !== value) {
            this._currentValue = value;
            this._updateLabelWinAmount();
        }
    }

    _updateLabelWinAmount() {
        this.labelWinAmount.string = this.currentValue > 0 ? "" + formatMoney(this._currentValue) : "";
    }

    onLoad() {
        super.onLoad();
        this.node.on("UPDATE_WIN_AMOUNT",this.updateWinAmount,this);
        this.node.on('FAST_UPDATE_WIN_AMOUNT', this.fastUpdateWinAmount, this);
        this.node.on('RESET_NUMBER', this.clearWinAmount, this);
        this.node.on('FADE_OUT_NUMBER', this.fadeOutNumber, this);
        this.labelWinAmount.string = '';
    }

    fastUpdateWinAmount({ value, time }) {
        if (!value) value = this._winValue;
        if (value < this._currentValue || value < this._winValue) {
            error("Logic fail: Could not speed up to the smaller value", { current: this._currentValue, endValue: this._winValue, fastUpdateValue: value });
            return;
        }
        this._tweenWinAmount(value, time);
    }

    clearWinAmount() {
        if (this.isDebug) {
            log("clearWinAmount");
        }
        this._resetLabel();
        this.currentValue = 0;
        this.labelWinAmount.string = "";
        this._winValue = 0;
    }

    fadeOutNumber(time: number = 1) {
        if (!this.labelWinAmount || this._winValue <= 0) return;
        (this.node as any).isFading = true;
        if (this._tweenValue) {
            this.currentValue = this._winValue;
            this.labelWinAmount.string = formatMoney(this.currentValue);
        }
        this.stopTweenValue();
        this.resetCallBack();
        this.scheduleOnce(() => {
            this._tweenFade = fadeOut(this.labelWinAmount.node, time, {
                onComplete: () => {
                    (this.node as any).isFading = false;
                    this.clearWinAmount();
                }
            })
        }, 0.1);
    }

    updateWinAmount({ value, time, isLastest }, callback?: Function) {
        if (this._tweenFade) {
            this._tweenFade.stop();
        }
        if (isLastest && this.dataStore) {
            value = this.dataStore.playSession.winAmount;
        }
        if (value < this._currentValue || value < this._winValue) {
            log("Logic fail somewhere, script clear winamount have not ran yet", { current: this._currentValue, endValue: this._winValue, fastUpdateValue: value });
            this.clearWinAmount();
        }
        this._callBackOnComplete = callback;
        this._winValue = value;
        time = this._currentValue === value ? 0 : time;
        this._tweenWinAmount(value, time);
    }

    _tweenWinAmount(value: number, time: number) {
        this._resetLabel();
        if (time === 0) {
            this.currentValue = value;
            this.labelWinAmount.string = formatMoney(this.currentValue);
            this.resetCallBack();
            log("show win Amount instantly", { value, time });
            return;
        }
        this._tweenValue = tweenMoney(this.labelWinAmount, time, value, {
            onComplete: () => {
                this.resetCallBack();
                this._tweenValue = null;
            }
        })
        
    }

    _resetLabel() {
        this.stopTweenFade();
        this.stopTweenValue();
        this.setOpacity(this.labelWinAmount.node, 255);
        (this.node as any).isFading = false;
    }

    resetCallBack() {
        this._callBackOnComplete && this._callBackOnComplete();
        this._callBackOnComplete = null;
    };

    stopTweenFade(){
        this._tweenFade && this._tweenFade.stop();
        this._tweenFade = null;
    }

    stopTweenValue() {
        this._tweenValue && this._tweenValue.stop();
        this._tweenValue = null;
    }

    onDestroy() {
        this.stopTweenFade();
        this.stopTweenValue();
    }
}
