
import { _decorator, Component, Node } from 'cc';
import { setOpacity, fadeOut } from '../../../../cc-common/cc-share/common/utils';

const { ccclass, property } = _decorator;

@ccclass('LoadingTransition')
export class LoadingTransition extends Component {
    @property
    delayTime: number = 1;
    @property
    fadeTime: number = 1;

    start () {
        this.node.active = true;
        setOpacity(this.node, 255);
        this.scheduleOnce(()=>{
            fadeOut(this.node, this.fadeTime, {onComplete:()=>{
                this.node.active = false;
                this.node['tweenFade'] = null;
            }})
        }, this.delayTime);
    }

    onDisable(){
        this.unscheduleAllCallbacks();
        if (this.node['tweenFade']) {
            this.node['tweenFade'].stop();
            this.node['tweenFade'] = null;
        }
    }
}
