
import { _decorator, Component } from 'cc';
import {DataStore} from "../../core/DataStore"; 
import { SlotSceneType } from '../CustomEnum';
const { ccclass } = _decorator;

@ccclass('MiniWriter')
export class MiniWriter extends Component {
    get dataStore(): DataStore {
        return this.node["dataStore"] || null;
    };
    currentPick: number = null;
    onLoad() {
        this.node['writer'] = this;
    }

    makeScriptMiniGameStart() {
        const listScript = [];

        listScript.push({
            command: "_miniGameStart",
        });
        return listScript;
    }

    makeScriptMiniGameClick(boxIndex:number) {
        this.currentPick = boxIndex;
        return [
            {command: "_sendRequestPlayMiniGame", data: {openCell: boxIndex}}
        ];
    }

    makeScriptResultReceive() {
        const {jackpotInfo, extraVipJackpot} = this.dataStore.playSession;
        let listScript = [];
        if (jackpotInfo) {
            let infoJP = jackpotInfo[jackpotInfo.length - 1];
            let arrayJP = infoJP.split(';');
            const jpId = arrayJP[0];
            const jpType = jpId.slice(7);
            const jackpotAmount = Number(arrayJP[1]);

            listScript.push({
                command: "_pauseUpdateJP"
            });

            listScript.push({
                command: "_updateValueJP",
                data: {
                    jpType: jpType,
                    jpValue: jackpotAmount - (extraVipJackpot || 0)
                }
            });
        }

        listScript.push({
            command: "_showResult",
        });
        return listScript;
    }

    makeScriptShowResult() {
        const listScript = [];
        const { winAmountPS, bonusPlayRemain, bonusGameMatrix, bonusValue, bonusGameWinAmount, bgWinAmtCurrent, jackpotInfo } = this.dataStore.playSession;
        const {betData} = this.dataStore;

        listScript.push({
            command: "_openPickedItem",
            data: {index: this.currentPick, value: bonusValue}
        });

        if (winAmountPS && winAmountPS > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: {winAmount:winAmountPS, time: 300}
            });
        }

        if (bonusPlayRemain) {
            listScript.push({
                command: "_miniGameRestart"
            });
            return listScript;
        } else {
            listScript.push({
                command: "_openAllItems",
                data: bonusGameMatrix
            });

            if (jackpotInfo) {
                let infoJP = jackpotInfo[jackpotInfo.length - 1];
                let arrayJP = infoJP.split(';');
                let jackpotAmount = 0;
                let jpLineId;
                if (arrayJP && arrayJP.length > 2) {
                    jackpotAmount = Number(arrayJP[1]);
                    jpLineId = arrayJP[2];
                }
                if(jpLineId){
                    listScript.push({
                        command: "_showJackpotPayLine",
                        data:{
                            jpPayLineID: jpLineId,
                        }
                    });
                }
                
                listScript.push({
                    command: "_showUnskippedCutscene",
                    data:{
                        name: SlotSceneType.JackpotWin,
                        content: {
                            winAmount: jackpotAmount,
                            currentBetData:betData.getTotalBet(),
                        }
                    }
                });
                listScript.push({
                    command: "_resumeUpdateJP",
                });
            } else {
                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.TotalWinPanel,
                        content: {
                            winAmount: bgWinAmtCurrent?bgWinAmtCurrent:bonusGameWinAmount,
                        }
                    }
                });
                listScript.push({
                    command: "_updateWinningAmount",
                    data: {winAmount: winAmountPS, time: 300}
                });
            }
            listScript.push({
                command: "_gameExit",
            });
            return listScript;
        }
    }
}