
import { _decorator, Component, Node, Event, Tween, ITweenOption } from 'cc';
import { SlotSoundPlayer } from '../sound/SlotSoundPlayer';
import { DataStore } from './DataStore';
import { Director } from './Director';
import { UIManager } from './UIManager';
import { getOpacity, setOpacity, fadeIn, fadeOut, fadeTo } from "../../../cc-share/common/utils";
import { EventListenerManager } from '../../../cc-share/components/EventListenerManager';
import { CustomEvent } from '../component/CustomType';

const { ccclass } = _decorator;

@ccclass('GameObject')
export class GameObject extends Component {
    get mainDirector(): Director {
        return (this.node as any).mainDirector;
    }
    get config(): any {
        return (this.node as any).config;
    }
    get dataStore(): DataStore {
        return (this.node as any).dataStore;
    }
    get soundPlayer(): SlotSoundPlayer {
        if(this.mainDirector) return this.mainDirector.soundPlayer;
        return null;
    }
    get eventManager(): EventListenerManager {
        if(this.mainDirector) return this.mainDirector.eventManager;
        return null;
    }
    get guiMgr(): UIManager {
        if(this.mainDirector) return this.mainDirector.guiMgr;
        return null;
    }

    onLoad() {
        this.node.on("GET_GAME_MODEL", this.getGameModel, this);
        if (!this.node["mainDirector"]) {
            this.node.dispatchEvent(new CustomEvent("GET_GAME_MODEL", true));
        }
    }

    getGameModel(evt: Event) {
        if (this.config) {
            evt.propagationStopped = true;
            (evt.target as any).config = this.config;
            (evt.target as any).dataStore = this.dataStore;
            (evt.target as any).mainDirector = this.mainDirector;
        }
    }

    getOpacity(node: Node) {
        return getOpacity(node);
    }

    setOpacity(node: Node, opacity: number) {
        setOpacity(node, opacity);
    }

    fadeIn(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
        return fadeIn(target, duration, options);
    }

    fadeOut(target: Node, duration: number, options?: ITweenOption): Tween<Node> {
        return fadeOut(target, duration, options);
    }

    fadeTo(target: Node, duration: number, opacity: number, options?: ITweenOption): Tween<Node> {
        return fadeTo(target, duration, opacity, options);
    }

    onDestroy(){
        this.node.off("GET_GAME_MODEL", this.getGameModel, this);
        if(this.eventManager){
            this.eventManager.targetOff(this);
        }
        (this.node as any).mainDirector = null;
        (this.node as any).config = null;
        (this.node as any).dataStore = null;
    }
}
