
import { _decorator, Node,find,Camera,Vec3,Rect,UITransform } from 'cc';
const { ccclass, property } = _decorator;
import { GameObject } from '../../core/GameObject';
import { EventListenerManager } from '../../../../cc-share/components/EventListenerManager';

@ccclass('SlotTableTouchSymbol')
export class SlotTableTouchSymbol extends GameObject {
    @property
    spineAnimBorderName: string = 'VFX_Symbol_Light';

    eventListenterManager: EventListenerManager = null;
    isAllowTouchSymbol: boolean = true;
    protected canvas: Node = null;
    protected camera : Camera = null;
    allSymbols = [];
    curPoint = null;
    expandRow = 0;
    matrix = [];
    isNeedReset: boolean = false;

    onLoad() {
        super.onLoad();
        this.init();
        this.node.on("ALLOW_TOUCH_SYMBOL", this.allowTouchSymbol, this);
        if (!this.eventListenterManager) {
            this.eventListenterManager = this.mainDirector.eventManager;
        }
        this.registerEvent();
    }
    registerEvent() {
        if (this.eventListenterManager) {
            this.eventListenterManager.on("ON_SHOW_SYMBOL_INFO", this.onShowSymbolInfo, this);
        }
    }
    unRegisterAll() {
        if (this.eventListenterManager) {
            this.eventListenterManager.targetOff(this);
        }
    }

    allowTouchSymbol(isAllow = false) {
        this.isAllowTouchSymbol = isAllow;
        this.guiMgr && this.guiMgr.node.emit("HIDE_INFO_SYMBOL");
    }
    init() {
        this.canvas = find('Canvas');
        if (this.canvas) {
            this.camera = this.canvas.getComponentInChildren(Camera);
        }
        if (this.camera) {
            this.node.on(Node.EventType.TOUCH_END, this.onTouchEnd, this);
        }
    }
    onTouchEnd(evt) {
        let wlocation = new Vec3(0, 0, 0);
        let location = new Vec3(evt.getLocation().x, evt.getLocation().y, 0);
        this.camera.screenToWorld(location, wlocation);
        if (this.isAllowTouchSymbol == false)
            return;
        this.allSymbols = this.getAllSymbol();
        for (let index = 0; index < this.allSymbols.length; index++) {
            let symbol = this.allSymbols[index];
            let widthSymbol = this.config.SYMBOL_WIDTH;
            let heigthSymbol = this.config.SYMBOL_HEIGHT;
            this.curPoint = symbol.parent.getComponent(UITransform).convertToNodeSpaceAR(wlocation);
            let rect = new Rect(symbol.getPosition().x - (widthSymbol / 2), symbol.getPosition().y - (heigthSymbol / 2), widthSymbol, heigthSymbol)
            let isContains = rect.contains(this.curPoint);
            if (isContains) {
                const spineData = this.findSpineData(symbol.symbol);
                const spineBorder = this.findSpineData(this.spineAnimBorderName);
                this.guiMgr && this.guiMgr.node.emit("SHOW_INFO_SYMBOL", wlocation, symbol, spineData, spineBorder);
            }
        }
    }
    getAllSymbol() {
        const arr = [];
        const colCount = this.node['reels'].length;
        for (let col = 0; col < colCount; col++) {
            const reel = this.node['reels'][col]
            const showSymbols: Node [] = reel && reel.getShowSymbols();
            if (showSymbols && showSymbols.length > 0) {
                for (let row = 0; row < showSymbols.length; row++) {
                    const symbol = showSymbols[row];
                    if (symbol) arr.push(symbol);
                    symbol["isLeftBorder"] = col < Math.floor(colCount/2);
                    symbol["isRightBorder"] = col > Math.floor(colCount/2);
                    symbol["isMiddle"] =  col === Math.floor(colCount/2);
                    symbol["collumnInfo"] = col;
                    symbol["rowInfo"] = showSymbols.length - row; // reverse direction from top to bottom
                }
            }
        }
        return arr
    }
    findSpineData(animName) {
        return this.mainDirector.spineSkeletonDatabase.getSpineSkeletonData(animName);
    }
    onDestroy() {
        this.unRegisterAll();
    }
    onShowSymbolInfo(isShowing = false) {
        //override here
    }

}
