
import { _decorator, SpriteFrame, Label, Sprite, Layout } from 'cc';
import { GameObject } from '../core/GameObject';
import {convertAssetArrayToObject, formatMoney} from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('SlotTablePaylineInfo')
export class SlotTablePaylineInfo extends GameObject {
    @property
    symbolPrefix: string = "";
    @property({ type: SpriteFrame })
    smallSymbols = [];
    @property({ type: Label })
    lbLeft;
    @property({ type: Label })
    lbRight;
    @property({ type: Sprite })
    symbolImg;

    assets = [];
    showPaylineInfo: any = null;
    protected _textLayout: Layout = null;
    protected _lastPayLine: string = "";
    hasShowed : boolean = false;
    start() {
        this.assets = convertAssetArrayToObject(this.smallSymbols);
        if (this.config && this.config.PAY_LINE_ALLWAYS)
            this.showPaylineInfo = this.showPaylineInfoAllways;
        else if (!this.showPaylineInfo) {
            this.showPaylineInfo = this.showPaylineInfoLine;
        }
        this.node.on("SHOW_PAYLINE", this.showPaylineInfo, this);
        this.node.on("HIDE_PAYLINE", this.hidePaylineInfo, this);
        this.hidePaylineInfo();
        this._textLayout = this.node.getComponentInChildren(Layout);
    };

    showPaylineInfoLine({ line }) {
        const { payLineID, payLineWinNumbers, payLineWinAmount, symbolName } = line;
        const { LINE, WINS } = this.config.MESSAGE_DIALOG;

        this.node.active = true;
        this.lbLeft.string = LINE + " " + payLineID + " " + WINS + " " + payLineWinNumbers + " x";
        this.lbRight.string = " = " + formatMoney(payLineWinAmount) + "";
        this.symbolImg.spriteFrame = this.assets[this.symbolPrefix + symbolName];
        if (this._textLayout && this._lastPayLine !== payLineID) {
            this.showBodyInfo();
        }
        this._lastPayLine = payLineID;
    };

    showPaylineInfoAllways({ line }) {
        const { symbolName, totalWinAmount, symbolCount, combination, payableSymbol } = line;
        const betDenom = this.calculateBetDenom();

        this.node.active = true;
        this.lbLeft.string = Number(symbolCount);
        this.symbolImg.spriteFrame = this.assets[this.symbolPrefix + symbolName];

        const symbolPayTableString = 'trng ' + payableSymbol;
        const combinationString = combination > 1 ? ' (x' + combination + ')' : '';
        const calculateDenom = ' = ' + formatMoney((payableSymbol * combination)) + ' x ' + formatMoney(betDenom) + ' = ';
        const winAmount = formatMoney(totalWinAmount);
        this.lbRight.string = symbolPayTableString + combinationString + calculateDenom + winAmount;
        if (this._textLayout && this._lastPayLine !== symbolName) {
            this.showBodyInfo();
        }
        this._lastPayLine = symbolName;
    };

    showBodyInfo() {
        this._textLayout.updateLayout(true);
        if (!this.hasShowed) {
            this.setOpacity(this.node, 0);
            this.scheduleOnce(()=>{  
                this.setOpacity(this.node, 255);
            },0.01);
        } else {
            this.setOpacity(this.node, 255);
        }
        this.hasShowed = true;
    }

    calculateBetDenom() {
        const { mainBets } = this.dataStore.betData;
        const {TOTAL_BET_CREDIT} = this.config;
        const betIds = this.dataStore.playSession.betId;
        const betIndex = Object.keys(mainBets).find(key => key == betIds[0]);
        return Number(mainBets[betIndex]) / TOTAL_BET_CREDIT;
    };

    hidePaylineInfo(){
        if (this._textLayout) {
            this.unscheduleAllCallbacks();
        }
        this.lbLeft.string = '';
        this.lbRight.string = '';
        this.symbolImg.spriteFrame = null;
        this.node.active = false;
        this.hasShowed = false;
    };
}
