
import { _decorator, Component, sp } from 'cc';
import { SlotCustomDataType, CustomEvent } from './CustomType';
const { ccclass, property } = _decorator;

@ccclass('SpineSkeletonDatabase')
export class SpineSkeletonDatabase extends Component {
    @property({ type: SlotCustomDataType })
    private spineDataList: SlotCustomDataType[] = [];

    onEnable(){
        this.node.dispatchEvent(new CustomEvent("SET_UP_SPINE_DATABASE", true, {spineSkeletonDatabase:this}));
    }

    onDisable(){
        this.node.dispatchEvent(new CustomEvent("SET_UP_SPINE_DATABASE", true, {spineSkeletonDatabase:null}));
    }
    
    getSpineSkeletonData(spineName:string):sp.SkeletonData{
        for(let i = 0; i<this.spineDataList.length; i++){
            if(this.spineDataList[i].spineName === spineName){
                return this.spineDataList[i].spineData;
            }
        }
        return null;
    }
}