
import { _decorator, Node } from 'cc';
import { PoolFactory } from '../component/PoolFactory';
import {GameObject } from "../core/GameObject";
import { PerformanceLevel } from "../component/CustomEnum";

const { ccclass, property } = _decorator;
@ccclass('SlotTableNearWinEffect')
export class SlotTableNearWinEffect extends GameObject {
    @property
    startAtScatterCount: number = 3;
    @property
    startAtBonusCount: number = 3;
    @property
    startAtJackpotCount: number = 3;
    @property
    stopAtScatterCount: number = 5;
    @property
    stopAtBonusCount: number = 5;
    @property
    stopAtJackpotCount: number = 5;
    @property({type: Node})
    animNode = null;
    @property({type: Node})
    nearWinHolder = null;
    @property
    paylineSymbolPrefabName:string = '';
    @property
    bonusSymbol: string = 'R';
    @property
    scatterSymbol: string = 'A';
    @property
    jackpotSymbol: string = 'JP';
    @property
    isSkipNearWinTurbo: boolean = true
    protected nearWinList: any = [];

    protected playSoundNearWin : boolean = false;
    protected sfxNearWin:string = 'NEAR_WIN';
    protected poolFactory:PoolFactory = null;
    protected usingObjs:Node[] =[];
    protected animationLevel: number = 2;
    onLoad () {
        this.node.on("RESET_NEAR_WIN",this.reset,this);
        this.node.on("REEL_STOP_NEARWIN",this.reelStopNearWin,this);
        this.node.on("REEL_DELAY_NEAR_WIN",this.adjustReelDelay,this);
        this.reset();
        if(this.mainDirector){
            this.poolFactory = this.mainDirector.poolFactory;
        }
    };

    reset() {
        this.hideAnimNearWin();
        this.clearSymbolPaylines();
    };

    adjustReelDelay({reels, matrix}) {
        const tableFormat:any[] = (this.node as any).tableFormat;
        let countScatter = 0;
        let countBonus = 0;
        let countJackpot = 0;
        let foundNearWin = false;
        this.nearWinList = [];

        let isSkipWhenTurbo = this.dataStore && this.dataStore.modeTurbo && this.isSkipNearWinTurbo;
        if (isSkipWhenTurbo) return;
        for (let col = 0; col < matrix.length; col++) {
            const isNearWinScatter = countScatter >= this.startAtScatterCount && countScatter < this.stopAtScatterCount;
            const isNearWinBonus = countBonus >= this.startAtBonusCount && countBonus < this.stopAtBonusCount;
            let isNearWinJackpot = countJackpot >= this.startAtJackpotCount && countJackpot < this.stopAtJackpotCount;
            let isNearWin = (isNearWinScatter || isNearWinBonus || isNearWinJackpot);
            for (let row = 0; row < matrix[col].length; ++row) {
                const symbolValue = matrix[col][row];
                if (symbolValue === this.bonusSymbol) {
                    this.createPaylineSymbol(tableFormat[col], symbolValue, col, row);
                    countBonus++;
                } else if (symbolValue === this.scatterSymbol) {
                    this.createPaylineSymbol(tableFormat[col], symbolValue, col, row);
                    countScatter++;
                } else if (symbolValue === this.jackpotSymbol) {
                    this.createPaylineSymbol(tableFormat[col], symbolValue, col, row);
                    countJackpot++;
                } else {
                    // row = matrix[col].length - 1;
                }
            }
            isNearWin = isNearWin || isNearWinJackpot;
            foundNearWin = foundNearWin || isNearWin;

            if (foundNearWin) {
                this.nearWinList[col] = { isNearWinScatter, isNearWinBonus, isNearWin };
                reels[col].emit('EXTEND_TIME_STOP', isNearWin);
            }
        }
    };

    reelStopNearWin({count, context}) {
        this.hideAnimNearWin();
        if (!context.isFastToResult) {
            this.runAnimationNearWin(this.jackpotSymbol, count);
            this.runAnimationNearWin(this.scatterSymbol, count);
            this.runAnimationNearWin(this.bonusSymbol, count);
        }
        const reels = (this.node as any).reels;
        if (this.nearWinList[count] && this.nearWinList[count].isNearWin && !context.isFastToResult) {

            this.showAnimNearWin(count);

            for (let i = count; i < reels.length; i++) {
                if (this.nearWinList[i] && this.nearWinList[i].isNearWin)
                    reels[i].emit('UPDATE_NEAR_WIN_SPEED');
            }
        } else {
            this.stopSoundNearWin();
        }
        if (count >= reels.length) {
            this.clearSymbolPaylines();
            this.hideAnimNearWin();
            this.stopSoundNearWin();
        }
    };

    hideAnimNearWin() {
        this.animNode.active = false;
    };

    showAnimNearWin(count) {
        this.sfxNearWin && this.soundPlayer && this.soundPlayer.playSfx(this.sfxNearWin);
        this.animNode.active = true;
        this.animNode.setPosition(this.getXPosition(count), this.animNode.position.y);
    };

    clearSymbolPaylines() {
        if (!this.nearWinHolder || this.animationLevel<Number(PerformanceLevel.Medium)) return;
        this.usingObjs.forEach(paylineSymbol => {
            if (paylineSymbol) {
                const {col, row, symbol} = (paylineSymbol as any);
                this.node.emit('SHOW_STATIC_SYMBOL', col, row, symbol, true);
                this.poolFactory.removeObject(paylineSymbol);
            }
        });
        this.usingObjs = [];
        this.setOpacity(this.nearWinHolder, 1);
    };

    runAnimationNearWin(symbolName, currentIndex){
        if (!this.nearWinHolder || this.animationLevel<Number(PerformanceLevel.Medium)) return;
        this.setOpacity(this.nearWinHolder, 255);
        this.usingObjs.forEach(paylineSymbol => {
            if (paylineSymbol["symbol"] === symbolName && paylineSymbol["col"] == currentIndex -1) {
                paylineSymbol.active = true;
                paylineSymbol.emit("ENABLE_HIGHLIGHT");
                paylineSymbol.emit("PLAY_ANIMATION");
                const {col, row, symbol} = (paylineSymbol as any);
                this.node.emit('SHOW_STATIC_SYMBOL', col, row, symbol, false);
            }
        });
    };

    getXPosition(index) {
        const tableFormat = (this.node as any).tableFormat;
        let startX = -(tableFormat.length / 2 - 0.5) * (this.node as any).config.SYMBOL_WIDTH;
        return (startX + this.config.SYMBOL_WIDTH * index);
    };

    createPaylineSymbol(totalSymbol, symbol, col, row) {
        const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
        this.animationLevel = this.config[performanceLvl].NEAR_WIN_ANIMATION;
        if (!this.nearWinHolder || this.animationLevel<Number(PerformanceLevel.Medium)) return;
        let paylineSymbol:Node = null;
        if(!this.poolFactory) this.poolFactory = this.mainDirector.poolFactory;
        paylineSymbol = this.poolFactory.getObject(this.paylineSymbolPrefabName);
        if(!paylineSymbol) return;
        paylineSymbol.active = true;
        paylineSymbol.parent = this.nearWinHolder;
        const posX = this.getXPosition(col);
        const posY = (totalSymbol/2 - row - 0.5) * this.config.SYMBOL_HEIGHT;
        paylineSymbol.setPosition(posX, posY);
        (paylineSymbol as any)["col"] = col;
        (paylineSymbol as any)["row"] = row;
        (paylineSymbol as any)["symbol"] = symbol;
        const spineData = this.node["getSymbolSpineData"](symbol);
        (paylineSymbol as any)["SlotSymbolPayline"].init(symbol, spineData);
        paylineSymbol.active = false;
        this.usingObjs.push(paylineSymbol);
    };

    stopSoundNearWin(){
        this.soundPlayer && this.soundPlayer.stopSfx(this.sfxNearWin);
    }
}
