
import { _decorator, Node, Color, Button, Label } from 'cc';
const { ccclass, property } = _decorator;
import { GameObject } from '../../core/GameObject';
import { formatMoney,findKeyByValue } from '../../../../cc-share/common/utils';
import { CustomEvent } from '../../component/CustomType';

@ccclass('BetSelectionPanelController')
export class BetSelectionPanelController extends GameObject {
    @property(Node)
    btnConfirm = null;
    @property(Node)
    btnClose = null;
    @property(Node)
    btnMaxBet = null;
    @property(Node)
    betOption = null;
    @property(Node)
    lblWinAmount = null;
    @property(Node)
    lblWallet = null;
    @property(Node)
    lblCurrentBet = null;
    @property(Node)
    lblMax = null;
    @property(Color)
    colorSelectedConfirm: Color = new Color();
    @property(Color)
    colorSelectedMaxbet: Color = new Color();
    @property(Color)
    colorUnSelected: Color = new Color();
    @property
    selectedValue: number = -1;

    selectedBet = 0;
    selectBetEvent = null;

    onLoad() {
        super.onLoad();
        this.node.on('UPDATE_VALUE', this.updateValue, this);
        this.node.on('CLEAR_ALL_BET', this.clearAllBets, this);
    }

    updateValue() {
        const currentBetData = this.dataStore.betData.getTotalBet();
        const { mainBets } = this.dataStore.betData;
        const betValues = Object.values(mainBets).sort((a, b) => { return b - a; })
        this.betOption.emit("UPDATE_DATA", betValues, currentBetData, this);
        const isMaxBet = betValues[0] == currentBetData;
        this.btnMaxBet.getComponent(Button).interactable = !isMaxBet;
        this.unSetSelectColorButtons(isMaxBet);
        this.updateBottomLabelValue();
    }

    updateBottomLabelValue() {
        const currentBetData = this.dataStore.betData.getTotalBet();
        const wallet = this.dataStore.getCurrentWallet();
        const { winAmountPS } = this.dataStore.playSession;

        this.lblWinAmount && (this.lblWinAmount.getComponent(Label).string = formatMoney(winAmountPS));
        this.lblCurrentBet && (this.lblCurrentBet.getComponent(Label).string = formatMoney(currentBetData));
        if (!this.dataStore.isTrialMode) {
            this.lblWallet && (this.lblWallet.getComponent(Label).string = formatMoney(wallet))

        } else {
            // this.lblWallet && (this.lblWallet.getComponent(Label).string = formatMoney(this.dataStore.getCurrentWallet))
        }
    }

    setSelectedBet(value, isMaxBet = false) {
        this.selectedBet = value
        this.btnMaxBet.getComponent(Button).interactable = !isMaxBet;
    }

    setSelectColorButtons() {
        this.btnMaxBet.getComponent(Button).target.color = this.colorSelectedMaxbet;
        this.btnConfirm.getComponent(Button).target.color = this.colorSelectedConfirm;
        this.btnClose.getComponent(Button).target.color = this.colorSelectedConfirm;
        this.lblMax.color = this.colorSelectedMaxbet;
    }

    unSetSelectColorButtons(isMaxBet = false) {
        if (!isMaxBet) {
            this.btnMaxBet.getComponent(Button).target.color = this.colorUnSelected;
            this.lblMax.color = this.colorUnSelected;
        } else {
            this.btnMaxBet.getComponent(Button).target.color = this.colorUnSelected;
            this.lblMax.color = this.colorSelectedMaxbet;
        }
        this.btnConfirm.getComponent(Button).target.color = this.colorUnSelected;
        this.btnClose.getComponent(Button).target.color = this.colorUnSelected;
    }

    clickBtnConfirm() {
        const { mainBets } = this.dataStore.betData;
        let keyId = findKeyByValue(mainBets, this.selectedBet)
        this.selectBetEvent = new CustomEvent('SELECT_BET_EVENT', true, { betValue: keyId })
        // this.selectBetEvent['betValue'] = this.selectBetEvent
        this.node.dispatchEvent(this.selectBetEvent);
    }
    onClickMaxBet() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.betOption.emit('SELECT_MAX_BET');
    }
    clearAllBets() {
        this.betOption.emit("CLEAR_ALL_BET");
    }
    clickBtnClose() {
        this.soundPlayer && this.soundPlayer.playSFXClick();
        this.node.parent.emit("HIDE");
    }
}
