
import { _decorator, Node, Label, ParticleSystem2D, SpriteFrame, Vec3, Sprite, tween, log } from 'cc';
const { ccclass, property, integer } = _decorator;
import { SlotUIScene } from "./../component/SlotUIScene";
import {formatMoney, fadeOut} from './../../../cc-share/common/utils';
import { PerformanceLevel, WinEffectLevel } from "../component/CustomEnum";
import EventNames from '../core/EventNames';

@ccclass('WinEffect')
export class WinEffect extends SlotUIScene {
    isShowFastEffect = false;
    isShowNormalEffect = false;
    isUpdating = false;
    speedUp = false;
    skippable = false;
    callback = null;
    label: any
    currentValue: number = 0
    currentTitle: number = 0;
    megaWinAmount: number = 0;
    superWinAmount: number = 0;

    nodeTween: any
    winAmountTween: any
    titleTween: any
    content: any

    @property({ type: Node })
    public winAmount: Node | null = null;
    @property({ type: Node })
    public overlayNode: Node | null = null;
    @property({ type: Node })
    public title: Node | null = null;
    @property({ type: Node })
    public winInfo: Node | null = null;
    @property({ type: Node })
    public coinsEffect: Node | null = null;
    @property({ type: ParticleSystem2D })
    public fastParticle: ParticleSystem2D | null = null;
    @property({ type: SpriteFrame })
    public titleFrame: Array<SpriteFrame> | null = [];

    @integer
    public bigWinDur = 3;
    @integer
    public megaWinDur = 5;
    @integer
    public superWinDur = 8;
    @integer
    public megaRate = 20;
    @integer
    public superRate = 30;
    @integer
    @integer
    public delayShowTime = 2;
    @integer
    public hideTime = 0.5;
    @integer
    public animDuration = 9;

    protected animationLevel:number = 2;

    onLoad() {
        super.onLoad();
        this.label = this.winAmount.getComponentInChildren(Label);
    }

    enter() {
        this.eventManager.emit(EventNames.BIGWIN_START);
        const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
        this.animationLevel = this.config[performanceLvl].BIG_WIN_ANIMATION;
        const {modeTurbo} = this.dataStore;
        if (this.coinsEffect) this.setOpacity(this.coinsEffect, 255);
        modeTurbo ? this.showFastEffectWin() : this.showEffectWin();
    }

    showFastEffectWin() {  // turbo
        if (this.isShowFastEffect) {
            this.callback && this.callback();
            this.callback = null;
            return;
        }
        this.isShowFastEffect = true;
        this.setOpacity(this.winInfo, 0);
        if (this.overlayNode) {
            this.overlayNode.active = false;
        }

        this.fastParticle && this.fastParticle.resetSystem();
        this.callback && this.callback();
        this.callback = null;
        this.nodeTween && this.nodeTween.stop();
        this.nodeTween = tween(this.node)
            .delay(1)
            .call(() => {
                this.fastParticle && this.fastParticle.stopSystem();
            })
            .delay(2)
            .call(() => {
                this.exit();
            })
            .start();
    }

    showEffectWin() {
        this.isShowNormalEffect = true;
        if (this.overlayNode) {
            this.overlayNode.active = true;
        }
        this.setOpacity(this.winInfo, 255);
        this.playSoundStart();

        this.winAmountTween = tween(this.winAmount).repeatForever(
            tween()
                .to(0.2, { scale: new Vec3(1.2, 1.2, 1.2) })
                .to(0.2, { scale: new Vec3(1, 1, 1) })
        ).start();

        this.titleTween = tween(this.title).repeatForever(
            tween()
                .to(0.5, { scale: new Vec3(1.2, 1.2, 1.2) })
                .to(0.5, { scale: new Vec3(1, 1, 1) })
        ).start();

        this.initValue();
        this.startParticle();
        this.startUpdateWinAmount();
    }

    playSoundStart() {
        if(!this.soundPlayer) return;
        this.soundPlayer.stopAllAudio();
        this.soundPlayer.playSfx('BIG_WIN');
        this.scheduleOnce(()=>{
            this.soundPlayer.playSfx('BIG_WIN_LOOP', true);
        },2);
    }

    playSoundEnd() {
        if(!this.soundPlayer) return;
        this.soundPlayer.stopAllEffects();
        this.soundPlayer.playSfx('BIG_WIN_END');
    }

    initValue() {
        this.winInfo.setScale(1, 1, 1);
        this.currentValue = 0;
        this.currentTitle = 0;
        this.label.string = '';
        if (this.title) this.title.getComponent(Sprite).spriteFrame = this.titleFrame[0];

        const { winType, bigWinConfig, currentBetData } = this.content;
        const listConfig = bigWinConfig ? bigWinConfig.split(',') : [];
        if (winType && bigWinConfig) {
            this.megaWinAmount = currentBetData * listConfig[WinEffectLevel.MEGA];
            this.superWinAmount = currentBetData * listConfig[WinEffectLevel.SUPER];
            switch (Number(WinEffectLevel[winType])) {
                case WinEffectLevel.SUPER:
                    this.animDuration = this.superWinDur;
                    break;
                case WinEffectLevel.MEGA:
                    this.animDuration = this.megaWinDur;
                    break;
                default:
                    this.animDuration = this.bigWinDur;
                    break;
            }
            if (winType) {
                log('show ' + winType + ' in ' + this.animDuration + ' second', ' with config: ', listConfig);
            }
        } else {
            this.megaWinAmount = currentBetData * this.megaRate;
            this.superWinAmount = currentBetData * this.superRate;
        }

        this.isUpdating = true;
        this.speedUp = false;
        this.bindQuickShow();
    }

    bindQuickShow() {
        this.skippable = false;
        tween(this.node)
            .delay(this.hideTime)
            .call(() => {
                this.skippable = true;
            })
            .delay(this.animDuration - this.delayShowTime)
            .call(() => {
                this.skippable = false;
            })
            .start();
    }

    startParticle() {
        if (!this.coinsEffect || this.animationLevel< Number(PerformanceLevel.Medium)) return;
        this.coinsEffect.emit('START_PARTICLE');
    }

    startUpdateWinAmount() {
        const reverseEasing = easing => t => 1 - easing(1 - t);
        const speedUpEasing = t => t * t; // constant accelerated
        const slowDownEasing = reverseEasing(t => t * t);
        const halfAmount = 0.5 * this.content.winAmount;
        this.nodeTween = tween(this);
        this.nodeTween
            .to(0.5 * this.animDuration, { currentValue: halfAmount }, { easing: speedUpEasing })
            .to(0.5 * this.animDuration, { currentValue: this.content.winAmount }, { easing: slowDownEasing })
            .delay(0.5)
            .call(() => {
                this.skippable = false;
                this.nodeTween = null;
                this.finish();
            })
            .start();
    }

    update(dt) {
        if (!this.isUpdating) return;
        this.label.string = formatMoney(this.currentValue);
        if (this.currentValue >= this.superWinAmount && this.currentTitle == 1) {
            this.changeTitle(2);
        } else if (this.currentValue >= this.megaWinAmount && this.currentTitle == 0) {
            this.changeTitle(1);
        }
    }

    changeTitle(index: number) {
        this.currentTitle = index;
        if (!this.title) return;

        this.titleTween = tween(this.title)
            .to(0.2, { scale: new Vec3(1.5, 1.5, 1.5) })
            .call(() => {
                this.title.getComponent(Sprite).spriteFrame = this.titleFrame[index];
            })
            .to(0.2, { scale: new Vec3(1, 1, 1) })
            .call(() => {
                tween(this.title).repeatForever(
                    tween()
                        .to(0.5, { scale: new Vec3(1.2, 1.2, 1.2) })
                        .to(0.5, { scale: new Vec3(1, 1, 1) })
                ).start();
            })
            .start();
    }

    onClick() {
        if (!this.isUpdating || this.speedUp) return;
        if (!this.skippable) return;

        this.speedUp = true;
        this.winAmountTween.stop();
        const scaleTime = 0.13;
        tween(this.winAmount).repeatForever(
            tween()
                .to(scaleTime, { scale: new Vec3(1.2, 1.2, 1.2) })
                .to(scaleTime, { scale: new Vec3(1, 1, 1) })
        ).start();

        if (this.nodeTween) {
            this.nodeTween.stop();
            this.nodeTween = null;
        }
        this.currentValue = this.content.winAmount;
        this.nodeTween = tween(this)
        this.nodeTween
            .to(1, { currentValue: this.content.winAmount })
            .call(() => {
                this.finish();
            })
            .start();
    }

    stopParticle() {
        if (!this.coinsEffect || this.animationLevel< Number(PerformanceLevel.Medium)) return;
        this.coinsEffect.emit('STOP_PARTICLE');
    }

    finish() {
        this.isUpdating = false;
        this.label.string = formatMoney(this.content.winAmount);
        this.winAmountTween && this.winAmountTween.stop();
        this.titleTween && this.titleTween.stop();
        this.nodeTween && this.nodeTween.stop();

        this.stopParticle();
        this.playSoundEnd();

        tween(this.winInfo)
            .delay(this.delayShowTime)
            .call(() => {
                fadeOut(this.winInfo, this.hideTime);
                if (this.coinsEffect) this.setOpacity(this.coinsEffect, 0);
            })
            .delay(this.hideTime)
            .call(()=>{
                this.label.string = '';
                this.exit();
                this.soundPlayer && this.soundPlayer.stopAllAudio();
                this.soundPlayer && this.soundPlayer.playMainBGM();
            })
            .start();
    }

    exit() {
        this.soundPlayer && this.soundPlayer.stopAllEffects();
        this.eventManager.emit(EventNames.BIGWIN_END);
        super.exit();
        this.isShowFastEffect = false;
        this.isShowNormalEffect = false;
    };

    onDisable() {
        if (this.nodeTween) {
            this.nodeTween.stop();
            this.nodeTween = null;
        }
    };

}
