

import { _decorator, Tween, tween, Node } from 'cc';
import { CustomEvent } from '../component/CustomType';
import { SlotGameMode } from "../component/CustomEnum";
import { GameObject } from '../core/GameObject';
import { SoundPlayerImpl } from '../../../cc-share/components/SoundPlayerImpl';

const { ccclass } = _decorator;
@ccclass('SlotSoundPlayer')
export class SlotSoundPlayer extends SoundPlayerImpl {

    _audioScatterReel: string | null;
    _audioBonusReel: string | null;
    _soundSmallWin: string | null;

    _tweenSwitchMusicBGM: Tween<Node> = null;
    _sfxTransition: string = "";

    onLoad() {
        this.node.addComponent(GameObject);
        this.node.dispatchEvent(new CustomEvent("SET_UP_SLOT_SOUND_MANAGER", true, { soundManager: this }));
        super.onLoad();
    }

    playMainBGM(currentGameMode?: SlotGameMode) {
        let dataStore = this.node['dataStore'];
        currentGameMode = currentGameMode || dataStore && dataStore.currentGameMode;
        let soundId = "BGM_MAIN";
        if (currentGameMode === SlotGameMode.FreeGame) {
            soundId = "BGM_FREE";
        } else if (currentGameMode === SlotGameMode.BonusGame) {
            soundId = "BGM_BONUS";
        }
        this.playMusic(soundId);
    }

    playSfxClickButtonTrial() {
        this.playSfx("BTN_TUTORIAL");
    }

    /**@table */
    playSoundGetScatter(count = 1) {
        let audio = "SCATTER" + count;
        if (audio) {
            this._audioScatterReel && this.stopSfx(this._audioScatterReel);
            this.playSfx(audio);
            this._audioScatterReel = audio;
        }
    }

    playSoundGetBonus(count = 1) {
        count = count >= 3 ? 3 : count;
        let audio = "BONUS_" + count;
        if (audio) {
            this._audioBonusReel && this.stopSfx(this._audioBonusReel);
            this.playSfx(audio);
            this._audioBonusReel = audio;
        }
    }

    playParticleFly() {
        this.playSfx("PARTICAL_FLY");
    }

    playParticleHit() {
        this.playSfx("PARTICAL_FLY");
    }

    playSFXOpenWild() {
        this.playSfx("OPEN_WILD");
    }

    playSoundSmallWin(rate: number) {
        let soundId: string;
        if (rate >= 5) {
            soundId = 'SMALL_WIN_3';
        } else if (rate > 1) {
            soundId = 'SMALL_WIN_2';
        } else {
            soundId = 'SMALL_WIN_1';
        }
        this._soundSmallWin = soundId;
        this.playSfx(soundId);
    }

    stopSoundSmallWin() {
        this._soundSmallWin && this.stopSfx(this._soundSmallWin);
    }

    /**
     * @transition
     */
    playSFXIntroFreeGame() {
        this.playSfx("INTRO_FREE");
    }

    playSFXIntroBonusGame() {
        this.playSfx("INTRO_BONUS");
    }

    playSFXEndIntroBonus() {
        this.playSfx("END_INTRO_BONUS");
    }

    playSFXTrialButton() {

    };

    switchMusicBGM(nextGameMode: SlotGameMode, sfxTransition: string = "", fadeTime: number = 0.2, delaySwitch: number = 0) {
        if (!this.isMuteMusic && this.isEnableBGM)
            this.fadeMusicTo(fadeTime, 0);

        if (sfxTransition) {
            this._sfxTransition = sfxTransition
            this.playSfx(this._sfxTransition);
        }

        this._tweenSwitchMusicBGM && this._tweenSwitchMusicBGM.stop();
        this._tweenSwitchMusicBGM = tween(this.node)
            .delay(delaySwitch)
            .call(() => {
                this.playMainBGM(nextGameMode);

                if (!this.isMuteMusic && this.isEnableBGM)
                    this.fadeMusicTo(fadeTime, this.musicVolume);
            })

        this._tweenSwitchMusicBGM.start();
    }

    forcePlayMusicBGM(gameMode?: SlotGameMode) {
        this._tweenSwitchMusicBGM && this._tweenSwitchMusicBGM.stop();
        this.stopSfx(this._sfxTransition);

        if (!this.isMuteMusic && this.isEnableBGM)
            this.fadeMusicTo(0, this.musicVolume);

        this.playMainBGM(gameMode);
    }
}
