
import { _decorator, Component, ParticleSystem2D } from 'cc';
const { ccclass, property } = _decorator;

const TYPE = {
    BIG: 0,
    MEGA: 1,
    SUPER: 2
}
@ccclass('CoinEffect')
export class CoinEffect extends Component {
    @property({ type: ParticleSystem2D })
    coinBigWin = null;
    @property({ type: ParticleSystem2D })
    coinMegaWin = [];
    @property({ type: ParticleSystem2D })
    coinSuperWin = [];

    onLoad() {
        this.node.on('SHOW_COIN_EFFECT', this.showCoinEffect, this);
        this.node.on('HIDE_COIN_EFFECT', this.hideCoinEffect, this);
        this.node.on('RESET_COIN_EFFECT', this.resetCoinEffect, this);
        this.resetCoinEffect();
    };

    resetCoinEffect() {
        if (this.coinBigWin) this.coinBigWin.node.active = false;
        if (this.coinMegaWin) {
            this.coinMegaWin.forEach(item => {
                item.node.active = false;
            });
        }
        if (this.coinSuperWin) {
            this.coinSuperWin.forEach(item => {
                item.node.active = false;
            });
        }
    };

    hideCoinEffect() {
        if (this.coinBigWin) this.coinBigWin.stopSystem();
        if (this.coinMegaWin) {
            this.coinMegaWin.forEach(item => {
                item.stopSystem();
            });
        }
        if (this.coinSuperWin) {
            this.coinSuperWin.forEach(item => {
                item.stopSystem();
            });
        }
    };

    showCoinEffect(type = TYPE.BIG, isSkipEffect) {
        if (type === TYPE.BIG) {
            if (this.coinBigWin) {
                this.coinBigWin.node.active = true;
                this.coinBigWin.resetSystem();
            }
        } else if (type === TYPE.MEGA) {
            if (!isSkipEffect) {
                if (this.coinBigWin) this.coinBigWin.stopSystem();
                if (this.coinMegaWin) {
                    this.coinMegaWin.forEach(item => {
                        item.node.active = true;
                        item.resetSystem();
                    });
                }
            }
        } else {
            if (!isSkipEffect) {
                if (this.coinBigWin) this.coinBigWin.resetSystem();
            }
            if (this.coinMegaWin) {
                this.coinMegaWin.forEach(item => {
                    item.stopSystem();
                });
            }
            if (this.coinSuperWin) {
                this.coinSuperWin.forEach(item => {
                    item.node.active = true;
                    item.resetSystem();
                });
            }
        }
    };
}
