
import { _decorator, Component, Node, Prefab, instantiate, tween, view, ParticleSystem2D, Vec3 } from 'cc';
import { setOpacity } from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('JackpotCoinEffect')
export class JackpotCoinEffect extends Component {
    duration = 10;
    moneyPool = []
    moneyIndex = 0;

    @property
    coinSpawnerCount = 3;

    @property({ type: Node })
    coinDropper = null;

    @property({ type: Node })
    diamondDropper = null;

    @property({ type: Node })
    moneySpawner = null;

    @property({ type: Prefab })
    moneyPrefab = new Prefab;

    @property({ type: [Node] })
    coinPiles = [];

    @property({ type: [Node] })
    diamondPiles = [];

    @property
    coinPipeDelayTime = 1.3;

    onLoad() {
        this.initValue();
        this.node.on('START_PARTICLE', this.startParticle, this);
        this.node.on('STOP_PARTICLE', this.stopParticle, this);
        this.node.on('DROP_MONEY', this.randomDropMoney, this);
        this.node.on('DROP_DIAMOND', this.dropDiamond, this);
        this.duration = 10;
    }

    initValue() {
        if (!this.moneyPrefab) {
            return;
        }

        this.moneyPool = [];
        for (let i = 0; i < 20; ++i) {
            const money = instantiate(this.moneyPrefab);
            money.parent = this.moneySpawner;
            setOpacity(money, 0);
            this.moneyPool.push(money);
        }
        setOpacity(this.moneySpawner, 0);
    }

    startParticle() {
        this.coinDropper.getComponent(ParticleSystem2D).resetSystem();
        this.coinPiles.forEach(pile => {
            pile.getComponent(ParticleSystem2D).resetSystem();
            pile.setPosition(0, -view.getVisibleSize().height / 2);
            tween(pile).by(this.duration, {position: new Vec3(0, 50, 0)}).start();
        });
    }

    stopParticle() {
        this.coinDropper.getComponent(ParticleSystem2D).stopSystem();
        this.coinPiles.forEach(pile => {
            pile.getComponent(ParticleSystem2D).stopSystem();
        });
        if (this.diamondDropper) {
            this.diamondDropper.getComponent(ParticleSystem2D).resetSystem();
            this.diamondDropper.getComponent(ParticleSystem2D).stopSystem();
            this.diamondPiles.forEach(pile => {
                pile.getComponent(ParticleSystem2D).resetSystem();
                pile.getComponent(ParticleSystem2D).stopSystem();
            });
        }
    }

    randomDropMoney() {
        if (!this.moneyPrefab) {
            return;
        }
        this.moneyIndex = 0;
        setOpacity(this.moneySpawner, 255);

        tween(this.moneySpawner).repeatForever(tween().delay(0.3).call(()=>{
            this.dropMoney();
        })).start();
    }

    dropMoney() {
        const money = this.moneyPool[this.moneyIndex];
        this.moneyIndex = (this.moneyIndex + 1) % this.moneyPool.length;
        money.position.x = (Math.random() - 0.5) * view.getVisibleSize().width;
        const randomAnimIdx = Math.random() * 3 | 0 + 1;
        const animName = 'TienRoi' + randomAnimIdx;
        setOpacity(money, 255);
        money.getComponent('sp.Skeleton').setAnimation(0, animName, false);
    }

    dropDiamond() {
        setOpacity(this.diamondDropper, 0);
        this.diamondDropper.getComponent(ParticleSystem2D).resetSystem();
    }

}
