
import { _decorator, Component, Label , JsonAsset, log} from 'cc';
import loadConfigAsync from "../../../cc-share/shareServices/loadConfigAsync";

const { ccclass, property } = _decorator;

@ccclass('VersionSlot')
export class VersionSlot extends Component {

    @property(JsonAsset)
    versionFile: JsonAsset;

    @property(Label)
    versionText: Label;

    onLoad() {
        const {IS_PRODUCTION} = loadConfigAsync.getConfig();
        if (IS_PRODUCTION) {
            this.node.active = false;
        }
    }

    start () {
        log(this.versionFile);
        this.versionText.string = this.versionFile.json["version"];
    }
}