
import { _decorator, Node, Button, Event, v3, log } from 'cc';
import { SlotGameMode, SlotSceneType, TutorialTriggerType, WalletType } from "../component/CustomEnum";
import { CustomEvent } from '../component/CustomType';

import { SlotUIScene } from '../component/SlotUIScene';
import { GameObject } from './GameObject';
import EventNames from './EventNames';
import loadConfigAsync from '../../../cc-share/shareServices/loadConfigAsync';
import { Setting } from '../component/Setting';

const { ccclass, property } = _decorator;

@ccclass('UIManager')
export class UIManager extends GameObject {

    sceneComp:SlotUIScene[] = [];
    buttons = [];

    @property({ type: Node })
    dialogMessage = null;

    @property({ type: Node })
    controllerHolder = null;
    @property({ type: Node })
    sceneHolder = null;

    @property({ type: Node })
    bet = null;

    @property({ type: Node })
    wallet = null;

    @property({ type: Node })
    jackpot = null;

    @property({ type: Node })
    turbo = null;

    @property({ type: Node})
    winAmount = null;

    @property({type: Node})
    paylineInfo: Node = null;

    @property({type: Node})
    toastInfo = null;

    @property({type: Node})
    topUI = null;

    @property({type: Node})
    questMgr = null;

    @property({type: Node})
    questProcessMgr = null;

    @property({type: Node})
    animWinEvent = null;

    @property({type: Node})
    topUserList = null;

    @property({type: Node})
    promoteWallet = null;

    @property({type: Node})
    multiScreenBtn: Node = null;

    _realBetData: any = null;
    _realExtraBetData: any = null;
    _lastWalletType = WalletType.NORMAL;
    sceneSiblingOffset = 0;
    popups: SlotSceneType[] = [];

    @property({type: Node})
    loadingScreen = null;

    onLoad() {
        debug: globalThis.UIManager = this; //TODO remove debug
        this.node.dispatchEvent(new CustomEvent("SET_UP_UI_MANAGER", true, {guiMgr:this}));
        super.onLoad();
        this.setupUI();
        this.node.on('SET_UP_SLOT_BUTTON', this.setUpSlotButton.bind(this));
        this.node.on('SET_UP_SPIN_TIMES', this.setUpSpinTimes.bind(this));

        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if (LOGIN_IFRAME) {
            this.displayMultiScreenButton(false);
        }
        else {
            this.displayMultiScreenButton(true);
        }
    }

    setUpSlotButton(evt: CustomEvent){
        if(evt.details){
            evt.propagationStopped = true;
            if(this.eventManager){
                this.eventManager.emit(evt.type, evt.details);
            }
        }
    }

    setUpSpinTimes(evt: CustomEvent){
        if(evt.details){
            evt.propagationStopped = true;
            if(this.eventManager){
                this.eventManager.emit(evt.type, evt.details);
            }
        }
    }

    setupUI() {
        this.sceneHolder.active = true;
        for (let i = 0; i < this.sceneHolder.children.length; i++) {
            const child = this.sceneHolder.children[i];
            if (child) {
                child.active = true;
            }
        }
        this.popups = [SlotSceneType.Setting, SlotSceneType.BetHistory, SlotSceneType.JackpotHistory, SlotSceneType.GameInfo];
        this.buttons = this.controllerHolder.getComponentsInChildren(Button);
        this.buttons.forEach(it => {
            it.interactable = false;
        })
    }

    hideAllUI() {
        this.bet.active = false;
        this.wallet.active = false;
        this.turbo.active = false;
        this.winAmount.active = false;
    };

    showUIMain() {
        this.bet.active = true;
        this.wallet.active = true;
        this.jackpot.active = true;
        this.turbo.active = true;
        this.winAmount.active = true;
        this.setOpacity(this.paylineInfo, 255);
        if (this.topUserList && !this.dataStore.isTrialMode)
            this.topUserList.active = true;
        if (this.questMgr)
            this.questMgr.getComponent('SlotQuestMgr').showIfAvailable();
    };

    onBackToMulti() {
        this.node.dispatchEvent(new Event('BACK_TO_MULTI', true));
    };

    showUIFree(){
        this.hideAllUI();
        this.setOpacity(this.paylineInfo, 255);
        this.turbo.active = true;
        this.winAmount.active = true;
        if (this.topUserList && !this.dataStore.isTrialMode)
            this.topUserList.active = true;
        if (this.questMgr)
            this.questMgr.getComponent('SlotQuestMgr').showIfAvailable();
    }

    showUITopUp(){
        this.hideAllUI();
        this.setOpacity(this.paylineInfo, 0);
        this.turbo.active = true;
        this.winAmount.active = true;
        if (this.topUserList)
            this.topUserList.active = false;
        if (this.questMgr)
            this.questMgr.active = false;
    }

    showUIBonus(){
        this.hideAllUI();
        this.setOpacity(this.paylineInfo, 0);
        this.winAmount.active = true;
        if (this.topUserList)
            this.topUserList.active = false;
        if (this.questMgr)
            this.questMgr.active = false;
    }

    updateBetInfoStatus(canShow = false){

    }

    enableButtons() {
        this.buttons.forEach(it => {
            it.interactable = true;
        })
    };

    onSettingClick() {
        this.showCutScene(SlotSceneType.Setting);
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }

    onInfoClick() {
        this.showCutScene(SlotSceneType.GameInfo);
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }

    showJackpotHistory() {
        this.showCutScene(SlotSceneType.JackpotHistory);
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }

    showBetHistory() {
        this.showCutScene(SlotSceneType.BetHistory);
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }

    /**@TopUI */
    disableTopButtons() {
        this.topUI.emit('DISABLE_BUTTONS');
    };

    enableTopButtons() {
        this.topUI.emit('ENABLE_BUTTONS');
    };

    /**@CutScenes */
    showCutScene(type: SlotSceneType, content = {}, callback = () => { }) {
        let scene = this.getCutscene(type);
        if(!scene){
            this.eventManager && this.eventManager.emit(EventNames.CREATE_ON_DEMAND_OBJECT, Number(type), ()=>{
                this.sceneComp.forEach(it => {
                    let sceneType = it.getType();
                    if (sceneType == type) {
                        log(`${this.config.GAME_ID} add cutscene ${it.node.name}`);
                        it.node.emit('PLAY', content, callback);
                        it.node._uiProps.colorDirty = true;
                    	this.sortCutScene(it.node);
		            }
                })
            })
        }else{
            this.sceneComp.forEach(it => {
                if (it.getType() == type) {
                    //it.node.setSiblingIndex(it.node.parent.children.length);
                    it.node.emit('PLAY', content, callback);
                    it.node._uiProps.colorDirty = true;
                }
            })
        }
    }

    sortCutScene(scene: Node) {
        if (this.sceneHolder.children.length < 2) return;
        let currentSibIndex = this.sceneHolder.children.length;
        let targetIndex = scene['sibIndex'];
        let nextSib = currentSibIndex;
        for (let i = currentSibIndex - 1; i > 0; i--) {
            let index = this.sceneHolder.children[i]['sibIndex'] || 0;
            if (targetIndex < index) {
                nextSib = i;
            }
        }
        scene.setSiblingIndex(nextSib);
    }

    hideCutScene(type: SlotSceneType) {
        this.sceneComp.forEach(it => {
            if (it.getType() == type) {
                it.node.emit('HIDE');
            }
        })
    }

    isShowingCutScene(type: SlotSceneType) {
        let isShow = false;
        this.sceneComp.forEach(it => {
            if (it.getType() == type) {
                if (it.node.active)
                    isShow = true;
            }
        })
        return isShow;
    }

    hideCutSceneWithCB(type: SlotSceneType, callback = () => { }) {
        this.sceneComp.forEach(it => {
            if (it.getType() == type) {
                it.node.emit('HIDE', ()=>{
                    callback&&callback();
                });
            }
        })
    }

    getCutscene(type: SlotSceneType):SlotUIScene{
        for(let i = 0; i<this.sceneComp.length; i++){
            const _type = this.sceneComp[i].getType();
            if(_type == type){
                return this.sceneComp[i];
            }
        }
        return null;
    }

    /**@Wallet */
    updateWallet(wallets) {
        this.wallet.emit("UPDATE_WALLET", wallets);
    }

    enableSwitchWallet(enable) {
        enable = enable && !this.dataStore.isTrialMode;
        this.wallet.emit('ENABLE_SWITCH', enable);
    }

    selectWallet() {
        this.wallet.emit('LOAD_WALLET_SELECT');
    }

    /**@Dialog */
    showDialog(strText: string, actionOK: ()=>void = null, actionCancel: ()=>void = null, isForce = false) {
        if (!this.dialogMessage.active || isForce)
            this.dialogMessage.emit('PLAY', { strText, actionOK, actionCancel});
    }

    hideDialog() {
        this.dialogMessage.active = false;
    }

    isDisplayDialog() {
        return this.dialogMessage.active;
    }

    isSpinVisible() {
        let cutsceneDisplay = this.sceneComp.filter(it => it.node.active == true).length > 0;
        let result = !cutsceneDisplay && this.extraSpinCheck();
        return result;
    }

    isDisplayFullScreen() {
        return this.dataStore.isDisplayFullScreen;
    }

    extraSpinCheck() {
        return true;
    }

    // -------------- Jackpot -------------
    initJackpot(data) {
        this.jackpot.emit("INIT_JACKPOT", data);
    };

    updateJackpot(data) {
        this.jackpot.emit("UPDATE_JACKPOT", data);
    };

   /**@Jackpot */
   updateValueJP(data) {
        const {jpType, jpValue} = data;
        this.jackpot.emit('UPDATE_VALUE_JACKPOT', jpType, jpValue);
    }

    pauseJackpot() {
        this.jackpot.emit('PAUSE_JACKPOT');
    }

    resumeJackpot() {
        this.jackpot.emit('RESUME_JACKPOT');
    }

    /**@Bet */
    setBetId(betId: string) {
        this.bet.emit('SET_BET_ID', betId);
    }
    disableBet() {
        this.bet.emit('DISABLE_BET');
    }

    enableBet() {
        this.bet.emit('ENABLE_BET');
    }

    initBet() {
        this.bet.emit('INIT_BET');
    };

    //Toast
    showToast(message) {
        this.toastInfo.emit('SHOW_TOAST_MESSAGE', message);
    }

    //winAmount
    getWinAmount() {
        return this.winAmount;
    }

    updateWinAmount(winAmount:number, timeShow: number, callback:any = null){
        this.winAmount.emit("UPDATE_WIN_AMOUNT", {value:winAmount, time:timeShow}, ()=>{
            callback&&callback();
        });
    }

    fadeOutWinAmount(){
        this.winAmount.emit("FADE_OUT_NUMBER",this.dataStore.modeTurbo ? 0.4 : 0.8)
    }

    changeToLastWin(){
        this.winAmount.emit("CHANGE_TO_LAST_WIN");
    }

    changeToWin(){
        this.winAmount.emit("CHANGE_TO_WIN");
    }

    showPaylineInfo(info:any){
        this.paylineInfo.emit('SHOW_PAYLINE',{line: info});
    }

    hidePaylineInfo(){
        this.paylineInfo.emit('HIDE_PAYLINE');
    }

    skipAllUIScenes(){
        for(let i = 0; i<this.sceneComp.length; i++){
            const scene = this.sceneComp[i];
            if(scene && scene.node.active){
                scene.node.emit("SKIP");
            }
        }
    }

    //Quest
    initQuest() {
        if (this.questMgr) {
            this.questMgr.emit('INIT_QUEST');
        }
    }

    forceUpdateQuest() {
        if (this.questMgr) {
            this.questMgr.emit('FORCE_UPDATE_QUEST');
        }
    }

    activeQuest(isActive) {
        if (this.questMgr) {
            if (isActive == false)
                this.questMgr.active = false;
            else
                this.questMgr.getComponent('SlotQuestMgr').showIfAvailable();
        }
    };

    refreshQuestUI() {
        log('UIManager refresh quest layout');
        if (this.questMgr) {
            this.questMgr.emit('REFRESH_LAYOUT');
        }
        this.questMgr.getComponent('SlotQuestMgr').showIfAvailable();
    }

    forceCloseEvent() {
        this.questMgr.getComponent('SlotQuestMgr').forceCloseEvent();
    }

    forceOpenPanel() {
        this.questMgr.getComponent('SlotQuestMgr').showBoard(false);
    }

    topUserUpdate(userList, userOnline) {
        if (this.topUserList) {
            this.topUserList.emit('UPDATE_DATA', userList, userOnline);
        }
    }

    activeQuestProcess(isActive) {
        if (this.questProcessMgr) {
            this.questProcessMgr.active = isActive;
        }
    };

    setDoneQuest() {
        this.questMgr.getComponent('SlotQuestMgr').setDoneCurrent();
    }

    finishEvent(callback) {
        this.questMgr.getComponent('SlotQuestMgr').finishEvent(callback);
    }

    setQuestPosition(parent: Node) {
        if (!this.questMgr) return;
        if (parent) {
            this.questMgr.removeFromParent();
            this.questMgr.setParent(parent);
            this.questMgr.setPosition(v3(0,0,0));
            this.questMgr.getComponent('SlotQuestMgr').showIfAvailable();
        }
    }

    setWalletTypes(types) {
        this.wallet.emit('UPDATE_WALLET_TYPE', types);
    }

    playAnimWinQuest(callback) {
        log('UIManager playAnimWinQuest');
        this.questMgr.getComponent('SlotQuestMgr').showBoard();
        this.scheduleOnce(()=>{
            let startPosition = this.questMgr.getComponent('SlotQuestMgr').getEffectPosition();
            if (this.animWinEvent) {
                this.animWinEvent.emit("PLAY_ANIMATION", startPosition, this.wallet, callback);
            }
            else {
                callback && callback();
            }
        }, 0.4);
    }

    stopAnimWinQuest() {
        if (this.animWinEvent) {
            this.animWinEvent.emit("RESET_ANIMATION");
        }
    }

    displayMultiScreenButton(isActive: boolean) {
        //if (this.multiScreenBtn) this.multiScreenBtn.active = isActive;
    }

    // notify win effect
    /*showBigWinEffect() {
        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_OPEN_BIGWIN', true);
            this.node.dispatchEvent(updatedEvent);
        }
    };

    hideBigWinEffect() {
        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_CLOSE_BIGWIN', true);
            this.node.dispatchEvent(updatedEvent);
        }
    };

    showJackpotEffect() {
        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_OPEN_JACKPOT', true);
            this.node.dispatchEvent(updatedEvent);
        }
    };

    hideJackpotEffect() {
        if (!this.dataStore.isTrialMode) {
            let updatedEvent:any = new Event('NOTIFY_CLOSE_JACKPOT', true);
            this.node.dispatchEvent(updatedEvent);
        }
    };*/

    pauseTopPlayer() {
        this.topUserList.getComponent("TopPlayers").pauseUpdate(true);
    }

    resumeTopPlayer() {
        this.topUserList.getComponent("TopPlayers").pauseUpdate(false);
    }

    updateSoundSetting() {
        let setting = this.node.getComponentInChildren(Setting);
        if (setting) {
            setting.syncSetting();
        }
    }
    closePopups() {
        this.sceneComp.forEach((scene) => {
            const isPopUp = this.popups.indexOf(scene.getType()) > -1;
            if (scene.node.active == true && isPopUp) {
                scene.node.emit("HIDE");
            }
        })
    }

    isDisplayLoading() {
        return this.loadingScreen && this.loadingScreen.active;
    }

}
