
import { _decorator, Component, Node, Label, v3, Button, Color, UITransform, Layout, Event } from 'cc';
import {CustomEvent} from '../CustomType';
const { ccclass, property } = _decorator;

@ccclass('PaginationMrg')
export class PaginationMrg extends Component {
    @property({ type: Button })
    btns = [];
    totalPage: number = 0;
    @property({ type: Node })
    preLabel = null;
    @property({ type: Node })
    nextLabel = null;
    clickEvent = null;

    onLoad() {
        this.node.on("UPDATE_PAGE", this.updatePage, this);
        this.node.on('RESET_PAGE_INDEX', this.resetPage, this);
        this.node.children.forEach(it => it.active = false);
        if (this.btns && this.btns.length > 0) {
            this.btns.forEach(it => {
                it.node.on(Node.EventType.TOUCH_END, this.clickPage, this);
                it.node['initWidth'] = it.node.getComponent(UITransform).width;
            })
        }
    };

    clickPage(event) {
        const clickedPage = parseInt(event.target.children[0].getComponent(Label).string);
        this.updatePage(clickedPage, this.totalPage);
        this.clickEvent = new Event('JUMP_TO_PAGE_HISTORY', true);
        this.clickEvent.clickedPage = clickedPage
        this.node.dispatchEvent(this.clickEvent)
    }

    updatePage(page, totalPage) {
        this.preLabel.active = ((page - 2) > 1) && (totalPage > 5);
        this.nextLabel.active = ((page + 2) < totalPage) && (totalPage > 5);
        this.totalPage = totalPage;
        let value = -1;
        switch(true) {
            case totalPage <= 5: 
                value = 1;
                break;
            case (totalPage > page + 1): 
                value = page - 2 <= 0 ? 1 : page - 2;
                break;
            case (totalPage <= page + 1):
                value = totalPage - 4;
                break;
        }
        this.btns.forEach((it, index) => {
            if (index < totalPage)
                this.updateLabelValue(it, value + index, page);
        });
        this.updateSizeForEachBtn();
        this.node.getComponent(Layout).updateLayout();
    };

    resetPage() {
        this.node.children.forEach(it => it.active = false);
    }

    updateSizeForEachBtn(){
        const lastBtn  = this.btns[this.btns.length - 1];
        let label = lastBtn.node.children[0].getComponent(Label);
        const offset   = ((label.string.length - 1) * lastBtn.node['initWidth']) ;
        const newWidth = lastBtn.node['initWidth'] +  offset;
        this.preLabel.getComponent(UITransform).width = newWidth;
        this.nextLabel.getComponent(UITransform).width = newWidth;
        this.btns.forEach(it => {
            it.node.getComponent(UITransform).width = newWidth;
        });
        this.node.dispatchEvent(new CustomEvent("UPDATE_NAVIGATION_BUTTON_PAGE", true, {offset:offset * 3}));
    }

    updateLabelValue(button, value, page) {
        button.node.active = true;
        const label = button.node.children[0].getComponent(Label);
        label.node.active = true;
        label.string = value;
        label.color = (value === page) ? Color.YELLOW : Color.WHITE;
        const scale = (value === page) ? 1.3 : 1;
        label.node.setScale(v3(scale, scale, scale));
    }

}
