
import { _decorator, Component, warn } from 'cc';
import BetData from "./BetData";
import { convertPayLineAlways, convertSlotMatrix, convertPayLine } from "../utils/SlotUtils";
import { StateGameMode, PerformanceLevel, WalletType, SlotGameMode } from "../component/CustomEnum";
import { QuestData } from "./QuestData"
const { ccclass, property } = _decorator;
@ccclass('DataStore')
export class DataStore extends Component {

    config: any = null;
    wallets = [];
    selectedWallet = WalletType.NORMAL;
    walletHasUpdate = false;
    betData: BetData;
    playSession: any = {};
    lastEvent: any = {};
    lastNormalResult: object = {};
    isAlways: boolean = false;
    spinTimes: number = 0;
    isAutoSpin: boolean = false;
    isTrialMode: boolean = false;
    isDisplayFullScreen: boolean = true;
    isAdsMode: boolean = false;
    modeTurbo: boolean = false;
    gameSpeed: number = 0;
    promotion: any = null;
    currentGameMode: SlotGameMode = SlotGameMode.NormalGame;
    extendData: any = {};
    questData = new QuestData();
    isAutoSpinNormal: boolean = false;
    walletTypes = [];
    
    get performanceLevel():string {
        let performance:string = 'HIGH_PERFORMANCE_LV';
        switch(this._performanceLevel)
        {
            case PerformanceLevel.Medium:
                performance = 'MEDIUM_PERFORMANCE_LV';
                break;
            case PerformanceLevel.Low:
                performance = 'LOW_PERFORMANCE_LV';
                break;
        }
        return performance;
    }
    @property({type:PerformanceLevel, visible: true})
    protected _performanceLevel:PerformanceLevel = PerformanceLevel.High;
    protected _pausedWallet = 0;
    protected _isDebug: boolean = true;

    onLoad() {
        this._isDebug && (window['dataStore'] = this);
        this.config = (this.node as any).config;
        (this.node as any).dataStore = this;
        this.betData = new BetData(this.config); // default
        this.isAlways = this.config.PAY_LINE_ALLWAYS;
        this.wallets[WalletType.NORMAL] = 0;
        this.wallets[WalletType.TRIAL] = 0;
        this.wallets[WalletType.PROMOTE] = 0;
    }
    /**
     * @extend and @override this func 
     * if the game have the different way to calculate money
     * Don't need to extends new BetData. 
     */
    getTotalBet() {
        return this.betData.getTotalBet();
    }

    /**
     * @wallet
     */
    getWalletAfterSpin() {
        return this.getCurrentWallet() - this.getTotalBet();
    }
    setWalletTypes(types) {
        this.walletTypes = types;
    }
    getWalletTypes() {
        return this.walletTypes;
    }
    switchWallet(wallet: WalletType) {
        this.selectedWallet = wallet;
    }
    getCurrentWallet(): number {
        return this.wallets[this.selectedWallet];
    }
    setWallet(wallet: WalletType, value: number) {
        if (this.wallets[wallet] != value) {
            this.wallets[wallet] = value;
            this.walletHasUpdate = true;
        }
    }
    getWallets() {
        return this.wallets;
    }
    getCurrentWalletType(): WalletType {
        return this.selectedWallet;
    }
    setCurrentWallet(value: number) {
        if (this.wallets[this.selectedWallet] != value) {
            this.wallets[this.selectedWallet] = value;
            this.walletHasUpdate = true;
        }
    }
    //save value only
    updateWallets({amount, pAmount}) {
        if (this.wallets[WalletType.NORMAL] !== amount || this.wallets[WalletType.PROMOTE] !== pAmount) {
            this.wallets[WalletType.NORMAL] = amount;
            this.wallets[WalletType.PROMOTE] = pAmount;
            this.walletHasUpdate = true;
        }
    }

    syncWalletUI(wallets){
        if ((!isNaN(wallets[WalletType.NORMAL]) && wallets[WalletType.NORMAL] !== this.wallets[WalletType.NORMAL]) ||
            (!isNaN(wallets[WalletType.PROMOTE]) && wallets[WalletType.PROMOTE] !== this.wallets[WalletType.PROMOTE]) ||
            (!isNaN(wallets[WalletType.TRIAL]) && wallets[WalletType.TRIAL] !== this.wallets[WalletType.TRIAL]))
            this.walletHasUpdate = true;
        else {
            this.walletHasUpdate = false;
        }
    }

    isPausingWallet(type: WalletType) {
        return ((this._pausedWallet & type) == type);
    }
    pauseCurrentWallet() {
        if (!this.isPausingWallet(this.selectedWallet)) {
            this.pauseWallet(this.selectedWallet);
        }
    }
    resumeCurrentWallet() {
        if (this.isPausingWallet(this.selectedWallet)) {
            this.resumeWallet(this.selectedWallet);
        }
    }
    pauseWallet(type: WalletType) {
        if (!this.isPausingWallet(type))
            this._pausedWallet = this._pausedWallet + type;
    }
    resumeWallet(type: WalletType) {
        if (this.isPausingWallet(type))
            this._pausedWallet -= type;
    }

    /**
     * @process_data
     */
    formatData(playSession) {
        const { NORMAL_TABLE_FORMAT, FREE_TABLE_FORMAT } = this.config;
        const { matrix, normalGameMatrix, freeGameMatrix, payLines, state,
            bonusGameMatrix, eventData, winAmountQuest, winAmountEvent, isTrialMode} = playSession;
        let tableFormat = NORMAL_TABLE_FORMAT;

        if (state == StateGameMode.FreeGame) {
            tableFormat = FREE_TABLE_FORMAT;
        }

        if (matrix) {
            playSession.matrix = this.convertMatrix(matrix, tableFormat);
        } else if (freeGameMatrix) {
            playSession.matrix = this.convertMatrix(freeGameMatrix, tableFormat);
        }
        else if (normalGameMatrix) {
            playSession.matrix = this.convertMatrix(normalGameMatrix, tableFormat);
        }
        if (playSession.payLines) {
            playSession.payLines = this.convertPayLine(payLines);
        }
        if(bonusGameMatrix){
            playSession.bonusGameMatrix = this.formatBonusMatrix(bonusGameMatrix);
        }

        if (!isTrialMode) {
            if (eventData) {
                this.questData.updateQuestData(eventData);
            }
            if (winAmountQuest || winAmountEvent) {
                this.questData.setWinAmount(winAmountQuest, winAmountEvent);
            }
            else if (playSession.version == 1) {
                this.questData.resetWinAmount();
            }
        }

        this.playSession = playSession;
        //warn("%c run data-update ", "color: red", this.playSession);
        return playSession;
    }
    convertMatrix(matrix: string[], tableFormat: number[] = null) {
        const { NORMAL_TABLE_FORMAT } = this.config;
        return convertSlotMatrix(matrix, tableFormat ? tableFormat : NORMAL_TABLE_FORMAT)
    }
    convertPayLine(payLines) {
        if (this.isAlways) {
            return convertPayLineAlways(payLines);
        } else {
            return convertPayLine(payLines);
        }
    }
    formatBonusMatrix(bonusGameMatrix: any[]){
        return bonusGameMatrix.map(value => Number(value));
    }

    setTurbo(value: boolean) {
        this.modeTurbo = value;
    }

    setLowPerformance(){
        this._performanceLevel = PerformanceLevel.Low;
    }

    setMediumPerformance(){
        this._performanceLevel = PerformanceLevel.Medium;
    }

    setHighPerformance(){
        this._performanceLevel = PerformanceLevel.High;
    };

    getQuestData() {
        return this.questData;
    }
}