
import { _decorator, Node, Prefab, instantiate, sys, Asset } from 'cc';
import EventNames from '../core/EventNames';
import { BaseDynamicLoading } from './BaseDynamicLoading';
const { ccclass, property } = _decorator;

export class SpawnObjectData{
    public spawnObject: Node;
    public id: number;
    constructor (spawnObject:Node, id:number){
        this.spawnObject = spawnObject;
        this.id = id;
    }
}
@ccclass('OnDemandLoadingControl')
export class OnDemandLoadingControl extends BaseDynamicLoading {
    @property(Node)
    loading:Node = null;

    protected _usingObjects: SpawnObjectData[] = [];

    onEnable(){
        if(this.eventManager){
            this.eventManager.on(EventNames.CREATE_ON_DEMAND_OBJECT, this.createObjectOnDemand, this);
            this.eventManager.on(EventNames.REMOVE_ON_DEMAND_OBJECT, this.destroyObjectOnDemand, this);
        }
    }

    onDisable(){
        if(this.eventManager){
            this.eventManager.off(EventNames.CREATE_ON_DEMAND_OBJECT, this.createObjectOnDemand, this);
            this.eventManager.off(EventNames.REMOVE_ON_DEMAND_OBJECT, this.destroyObjectOnDemand, this);
        }
    }

    runAsyncLazyLoading(){
        for(let i = 0; i<this.assetPaths.length; i++){
            const assetPathConfig = this.assetPaths[i];
            if(assetPathConfig){
                log(">>>> Run Async Lazy Loading for: "+ assetPathConfig.path);
                if((assetPathConfig.lazyLoadOnNative && sys.isNative) || (assetPathConfig.lazyLoadOnWeb && sys.isBrowser)){
                    this.createObjectOnDemand(assetPathConfig.id, ()=>{
                        log(">>>> Complete Lazy Loading for: "+ assetPathConfig.path);
                    }, false);
                }
            }
        }
    }

    runSyncLazyLoading(index){
        const assetPathConfig = this.assetPaths[index];
        if(assetPathConfig){
            log(">>>> Run Sync Lazy Loading for: "+ assetPathConfig.path);
            if((assetPathConfig.lazyLoadOnNative && sys.isNative) || (assetPathConfig.lazyLoadOnWeb && sys.isBrowser)){
                this.createObjectOnDemand(assetPathConfig.id, ()=>{
                    this.runSyncLazyLoading(++index);
                }, false);
            }else{
                this.runSyncLazyLoading(++index);
            }
        }else{
            log(">>>> Lazy Load Completed");
        }
    }

    createObjectOnDemand(assetId:number, callback:Function = null, isShowLoadingIcon:boolean = true){
        const objectToDestroy = this.getObjectSpawnedById(assetId);
        if(objectToDestroy && objectToDestroy.spawnData && objectToDestroy.spawnData.spawnObject) {
            callback&&callback();
            return;
        }
        if(this.loading && isShowLoadingIcon) this.loading.active = true;
        const _assetData = this.getAssetLoadedById(assetId);
        if(_assetData && _assetData.asset){
            this.spawnOnDemandObject(_assetData.asset, _assetData, callback);
        }else{
            for(let i = 0; i<this.assetPaths.length; i++){
                const assetPathConfig = this.assetPaths[i];
                if(assetPathConfig && assetPathConfig.id === assetId){
                    this._dynamicLoadByPath(assetPathConfig.path, assetPathConfig.id, assetPathConfig.parentNode, (asset)=>{
                        this.spawnOnDemandObject(asset, assetPathConfig, callback);
                    });
                    break;
                }
            }
        }
    }

    spawnOnDemandObject(asset: Asset, assetData: Object, callback:Function = null){
        const node:Node = instantiate(asset as Prefab);
        node.active = true;
        node.parent = assetData.parentNode;
        node['sibIndex'] = assetData.index;
        //node.setSiblingIndex(assetData.id);
        const spawnObject = new SpawnObjectData(node, assetData.id);
        this._usingObjects.push(spawnObject);
        callback&&callback();
        if(this.loading) this.loading.active = false;
    }

    destroyObjectOnDemand(id:number, releaseResource: boolean, callback:Function = null){
        const objectToDestroy = this.getObjectSpawnedById(id);
        if(objectToDestroy){
            this._usingObjects.splice(objectToDestroy.index, 1);
            if(objectToDestroy.spawnData) objectToDestroy.spawnData.spawnObject.destroy();
            callback && callback();
        }
        
        if(releaseResource){
            let assetData = this.getAssetLoadedById(id);
            this.clearSingleResourceLoaded(assetData);
        }
        if(sys.isNative){
            log('>>>>> GC Collect in Native at: destroyObjectOnDemand ');
            sys.garbageCollect();
        }
    }

    getObjectSpawnedById(id: number): {spawnData:SpawnObjectData, index:number}{
        for(let i = 0; i<this._usingObjects.length; i++){
            if(this._usingObjects[i].id === id){
                return {
                    spawnData: this._usingObjects[i],
                    index: i,
                }
            }
        }
        return null;
    }
}