
import { SlotGameMode, SlotSceneType, StateGameMode } from "../component/CustomEnum";
import { _decorator, Component } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('SlotGameWriter')
export class SlotGameWriter extends Component {
    onLoad() {
        this.node['writer'] = this;
    }

    makeScriptResume(): any {
        // todo: always rewrite this resume logic
        const dataStore = (this.node as any).dataStore;

        if (dataStore) {
            const { config, promotion } = dataStore;
            const { NORMAL_TABLE_FORMAT, FREE_TABLE_FORMAT } = config;
            const { bonusGameMatrix, normalGameMatrix, normalGamePaylines, bonusGameRemain, freeGameRemain,
                freeGameMatrix, winAmountPS, betId, freeSpinOption, walletType } = dataStore.playSession;

            const normalSpinMatrix = dataStore.convertMatrix(normalGameMatrix, NORMAL_TABLE_FORMAT);
            const normalPayLines = normalGamePaylines ? dataStore.convertPayLine(normalGamePaylines) : null;

            const isBonusGameInNormal = (bonusGameRemain && bonusGameRemain > 0 && !freeGameMatrix);
            const isBonusGameInFree = (bonusGameRemain && bonusGameRemain > 0 && !isBonusGameInNormal);
            const isFreeGame = ((freeGameRemain && freeGameRemain > 0) || isBonusGameInFree);

            let listScript = [];
            let promotionRemain, promotionTotal;

            if (promotion) {
                promotionRemain = promotion.promotionRemain;
                promotionTotal = promotion.promotionTotal;
            }

            listScript.push({
                command: "_updateBetId",
                data: betId
            });
            listScript.push({
                command: "_loadWallet",
                data: walletType
            })
            listScript.push({
                command: "_disableBet",
            });
            listScript.push({
                command: "_updateMatrix",
                data: { matrix: normalSpinMatrix },
            });
            listScript.push({
                command: "_setUpPaylines",
                data: { matrix: normalSpinMatrix, payLines: normalPayLines },
            });
            listScript.push({
                command: "_showBigWild",
                data: { isResume: true }
            })
            // const updatedWinAmount = winAmount - (betValue * currentBonusCredits);
            if (winAmountPS && winAmountPS > 0) {
                listScript.push({
                    command: "_updateWinningAmount",
                    data: { winAmount: winAmountPS, time: 0 }
                });
            }
            if (isBonusGameInNormal) {
                listScript.push({
                    command: "_showBonusPayLine",
                });

                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.IntroBonusGame
                    }
                });

                listScript.push({
                    command: "_newGameMode",
                    data: {
                        name: SlotGameMode.BonusGame,
                        data: bonusGameMatrix
                    },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.NormalGame, },
                });
            }
            if (freeSpinOption && freeSpinOption > 0) {
                listScript.push({
                    command: "_showScatterPayLine",
                });
                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.FreespinOption
                    }
                });
                listScript.push({
                    command: "_newGameMode",
                    data: { name: SlotGameMode.FreeGame, data: normalSpinMatrix, },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.NormalGame, },
                });
            }
            if (isFreeGame || isBonusGameInFree) {
                let freeSpinMatrix = normalSpinMatrix;
                if (freeGameMatrix) {
                    freeSpinMatrix = dataStore.convertMatrix(freeGameMatrix, FREE_TABLE_FORMAT);
                }
                listScript.push({
                    command: "_showScatterPayLine",
                });
                if (isBonusGameInFree) {
                    listScript.push({
                        command: "_showCutscene",
                        data: {
                            name: SlotSceneType.IntroBonusGame
                        }
                    });
                    listScript.push({
                        command: "_newGameMode",
                        data: {
                            name: SlotGameMode.BonusGame,
                            data: bonusGameMatrix
                        },
                    });
                }
                if (freeGameRemain && freeGameRemain > 0) {
                    listScript.push({
                        command: "_showCutscene",
                        data: {
                            name: SlotSceneType.IntroFreeGame
                        }
                    });
                    listScript.push({
                        command: "_newGameMode",
                        data: { name: SlotGameMode.FreeGame, data: freeSpinMatrix, },
                    });
                }

                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.NormalGame, },
                });
            }
            if (normalPayLines && normalPayLines.length > 0) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
            }
            listScript.push({
                command: "_showBigWin",
                data: {
                    name: SlotSceneType.BigWin,
                }
            });

            if (normalPayLines && normalPayLines.length > 0) {
                listScript.push({
                    command: "_showNormalPayline",
                });
            }
            listScript.push({
                command: "_gameFinish"
            });
            listScript.push({
                command: "_gameRestart"
            });

            if (promotion && promotionRemain && promotionTotal && promotionRemain > 0) {
                listScript.push({
                    command: "_showPromotionPopup",
                });
            }

            return listScript;
        }
    }

    makeScriptUpdateWalletData(wallets: any): any {
        const listScript = [];
        listScript.push({
            command: "_updateWallet",
        });
        return listScript;
    }

    makeScriptGameStart() {
        const listScript = [];

        listScript.push({
            command: "_gameStart",
        });
        return listScript;
    }

    makeScriptSwitchMode() {
        const dataStore = (this.node as any).dataStore;
        dataStore.playSession.winAmount = 0;
        const listScript = [];
        listScript.push({
            command: "_forceToClearPaylines"
        });
        listScript.push({
            command: "_updateLastWin",
            data: false
        });
        listScript.push({
            command: "_resetAutoSpin",
        });
        listScript.push({
            command: "_resetFSM",
        });
        listScript.push({
            command: "_clearWinAmount",
        });
        return listScript;
    }

    makeScriptSpinClick(): any {
        let listScript = [];
        const dataStore = (this.node as any).dataStore;
        const { betId } = dataStore.betData;
        const { spinTimes, isAutoSpin, promotion, currentGameMode } = dataStore;
        const { freeGameRemain, freeGame } = dataStore.playSession;
        let availableSpinTimes = 0;
        let promotionRemain, promotionBetId;
        if (promotion) {
            promotionRemain = promotion.promotionRemain;
            promotionBetId = promotion.promotionBetId;
        }

        if (freeGameRemain && freeGameRemain > 0) {
            availableSpinTimes = freeGameRemain - 1;
        } else if (promotion && promotionRemain > 0) {
            availableSpinTimes = promotionRemain - 1;
            listScript.push({
                command: "_updatePromotionRemain",
                data: availableSpinTimes
            });
        }
        else if (isAutoSpin) {
            if (spinTimes && spinTimes > 0) {
                availableSpinTimes = spinTimes - 1;
            }
        } else {
            availableSpinTimes = 0;
        }
        dataStore.spinTimes = availableSpinTimes;

        const canISpin = dataStore.getWalletAfterSpin() >= 0;
        if (canISpin || freeGameRemain > 0 || freeGame > 0 || promotion) {
            listScript.push({
                command: '_disableBet'
            });
            listScript.push({
                command: "_clearPaylines"
            });
            if (currentGameMode === SlotGameMode.NormalGame)
                listScript.push({
                    command: '_clearWinAmount'
                });
            listScript.push({
                command: "_updateSpinTimes",
                data: availableSpinTimes
            });
            if (currentGameMode === SlotGameMode.NormalGame && !promotion) {
                listScript.push({
                    command: "_updateWalletAfterClickSpin",
                });
            }
            listScript.push({
                command: "_pauseWallets",
            });
            listScript.push({
                command: "_pauseTopPlayer",
            });

            if (promotion && !freeGameRemain && !freeGame) {
                listScript.push({
                    command: "_sendSpinToNetwork",
                    data: { currentBetData: promotionBetId }
                });
            } else {
                listScript.push({
                    command: "_sendSpinToNetwork",
                    data: { currentBetData: betId }
                });
            }

            listScript.push({
                command: "_spinClick"
            });
        } else {
            dataStore.spinTimes = 0;
            dataStore.isAutoSpin = false;
            dataStore.isAutoSpinNormal = false;
            listScript = [
                {
                    command: "_enableBet",
                },
                {
                    command: '_resetSpinButton'
                },
                {
                    command: "_showMessageNoMoney",
                },
            ];
        }
        return listScript;
    }

    makeScriptResultReceive(): any {
        const dataStore = (this.node as any).dataStore;
        const { matrix, jackpotInfo, state, extraVipJackpot } = dataStore.playSession;

        let listScript = [];

        if (state == StateGameMode.FreespinOption) {
            listScript.push({
                command: "_showFreeOptionResult",
            });
        } else {
            if (jackpotInfo) {
                let infoJP = jackpotInfo[jackpotInfo.length - 1];
                let arrayJP = infoJP.split(';');
                const jpId = arrayJP[0];
                const jpType = jpId.slice(7);
                const jackpotAmount = Number(arrayJP[1]);

                listScript.push({
                    command: "_pauseUpdateJP"
                });

                listScript.push({
                    command: "_updateValueJP",
                    data: {
                        jpType: jpType,
                        jpValue: jackpotAmount - (extraVipJackpot || 0),
                    }
                });
            }
            listScript.push({
                command: "_resultReceive",
                data: matrix,
            });

            listScript.push({
                command: "_showResult",
                data: matrix,
            });
        }

        return listScript;
    }

    makeScriptShowResults(): any {
        const dataStore = (this.node as any).dataStore;
        const {
            state, matrix, winAmount, winAmountPS, payLines,
            bonusGame, freeGame, freeGameRemain, freeGameTotal, freeSpinOption,
            jackpotInfo, extraJackpot, extraVipJackpot } = dataStore.playSession;
        const { betData } = dataStore;
        const listScript = [];

        listScript.push({
            command: "_setUpPaylines",
            data: { matrix, payLines },
        });

        listScript.push({
            command: "_showBigWild",
            data: { isResume: false }
        });

        //TODO: jackpot
        if (jackpotInfo) {
            let infoJP = jackpotInfo[jackpotInfo.length - 1];
            let arrayJP = infoJP.split(';');
            let jackpotAmount = 0;
            let jpLineId;
            if (arrayJP && arrayJP.length > 2) {
                jackpotAmount = Number(arrayJP[1]);
                jpLineId = arrayJP[2];
            }
            if (jpLineId) {
                listScript.push({
                    command: "_showJackpotPayLine",
                    data: {
                        jpPayLineID: jpLineId,
                    }
                });
            }

            listScript.push({
                command: "_showCutscene",
                data: {
                    name: SlotSceneType.IntroJackpotGame
                }
            });
            listScript.push({
                command: "_showUnskippedCutscene",
                data: {
                    name: SlotSceneType.JackpotWin,
                    content: {
                        winAmount: jackpotAmount,
                        currentBetData: betData.getTotalBet(),
                        extraJackpot,
                        extraVipJackpot
                    }
                }
            });
            listScript.push({
                command: "_resumeUpdateJP",
            });
        }

        if (state == StateGameMode.NormalGame) {
            const { spinTimes } = dataStore;
            if (bonusGame && bonusGame > 0) {
                if (winAmount && winAmount > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                } else {
                    listScript.push({
                        command: '_clearWinAmount'
                    });
                }
                listScript.push({
                    command: "_showBonusPayLine",
                });
                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.IntroBonusGame
                    }
                });
                listScript.push({
                    command: "_newGameMode",
                    data: { name: SlotGameMode.BonusGame, },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.NormalGame, },
                });
                if (!freeGame && spinTimes && spinTimes > 0) {
                    listScript.push({
                        command: "_resumeSpinTime",
                        data: spinTimes,
                    });
                }
            }

            if ((freeSpinOption && freeSpinOption > 0) || (freeGame && freeGame > 0)) {
                const { spinTimes } = dataStore;
                if (!bonusGame) {
                    if (winAmountPS && winAmountPS > 0) {
                        listScript.push({
                            command: '_updateWinningAmount',
                            data: { winAmount: winAmountPS, time: 10 }
                        });
                    } else {
                        listScript.push({
                            command: '_clearWinAmount'
                        });
                    }
                }
                listScript.push({
                    command: "_showScatterPayLine",
                });
                if (freeSpinOption && freeSpinOption > 0) {
                    listScript.push({
                        command: "_showCutscene",
                        data: {
                            name: SlotSceneType.FreespinOption
                        }
                    });
                }
                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.IntroFreeGame
                    }
                });
                listScript.push({
                    command: "_newGameMode",
                    data: { name: SlotGameMode.FreeGame, data: matrix },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.NormalGame, },
                });

                if (spinTimes && spinTimes > 0) {
                    listScript.push({
                        command: "_resumeSpinTime",
                        data: spinTimes,
                    });
                }
            }
            if (payLines && payLines.length > 0) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
            }
            listScript.push({
                command: "_showBigWin",
                data: {
                    name: SlotSceneType.BigWin,
                }
            });

            if (payLines && payLines.length > 0) {
                listScript.push({
                    command: "_showNormalPayline",
                });
            }
            listScript.push({
                command: "_gameFinish"
            });

            listScript.push({
                command: "_gameRestart"
            });
        } else if (state == StateGameMode.FreeGame) {
            if (bonusGame && bonusGame > 0) {
                if (winAmount && winAmount > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                }
                listScript.push({
                    command: "_showBonusPayLine",
                });

                listScript.push({
                    command: "_showCutscene",
                    data: {
                        name: SlotSceneType.IntroBonusGame
                    }
                });

                listScript.push({
                    command: "_newGameMode",
                    data: { name: SlotGameMode.BonusGame, },
                });
                listScript.push({
                    command: "_resumeGameMode",
                    data: { name: SlotGameMode.FreeGame, },
                });
            }

            if (payLines && payLines.length > 0) {
                listScript.push({
                    command: "_blinkAllPaylines",
                });
                listScript.push({
                    command: "_showFreePayline",
                });
            }
            if (!freeGameRemain || freeGameRemain <= 0) {
                if (winAmountPS && winAmountPS > 0) {
                    listScript.push({
                        command: '_updateWinningAmount',
                        data: {
                            winAmount: winAmountPS,
                            time: 300
                        }
                    });
                }
                listScript.push({
                    command: "_delayTimeScript",
                    data: 1
                });
                listScript.push({
                    command: "_showUnskippedCutscene",
                    data: {
                        name: SlotSceneType.TotalWinPanel,
                        content: {
                            winAmount: winAmountPS,
                            freeGameTotal: freeGameTotal,
                        }
                    }
                });
                listScript.push({
                    command: "_clearPaylines",
                });
                listScript.push({
                    command: "_gameExit",
                });
            } else {
                if (freeGameRemain > dataStore.spinTimes) {
                    listScript.push({
                        command: "_showScatterPayLine",
                    });
                    listScript.push({
                        command: "_moveParticles",
                    });
                    listScript.push({
                        command: "_addSpinTimesAnimation",
                        data: freeGameRemain - dataStore.spinTimes
                    });

                    dataStore.spinTimes = freeGameRemain;
                    listScript.push({
                        command: "_updateSpinTimes",
                        data: freeGameRemain
                    });
                    listScript.push({
                        command: "_delayTimeScript",
                        data: 0.5
                    });
                } else {
                    dataStore.spinTimes = freeGameRemain;
                    listScript.push({
                        command: "_updateSpinTimes",
                        data: freeGameRemain
                    });
                }
                listScript.push({
                    command: "_gameRestart"
                });
            }
        }

        return listScript;
    }

    makeScriptFreeOptionResult() {
        const listScript = [];
        listScript.push({
            command: "_hideCutscene",
            data: {
                name: SlotSceneType.FreespinOption
            }
        });
        return listScript;
    };

    makeScriptGameFinish() {
        const dataStore = (this.node as any).dataStore;
        const { winAmountPS } = dataStore.playSession;
        const listScript = [];

        if (dataStore.isTrialMode && winAmountPS) {
            dataStore.setCurrentWallet(dataStore.getCurrentWallet() + winAmountPS);
        }

        return listScript;
    };

    makeScriptSetUpBet(value: number): any {
        return [{
            command: "_updateBet",
            data: value,
        }];
    }

    makeScriptGameRestart(): any {
        const dataStore = (this.node as any).dataStore;
        const listScript = [];
        const { freeGameRemain, respinGameRemain } = dataStore.playSession;
        const { spinTimes, promotion } = dataStore;
        let promotionRemain;
        if (promotion) {
            promotionRemain = promotion.promotionRemain;
        }

        this.scriptUpdateWinAmount(listScript);
        if (promotionRemain && promotionRemain > 0) {
            listScript.push({
                command: "_resetPromotionButtons"
            });
        }

        if (spinTimes && spinTimes > 0) {
            if ((freeGameRemain && freeGameRemain > 0) || (respinGameRemain && respinGameRemain > 0)) {
                listScript.push({
                    command: "_runAutoSpin"
                });
            } else if (!promotion || !promotionRemain) {
                listScript.push({
                    command: "_runAutoSpin"
                });
            }
        } else {
            dataStore.isAutoSpinNormal = false;
            if (!promotionRemain || promotionRemain <= 0) {
                listScript.push({
                    command: '_enableBet'
                });
                listScript.push({
                    command: "_exitPromotionMode"
                });
                listScript.push({
                    command: "_resetSpinButton",
                })
            }

        }
        listScript.push({
            command: "_runAsyncScript",
        });
        return listScript;
    }

    makeScriptSpinByTimes(times: number): any {
        const dataStore = (this.node as any).dataStore;
        dataStore.isAutoSpin = true;
        dataStore.spinTimes = times;
        return [
            {
                command: "_runAutoSpin",
            },
        ];
    }

    makeScriptDisableAutoSpin(): any {
        const dataStore = (this.node as any).dataStore;
        dataStore.isAutoSpin = false;
        dataStore.spinTimes = 0;
        const listScript = [];
        listScript.push({
            command: "_updateSpinTimes",
            data: 0
        });
        return listScript;
    }

    scriptUpdateWinAmount(listScript: {}[]): any {
        const dataStore = (this.node as any).dataStore;
        const { winAmountPS, winAmount, isFinished } = dataStore.playSession;
        const { winAmountQuest, eventInfo } = dataStore.questData;
        const startedShowAnim = dataStore.questData ? dataStore.questData.startedShowAnim : false;
        if (eventInfo && isFinished && !startedShowAnim) {
            if (winAmountQuest) {
                listScript.push({
                    command: "_openPanelQuest"
                });
                listScript.push({
                    command: "_playAnimWinQuest"
                });
                listScript.push({
                    command: "_afterPlayAnimWinQuest"
                });
                dataStore.questData.startedShowAnim = true;
                dataStore.questData.finishedShowAnim = false;
            }
            else {
                console.log('winAmount quest is 0');
            }
        }
        if (winAmount && winAmount > 0) {
            listScript.push({
                command: "_updateWinningAmount",
                data: { winAmount: winAmountPS, time: 300 }
            });
        }
        if (isFinished) {
            listScript.push({
                command: "_resumeWallets"
            })
            listScript.push({
                command: "_resumeTopPlayer"
            })
        }
    }
}


