
import { _decorator, Asset, Node, assetManager, sp, AssetManager, log, error } from 'cc';
import { GameObject } from '../core/GameObject';
import {AssetPathConfig} from './CustomType';
const { ccclass, property } = _decorator;

export class AssetData{
    public asset: Asset;
    public id: number;
    public dynamicPath: string;
    public parentNode: Node;
    constructor (asset:Asset, id:number, path: string, parent: Node){
        this.asset = asset;
        this.id = id;
        this.dynamicPath = path;
        this.parentNode = parent;
    }
}
@ccclass('BaseDynamicLoading')
export class BaseDynamicLoading extends GameObject {
    @property
    dynamicLoading: boolean = false;
    @property({visible: function(this:BaseDynamicLoading){
        return this.dynamicLoading;
    }})
    resourceRootPath: string = "";
    
    @property({type:AssetPathConfig, visible: function(this:BaseDynamicLoading){
        return this.dynamicLoading;
    }})
    assetPaths: AssetPathConfig[] = [];

    protected _loadedAsset: AssetData[] = [];
    protected _localBundle: AssetManager.Bundle = null;
    private _bundlePrefix:string = 'bundle';
    protected _bundlePath: string = "";

    onLoad(){
        super.onLoad();
        this._loadedAsset = [];
        this.setUpAssetBundle();
    }

    clearSingleResourceLoaded(assetData: AssetData){
        if(assetData){
            const index = this._loadedAsset.indexOf(assetData);
            let assets:Asset[] = [];
            let deps = assetManager.dependUtil.getDepsRecursively(assetData.asset._uuid);
            for(let i = 0; i<deps.length; i++){
                const _uuid = deps[i];
                const item = assetManager.assets.get(_uuid);
                if(item){
                    item.addRef();
                    assets.push(item);
                }
            }
            assets.forEach(item => {
                if(item){
                    item.decRef(true);
                    if(item instanceof sp.SkeletonData){
                        item.reset();
                    }
                }
            });
            assetData.asset.decRef(true);
            if(index>-1){
                this._loadedAsset.splice(index, 1);
            }
        }
    }

    clearResourcesLoaded(){
        this._loadedAsset.forEach(assetData =>{
            this.clearSingleResourceLoaded(assetData);
        })
        this._loadedAsset = [];
    }

    protected _dynamicLoadByPath(assetPath:string, assetId: number, parent: Node, completeCB:(asset:Asset)=> void = null) {
        if(!this._localBundle) this._localBundle = assetManager.getBundle(this._bundlePath);
        if(this._localBundle){
            const _asset = this._localBundle.get(this.resourceRootPath + assetPath);
            if(_asset){
                // this.onLoadAssetCompletely(_asset, assetPath, assetId, parent, completeCB);
                completeCB && completeCB(_asset);
            }else{
                this._localBundle.load(this.resourceRootPath + assetPath, (err, asset) => {
                    if (err) {
                        error(`Error while loading asset path: ${assetPath}`);
                    } else {
                        // this.onLoadAssetCompletely(asset, assetPath, assetId, parent, completeCB);
                        completeCB && completeCB(asset);
                    }
                });
            }
        }else{
            log('>>>>> Asset Bundle is not ready');
        }
    }

    onLoadAssetCompletely(asset: Asset, assetPath:string, assetId: number, parent: Node, completeCB:(asset:Asset)=> void = null){
        if (this.node) {
            const assetData:AssetData = new AssetData(asset, assetId, assetPath, parent);
            this._loadedAsset && this._loadedAsset.push(assetData);
            let deps = assetManager.dependUtil.getDepsRecursively(asset._uuid);
            for(let i = 0; i<deps.length; i++){
                const _uuid = deps[i];
                const item = assetManager.assets.get(_uuid);
                if(item && item instanceof sp.SkeletonData){
                    item.getRuntimeData();
                    item.addRef();
                }
            }
            asset.addRef();
            completeCB&&completeCB(asset);
        }else{
            asset.decRef(true);
        }
    }

    getAssetLoadedByPath(path: string):AssetData{
        for(let i = 0; i<this._loadedAsset.length; i++){
            if(path === this._loadedAsset[i].dynamicPath){
                return this._loadedAsset[i];
            }
        }
        return null;
    }

    getAssetLoadedById(id: number): AssetData{
        for(let i = 0; i<this._loadedAsset.length; i++){
            if(id === this._loadedAsset[i].id){
                return this._loadedAsset[i];
            }
        }
        return null;
    };

    setUpAssetBundle() {
        this._bundlePath = this._bundlePrefix + this.config.GAME_ID;
        this._localBundle = assetManager.getBundle(this._bundlePath)
        if (!this._localBundle) {
            assetManager.loadBundle(this._bundlePath, (err, bundle) => {
                if (err) {
                    error(`Error while loading bundle with path: ${this._bundlePath}`);
                } else {
                    this._localBundle = bundle;
                    this.onAssetBundleReady();
                }
            });
        }else{
            this.onAssetBundleReady();
        }
    };

    onAssetBundleReady() {
        // override code here
    };

    onDestroy(){
        this.clearResourcesLoaded();
    }
}