
import { _decorator, Node, CCInteger, UITransform, Event, EventTouch, view, tween, v3, sys } from 'cc';
import { GameObject } from  '../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('Draggable')
export class Draggable extends GameObject {
    deltaX = 0;
    deltaY = 0;
    originalY = 0;
    originalX = 0;

    @property(Node)
    dragHolder: Node = null; // Node area for drag and drop

    @property(Node)
    overlay: Node = null; // Node for dim dragHolder

    @property(CCInteger)
    opacity = 180;

    @property
    stickToBorder = false;

    @property
    borderOffsetX = 30;

    moveTween = null;
    dragging: boolean = false;
    borderLeftX = 0;
    borderRightX = 0;

    onLoad() {
        super.onLoad();
        if (!this.dragHolder) return;

        this.dragHolder.on(Node.EventType.TOUCH_START, this._onDragStart, this);
        this.dragHolder.on(Node.EventType.TOUCH_MOVE, this._onDragMove, this);
        this.dragHolder.on(Node.EventType.TOUCH_END, this._onDragEnd, this);
        this.node.on(Node.EventType.MOUSE_UP, this.moveToBorder, this);
        this.node.on(Node.EventType.TOUCH_CANCEL, this.moveToBorder, this);
        view.on('canvas-resize', this._thisOnResized, this);

        if (this.overlay) {
            this.overlay.on(Node.EventType.TOUCH_END, this._onOverlayTouchEnd, this);
        }

        if (!this.dragHolder.getComponent(UITransform)) {
            this.dragHolder.addComponent(UITransform);
        }
        this.calculateBorder();
        globalThis.multislot = this;
    }

    calculateBorder() {
        let maxWidth = view.getDesignResolutionSize().width;
        if (sys.isNative)
            maxWidth = view.getVisibleSize().width;

        this.borderLeftX = -maxWidth/2 + this.borderOffsetX;
        this.borderRightX = maxWidth/2 - this.borderOffsetX;
    }

    _onDragStart(event: EventTouch) {
        this.dragging = true;
        if (this.overlay) this.overlay.active = true;
        this.originalX = this.dragHolder.position.x;
        this.originalY = this.dragHolder.position.y;
        this.deltaX = event.getLocationX() - this.dragHolder.position.x;
        this.deltaY = event.getLocationY() - this.dragHolder.position.y;
        this.setOpacity(this.dragHolder, 255);
    }

    _onDragMove(event) {
        if (!this.dragging) return;
        this.dragging = true;
        let delta = event.getUIDelta();
        const fullWidth = view.getVisibleSize().width;
        const fullHeight = view.getVisibleSize().height;

        const { width, height } = this.dragHolder.getComponent(UITransform)
        let minX = -fullWidth / 2 + (width / 2);
        let maxX = fullWidth / 2 - (width / 2);
        let minY = -fullHeight / 2 + (height / 2);
        let maxY = fullHeight / 2 - (height / 2);
        let moveX = this.dragHolder.position.x + delta.x;
        let moveY = this.dragHolder.position.y + delta.y;
        if(moveX < minX){
            moveX = minX;
        } else if(moveX > maxX){
            moveX = maxX;
        }
        if(moveY < minY){
            moveY = minY;
        } else if(moveY > maxY){
            moveY = maxY;
        }
        this.dragHolder.setPosition(moveX, moveY);
    }

    _onDragEnd(event) {
        let distance = Math.sqrt(Math.pow(this.dragHolder.position.x - this.originalX, 2) + Math.pow(this.dragHolder.position.y - this.originalY, 2))
        if (distance < 10) {
            this.node.dispatchEvent(new Event('BACK_TO_MULTI', true));
        }
        this.dragging = false;
        if (this.stickToBorder) {
            this.moveToBorder();
        }
    }

    moveToBorder() {
        this.calculateBorder();
        let distanceLeft = Math.abs(this.node.position.x - this.borderLeftX);
        let distanceRight = Math.abs(this.borderRightX - this.node.position.x);
        if (this.moveTween) {
            this.moveTween.stop();
        }
        if (distanceLeft <= distanceRight)
            this.moveToLeft(distanceLeft / 300);
        else
            this.moveToRight(distanceRight / 300);
    }

    easeOutBack(x: number): number {
        const c1 = 1.70158;
        const c3 = c1 + 1;
        
        return 1 + c3 * Math.pow(x - 1, 3) + c1 * Math.pow(x - 1, 2);
    }
        
    moveToLeft(time) {
        let moveTime = _.clamp(time, 0.8, 1.2);
        this.moveTween = tween(this.node)
        .to(moveTime, {position: v3(this.borderLeftX, this.node.position.y, this.node.position.z)}, { easing: t => this.easeOutBack(t)})
        .start();
    }

    moveToRight(time) {
        let moveTime = _.clamp(time, 0.8, 1.2);
        this.moveTween = tween(this.node)
        .to(moveTime, {position: v3(this.borderRightX, this.node.position.y, this.node.position.z)}, { easing: t => this.easeOutBack(t)})
        .start();
    }

    _onOverlayTouchEnd(event) {
        if (this.overlay) this.overlay.active = false;
        this.setOpacity(this.dragHolder, this.opacity);
    }

    onDestroy() {
        if (this.moveTween) {
            this.moveTween.stop();
        }
        if (this.dragHolder) {
            this.dragHolder.off(Node.EventType.TOUCH_START, this._onDragStart, this);
            this.dragHolder.off(Node.EventType.TOUCH_MOVE, this._onDragMove, this);
            this.dragHolder.off(Node.EventType.TOUCH_END, this._onDragEnd, this);
        }
        if (this.overlay) this.overlay.off(Node.EventType.TOUCH_END, this._onDragEnd, this);
    }
}
