
import { _decorator, Component, tween, Tween, TweenEasing, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('DialogDisplayControl')
export class DialogDisplayControl extends Component {
    @property
    scaleEaseType:TweenEasing = 'backOut';
    @property
    duration:number = 0.5;

    _scaleTween:Tween<any> = null;

    onEnable(){
        this.tweenScale();
    }

    onDisable(){
        if(this._scaleTween) this._scaleTween.stop();
        this.node.scale = new Vec3(0.001, 0.001, 0.001);
    }

    tweenScale(){
        if(this._scaleTween) this._scaleTween.stop();
        this.node.scale = new Vec3(0.001, 0.001, 0.001);
        this._scaleTween = tween(this.node)
        .to(this.duration, {scale:Vec3.ONE}, {easing:this.scaleEaseType});
        this._scaleTween.start();
    }
}
