
import { _decorator, Prefab, Node, sp, SpriteFrame, Label, Event, Asset, JsonAsset } from 'cc';
import { SlotSceneType, WalletType, VerticalExpandDirection, HorizontalExpandDirection } from "./CustomEnum";
const { ccclass, property } = _decorator;

@ccclass('PoolPrefab')
export class PoolPrefab {
    @property({ displayName: 'Prefab Name', visible: true })
    prefabName: string = '';

    @property({ type: Prefab, displayName: 'Prefab', visible: true })
    prefab: Prefab | null = null;

    @property({ displayName: 'Initial Count', visible: true })
    initialCount: number = 5;
}

@ccclass('RotatingObject')
export class RotatingObject {
    @property({ type: Node, displayName: 'Node to rotate', visible: true })
    node: Node = null;
    @property({ displayName: 'Rotating Speed' })
    speed: number = 10;
    @property({ displayName: 'Rotating Speed Variance' })
    speedVar: number = 5;
    @property({ displayName: 'Clockwise = 1, Counter Clockwise = -1' })
    clockwise: number = 1;
    @property({ displayName: 'Min Angle' })
    minAngle: number = 0;
    @property({ displayName: 'Max Angle' })
    maxAngle: number = 10;
}

@ccclass('SlotCustomDataType')
export class SlotCustomDataType {
    @property
    spineName: string = '';
    @property({ type: sp.SkeletonData })
    spineData: sp.SkeletonData = null;
}

@ccclass('WalletString')
export class WalletString {
    @property({ type: WalletType })
    walletType = WalletType.NORMAL;

    @property({ type: Label })
    displayString = null;
}



@ccclass('JackpotType')
export class JackpotType {
    @property
    jpName = ''

    @property({ type: SpriteFrame })
    image = null;
}

@ccclass("JackpotObject")
export class JackpotObject {
    @property
    jpType = "";
    @property({ type: Node })
    jpNode: Node = null;
}

@ccclass("ScenePrefab")
export class ScenePrefab {
    @property({ type: SlotSceneType })
    sceneType = SlotSceneType.Other;
    @property
    prefabPath = "";
}
export class CustomEvent extends Event {
    constructor(name: string, bubbles?: boolean, details?: any) {
        super(name, bubbles);
        this.details = details;
    }
    public details: any = null;
}

@ccclass("AssetPathConfig")
export class AssetPathConfig {
    @property
    id: number = -1;
    @property
    path = "";
    @property
    index: number = 0;
    @property(Node)
    parentNode: Node = null;
    @property
    lazyLoadOnWeb: boolean = false;
    @property
    lazyLoadOnNative: boolean = false;
}
@ccclass("ExpandingConfig")
export class ExpandingConfig {
    /**
     * @en
     * Expand Width - Unit is number of collum.
     */
    @property
    expandWidth: number = 200;
    /**
     * @en
     * Expand Height - Unit is number of row.
     */
    @property
    expandHeight: number = 200;
    @property({ type: HorizontalExpandDirection })
    expandDirectionHorizontal: HorizontalExpandDirection = HorizontalExpandDirection.LeftToRight;
    @property({ type: VerticalExpandDirection })
    expandDirectionVertical: VerticalExpandDirection = VerticalExpandDirection.BottomToTop;
}

@ccclass("SpriteFrameSet")
export class SpriteFrameSet {
    @property
    path: string = '';
    @property({ type: SpriteFrame })
    spriteFrame = null;
}

@ccclass("TutorialDataConfig")
export class TutorialDataConfig {
    @property
    currencyCode: string = '';
    @property({ type: JsonAsset })
    tutorialData: JsonAsset = null;
    @property({ type: JsonAsset })
    tutorialSteps: JsonAsset = null;
    @property({ type: JsonAsset })
    tutorialText: JsonAsset = null;
}