/** 
 * @gamble_Game
 * pick one box
 * double or nothing
 */
import { _decorator, Node, Prefab, instantiate, v3, Vec3, Event } from 'cc';
import { SlotGameMode } from '../CustomEnum';
import { script, BaseDirector } from '../../core/BaseDirector'
const { ccclass, property, integer } = _decorator;

@ccclass('GambleDirector')
export class GambleDirector extends BaseDirector {
    @property(Node)
    table: Node;
    @property(Prefab)
    boxPrefab: Prefab;
    @integer
    boxNumber = 3;

    listBoxes: any[] = [];
    listOpenedIndexes: number[] = [];
    isWaitingResult: boolean = false;
    currentPick: number;
    maxOpenBoxes: number = 1;
    remainValues: any[] = [];
    isIdle: boolean = true;
    callbackStateUpdate: any = null;
    _delayTimeCallback: Function = null;
    countDownText = null;
    isClickStopGamble: boolean = false;

    onLoad() {
        // Debug: { window['gamble'] = this; }
        super.onLoad();
        this.setOpacity(this.node, 0);
        this.node.on("GAME_UPDATE", this.stateUpdate, this);
        this.node.on("GAME_ENTER", this.ready, this);
        this.node.on("GAME_SHOW",this.showGame,this);
        this.node.on("GAME_HIDE",this.hideGame,this);
        this.node.on('CLICK_BOX', this.onClickBox, this);
        this.init();
        if(this.mainDirector) this.mainDirector.gameModes[SlotGameMode.GambleGame] = this.node;
    }

    showGame() {
        this.node.active = true;
    }

    hideGame() {
        this.node.active = false;
    }

    init() {
        this.writer = this.node['writer'];
        this.isWaitingResult = false;
        this.node.active = false;
        this.initBoxes();
    }

    initBoxes() {
        for (let index = 0; index < this.boxNumber; index++) {
            let box: any = instantiate(this.boxPrefab);
            box.boxIndex = index;
            box.active = true;
            this.table.addChild(box);
            this.listBoxes.push(box);
            box.setPosition(this.getPosByIndex(index));
        }
    }

    protected getPosByIndex(index: number): Vec3 {
        let WIDTH = 175;
        const x = (index - (this.boxNumber - 1) / 2) * WIDTH;
        return v3(x, 0, 0);
    }

    onClickBox(event: Event) {
        event.propagationStopped = true;
        if (this.isWaitingResult) return;
        if(this.isClickStopGamble) return;

        let box: any = event.target;
        if (this.canOpenBox(box)) {
            this.isIdle = false;
            this.currentPick = box.boxIndex;
            this.runAction("GambleGameClick", this.currentPick);
            this.listOpenedIndexes.push(this.currentPick);
            box.emit("DISABLE");
            box.emit("PLAY_ANIM_CLICK");
        }
    }

    onClickStopGamble(){
        if (this.isWaitingResult) return;
        if(this.isClickStopGamble) return;
        const { gambleRemain } = this.dataStore.playSession;
        if (!gambleRemain) return this._gameExit();
        this.isWaitingResult = true;
        this.isClickStopGamble = true;
        this.mainDirector.gameStateManager.triggerGambleSpinRequest(1, 0);
    }

    canOpenBox(box): boolean {
        if (this.listOpenedIndexes.length >= this.maxOpenBoxes) return false;
        if (this.listOpenedIndexes.indexOf(box.boxIndex) > -1) return false;
        return this.listOpenedIndexes.length < this.maxOpenBoxes;
    }

    resetGambleGame() {
        this.isIdle = true;
        this.currentPick = void 0;
        this.listOpenedIndexes = [];
        this.isClickStopGamble = false;
        this.listBoxes.forEach(box => box.emit("RESET"));
    }

    /**
     * @receive_data
     * */
    stateUpdate(callback: void) {
        this.callbackStateUpdate = callback;
        if (this.isClickStopGamble) {
            this._gameExit();
            return;
        }
        this.runAction('ResultReceive');
    }

    runCallbackStateUpdate() {
        if (typeof this.callbackStateUpdate == "function") {
            this.callbackStateUpdate();
            this.callbackStateUpdate = null;
        }
    }

    ready() {
        this.isWaitingResult = false;
        this.runAction("GambleGameStart");
    }

    _checkPauseTutorial(flag: string) {
        return this.guiMgr.checkPauseTutorial(flag);
    }

    pickOutRandomValue() {
        let randomIndex = Math.floor(Math.random() * this.remainValues.length);
        return this.remainValues.splice(randomIndex, 1)[0];
    }

    /** 
     * @commands
     */
    _enableGambleButton(script: script[], isEnable: boolean) {
        this.eventManager.emit("ENABLE_GAMBLE_BUTTON", isEnable);
        this.executeNextScript(script);
    }
    _delayTimeScript(script: script[], time) {
        this._delayTimeCallback = () => {
            this.executeNextScript(script);
        };
        this.scheduleOnce(this._delayTimeCallback, time);
    }
    _showCutscene(script: script[], { name, content }) {
        if (this._checkPauseTutorial('pauseFreeGame')
            || this._checkPauseTutorial('pauseBonusGame')
            || this._checkPauseTutorial('pauseGambleGame')
        ) {
            this.storeCurrentScripts = '_showCutscene';
            let data = { name, content };
            this.storeNextScripts = {
                script,
                data
            };
        } else {
            this.guiMgr.showCutScene(name, content, () => {
                this.executeNextScript(script);
            });
        }
    }

    _gambleGameStart(scripts: script[]) {
        if (this._checkPauseTutorial('pauseGambleGame')) {
            this.storeCurrentScripts = '_gambleGameStart';
            this.storeNextScripts = {
                scripts,
                data: {},
            };
        } else {
            this.executeNextScript(scripts);
        }
    }

    _sendRequestPlayGamble(scripts: script[], { openCell }) {
        this.isWaitingResult = true;
        const { winAmountPS } = this.dataStore.playSession;
        this.dataStore.setCurrentWallet(this.dataStore.getCurrentWallet() - winAmountPS)
        this.mainDirector.guiMgr.updateWallet(this.dataStore.getWallets());
        this.dataStore.pauseCurrentWallet();
        this.mainDirector.gameStateManager.triggerGambleSpinRequest(openCell, winAmountPS)
        this.executeNextScript(scripts);
    }

    _showResult(script: script[]) {
        this.isWaitingResult = false;
        this.runAction("ShowResult");
        this.executeNextScript(script);
    }

    _openPickedBox(scripts: script[], data) {
        const { index, value } = data;
        const box: any = this.listBoxes[index];
        box.emit("OPEN", value, () => {
            this.executeNextScript(scripts);
        })
    }

    _setRemainValues(scripts: script[], remainValues: any[]) {
        this.remainValues = remainValues;
        this.executeNextScript(scripts)
    }

    _openAllBoxes(scripts: script[]) {
        for (let i = 0; i < this.listBoxes.length; i++) {
            let box: any = this.listBoxes[i];
            if (box.isOpen === false) {
                let randomValue = this.pickOutRandomValue();
                box.emit("STOP_ANIM_CLICK");
                box.emit("OPEN", randomValue, null);
                this.setOpacity(box, 180);
            }
        }
        this.scheduleOnce(() => {
            this.executeNextScript(scripts);
        }, 2);
    }

    _updateWinningAmount(scripts: script[], { winAmount, time }) {
        this.guiMgr.updateWinAmount(winAmount, time / 1000);
        this.executeNextScript(scripts);
    }

    _clearWinAmount(script: script[]) {
        this.guiMgr.fadeOutWinAmount();
        this.executeNextScript(script);
    }

    _showBigWin(script: script[], { name, content }) {
        if (this.canStoreAsyncScript()) {
            const callback = () => {
                this.guiMgr.showCutScene(name, content, () => {
                    this.runAsyncScript()
                });
            };
            this.storeAsyncScript(script, { callback, name: "_showBigWin", isSkippable: true });
        } else {
            this.guiMgr.showCutScene(name, content, () => {
                this.executeNextScript(script);
            });
        }
    }

    _gambleGameRestart(scripts: script[]) {
        this.resetGambleGame();
        this.executeNextScript(scripts);
    }

    _gameExit() {
        this.resetGambleGame();
        this.eventManager.emit("HIDE_GAMBLE_GAME");
        (this.node as any).exit();
    }
}

