
import { _decorator, Component, instantiate, NodePool, Node } from 'cc';
import {PoolPrefab, CustomEvent} from "./CustomType";
const { ccclass, property } = _decorator;

@ccclass('PoolFactory')
export class PoolFactory extends Component {
    @property({type:PoolPrefab, displayName:'Pool Prefab List', visible:true})
    poolPrefabList:Array<PoolPrefab> = [];

    pools :Array<any> = [];

    onLoad(){
        for(let i = 0; i<this.poolPrefabList.length; i++){
            const prefabObject = this.poolPrefabList[i];
            if(prefabObject){
                const prefabName = prefabObject.prefabName;
                const initialCount = prefabObject.initialCount;
                const prefab = prefabObject.prefab;
                
                if(prefabName && initialCount && prefab){
                    const aPool = new NodePool(prefabName);
                    for(let j = 0; j<initialCount; j++){
                        const obj = instantiate(prefab);
                        obj.name = prefabName;
                        obj.active = false;
                        aPool.put(obj);
                    }

                    const poolObject = {
                        prefabName: prefabName,
                        prefab: prefab,
                        pool: aPool,
                    }

                    this.pools[i] = poolObject;
                }
            }
        }

        this.node.on("GET_OBJECT", this.getObject, this);
        this.node.on("REMOVE_OBJECT", this.removeObject, this);
        this.setupInstant();
    }
    
    setupInstant() {
        this.node.dispatchEvent(new CustomEvent("SET_UP_POOL_FACTORY", true, { poolFactory: this }));
    }

    getObject(_prefabName:string):Node{
        let obj:Node = null;
        for(let i = 0; i<this.pools.length; i++){
            const poolObject = this.pools[i];
            if(poolObject){
                const {prefabName, prefab, pool} = poolObject;
                if(prefabName == _prefabName){
                    if(pool.size()>0){
                        obj = pool.get();
                    }else{
                        obj = instantiate(prefab);
                        obj.name = prefabName;
                        obj.active = false;
                    }
                    break;
                }
            }
        }
        return obj;
    }

    removeObject(node:Node){
        let name = node.name;
        for(let i = 0; i<this.pools.length; i++){
            const poolObject = this.pools[i];
            if(poolObject){
                const {prefabName, pool} = this.pools[i];
                if(name == prefabName){
                    node.active = false;
                    pool.put(node);
                    break;
                }
            }
        }
    }

    onDestroy(){
        for(let i = 0; i<this.pools.length; i++){
            const poolObject = this.pools[i];
            if(poolObject){
                const {pool} = poolObject;
                if(pool){
                    pool.clear();
                }
                poolObject.prefab = null;
            }
        }
        this.pools = [];
        this.pools = null;
        this.poolPrefabList = [];
        this.poolPrefabList = null;
        this.node.off("GET_OBJECT", this.getObject, this);
        this.node.off("REMOVE_OBJECT", this.removeObject, this);
    }
}