
import { _decorator, instantiate, Prefab, Node, CCInteger } from 'cc';
import { SlotGameMode, PerformanceLevel } from './CustomEnum';
import { DynamicLoadingPrefabControl } from './DynamicLoadingPrefabControl';
import {AssetPathConfig} from './CustomType';
const { ccclass, property } = _decorator;


export class BigWildObject{
    public col: number;
    public bigWild: Node;
    constructor(_col:number,_bigWild:Node){
        this.col = _col;
        this.bigWild = _bigWild;
    }
}
@ccclass('BigWildTable')
export class BigWildTable extends DynamicLoadingPrefabControl {
    @property({type:SlotGameMode})
    currentGameMode : SlotGameMode = SlotGameMode.NormalGame;
    @property({type:Prefab, visible: function (this: BigWildTable){
        return !this.dynamicLoading;
    }})
    bigWildPrefabs:Prefab[] = [];

    @property({type:AssetPathConfig, visible: function(this:BigWildTable){
        return this.dynamicLoading;
    }})
    highPerfAssetPaths: AssetPathConfig[] = [];

    @property({type:AssetPathConfig, visible: function(this:BigWildTable){
        return this.dynamicLoading;
    }})
    lowPerfAssetPaths: AssetPathConfig[] = [];

    @property({type:CCInteger})
    bigWildCollumnIndexList:number[] = [];

    bigWilds: BigWildObject[] = [];

    _isPlayingHighPerf: boolean = true;
    _hasLastBigWild: boolean = false;
    _isPlayingBigWild: boolean = false;

    onLoad() {
        super.onLoad();
        //this.eventManager.on(EventNames.ON_CHANGE_PERFORMANCE, this.onChangePerformance, this);
    }

    onEnable(){
        this.eventManager.on("INIT_BIG_WILD",this.createBigWilds,this);
        this.eventManager.on("SHOW_BIG_WILD",this.showBigWild,this);
        this.eventManager.on("RESET_BIG_WILD", this.reset, this);
        this.eventManager.on("PLAY_ANIMATION", this.playAnimation, this);
        this.eventManager.on("STOP_ANIMATION", this.stopAnimation, this);
    }

    start(){
        if(this.bigWilds.length==0){
            this.createBigWilds();
        }
    }

    onDisable(){
        this.eventManager.off("INIT_BIG_WILD",this.createBigWilds,this);
        this.eventManager.off("SHOW_BIG_WILD",this.showBigWild,this);
        this.eventManager.off("RESET_BIG_WILD", this.reset, this);
        this.eventManager.off("PLAY_ANIMATION", this.playAnimation, this);
        this.eventManager.off("STOP_ANIMATION", this.stopAnimation, this);
    }

    createBigWilds(callback: Function = null){
        const { SYMBOL_WIDTH, NORMAL_TABLE_FORMAT, FREE_TABLE_FORMAT } = this.config;
        const tableFormat = this.currentGameMode === SlotGameMode.FreeGame? FREE_TABLE_FORMAT : NORMAL_TABLE_FORMAT;
        let startX = -(tableFormat.length / 2 - 0.5) * SYMBOL_WIDTH;
        let count:number = 0;
        if(this.dynamicLoading){
            for(let i = 0; i<this.bigWildCollumnIndexList.length; i++){
                const col = this.bigWildCollumnIndexList[i];
                this.createBigWildByPath(this.highPerfAssetPaths[i], 
                    bigWild=>{
                        if(bigWild){
                            bigWild.name = 'BigWild_Collumn_'+ col;
                            bigWild.parent = this.node;
                            bigWild.setPosition(startX + col*SYMBOL_WIDTH, 0);
                            const bigWildObject = new BigWildObject(col, bigWild);
                            this.bigWilds.push(bigWildObject);
                            this._isPlayingHighPerf = this.isHighPerformance();
                            count++;
                            if(count>=this.bigWildCollumnIndexList.length){
                                callback&&callback();
                            }
                        }
                    });
            }
        }else{
            for(let i = 0; i<this.bigWildCollumnIndexList.length; i++){
                const col = this.bigWildCollumnIndexList[i];
                const bigWild = instantiate(this.bigWildPrefabs[i]);
                if(bigWild){
                    bigWild.name = 'BigWild_Collumn_'+ col;
                    bigWild.parent = this.node;
                    bigWild.setPosition(startX + col*SYMBOL_WIDTH, 0);
                    const bigWildObject = new BigWildObject(col, bigWild);
                    this.bigWilds.push(bigWildObject);
                    this._isPlayingHighPerf = this.isHighPerformance();
                    count++;
                    if(count>=this.bigWildCollumnIndexList.length){
                        callback&&callback();
                    }
                }
            }
        }
    }

    showBigWild(currentMode: SlotGameMode, row:number, col:number, isIdleFirst:boolean = false, playContinuosIdle:boolean = false){
        if(currentMode !== this.currentGameMode) return;
        if(this.bigWilds.length==0){
            this.createBigWilds(()=>{
                for(let i=0; i<this.bigWilds.length; i++){
                    const bigWildObject = this.bigWilds[i];
                    if(bigWildObject && bigWildObject.col == col){
                        bigWildObject.bigWild.emit("PLAY_BIG_WILD", row, col, isIdleFirst, playContinuosIdle);
                    }
                }
            });
        }else{
            for(let i=0; i<this.bigWilds.length; i++){
                const bigWildObject = this.bigWilds[i];
                if(bigWildObject && bigWildObject.col == col){
                    bigWildObject.bigWild.emit("PLAY_BIG_WILD", row, col, isIdleFirst, playContinuosIdle);
                }
            }
        }
        this._isPlayingHighPerf = this.isHighPerformance();
        this._hasLastBigWild = true;
        this._isPlayingBigWild = true;
    }

    playAnimation(currentMode: SlotGameMode, row:number, col:number){
        if(currentMode !== this.currentGameMode) return;
        for(let i=0; i<this.bigWilds.length; i++){
            const bigWildObject = this.bigWilds[i];
            if(bigWildObject && bigWildObject.col == col){
                bigWildObject.bigWild.emit("PLAY_ANIMATION", row);
            }
        }
    }

    stopAnimation(currentMode: SlotGameMode, col:number){
        if(currentMode !== this.currentGameMode) return;
        for(let i=0; i<this.bigWilds.length; i++){
            const bigWildObject = this.bigWilds[i];
            if(bigWildObject && bigWildObject.col == col){
                bigWildObject.bigWild.emit("STOP_ANIMATION");
            }
        }
    }

    reset(currentMode: SlotGameMode){
        if(currentMode !== this.currentGameMode) return;
        for(let i=0; i<this.bigWilds.length; i++){
            const bigWildObject = this.bigWilds[i];
            if(bigWildObject){
                bigWildObject.bigWild.emit("RESET_BIG_WILD");
            }
        }
        this._isPlayingBigWild = false;
        if(this._hasLastBigWild && this.bigWilds.length > 0){
            this.reCreateBigWilds();
        }
        this._hasLastBigWild = false;
    }

    createBigWildByPath(assetPathConfig: AssetPathConfig, completeCB:(_node:Node)=>void = null){
        if(assetPathConfig){
            this._dynamicLoadByPath(assetPathConfig.path, assetPathConfig.id, assetPathConfig.parentNode, (prefab)=>{
                const node:Node = instantiate(prefab as Prefab);
                node.active = true;
                this._usingObjects.push(node);
                completeCB&&completeCB(node);
            });
        }
    }

    reCreateBigWilds(){
        if(this.isHighPerformance()!==this._isPlayingHighPerf){
            this.destroyDynamicLoadingObjects();
            this.bigWilds = [];
            this.createBigWilds();
        }
    }

    onChangePerformance(isHighPer:boolean){
        if(!this._isPlayingBigWild){
            this.reCreateBigWilds();
        }
    }

    isHighPerformance(){
        const performanceLvl = (this.dataStore &&this.dataStore.performanceLevel) || 'HIGH_PERFORMANCE_LV';
        const animationLevel = this.config[performanceLvl].PAYLINE_ANIMATION;
        return animationLevel >= Number(PerformanceLevel.Medium)
    }

    onDestroy() {
        //this.eventManager.off(EventNames.ON_CHANGE_PERFORMANCE, this.onChangePerformance, this);
    }
}