
import { _decorator, SpriteFrame, PageViewIndicator, SpriteComponent } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('CustomPageViewIndicator')
export class CustomPageViewIndicator extends PageViewIndicator {
    @property(SpriteFrame)
    unSelectedSpriteFrame: SpriteFrame = null;

    _changedState() {
        let indicators = this._indicators;
        if (indicators.length === 0) return;
        let idx = (this._pageView as any)._curPageIdx;
        if (idx >= indicators.length) return;
        for (let i = 0; i < indicators.length; ++i) {
            let node = indicators[i];
            if(node){
                let sprite = node.getComponent(SpriteComponent);
                if(sprite){
                    sprite.spriteFrame = this.unSelectedSpriteFrame;
                }
            }
        }
        this.setSpriteFrame({indicators, idx});
    };

    setSpriteFrame({indicators, idx}) {
        let node = indicators[idx];
        if(node){
            let sprite = node.getComponent(SpriteComponent);
            if(sprite){
                sprite.spriteFrame = this.spriteFrame;
            }
        }
    }
}
