import { log } from "cc";

interface IEventInfo {
    inited: Boolean,
    eventTitle: string,
    eventId: string,
    questId: string,
    serverTime: number,
    endTime: number,
    currentIndex: number,
    periodTime: any[],
    progressStatus: number,
    isCurrentGame: Boolean,
    isFinished: Boolean,
    eventType: number
}

interface IQuestProgressItem {
    gameId: string,
    winAmount: Number,
    isFinished: Boolean,
    isValid: Boolean,
    index: Number,
    betIds: string[]
}

interface ITaskData {
    Id: string,
    Des: string,
    ProgressStr: string,
    Progress: Number,
    Finished: Boolean,
    Current: Number,
    Total: Number
}

class QuestData {
    eventInfo: IEventInfo;
    taskData: ITaskData[] = [];
    questProcess: IQuestProgressItem[] = [];
    winAmountQuest: number = 0;
    winAmountEvent: number = 0;
    finishedCurrent: Boolean = false;
    hasUpdate: Boolean = false;
    active: Boolean = false;
    receivedServerFinish: Boolean = false;
    startedShowAnim: Boolean = false;
    finishedShowAnim: Boolean = false;
    initQuestData(data) {
        log(`initQuestData ${data}`)
        let params = data.split(',');
        let questInfo = params[0].split(';');
        let questTime = questInfo[3].split('/');
        let questActive = (questInfo[4] === '1');
        this.eventInfo = {
            inited: true,
            eventTitle: questInfo[0],
            eventId: questInfo[1],
            questId: questInfo[2],
            serverTime: parseInt(questTime[0]),
            endTime: parseInt(questTime[1]),
            currentIndex: 0,
            periodTime: this.parseTimePeriod(questInfo[5]),
            progressStatus: parseInt(questInfo[6]),
            eventType: parseInt(questInfo[7]),
            isCurrentGame: false,
            isFinished: false,
        }
        this.active = questActive;
        this.taskData = this.parseTaskArray((params.slice(2, params.length).join(',')));
        this.questProcess = this.mapQuestData(params[1].split(';'));
        this.eventInfo.isCurrentGame = (this.eventInfo.progressStatus == 1);
        this.finishedCurrent = (this.eventInfo.progressStatus == 2);
        this.receivedServerFinish = false;

        if (!this.finishedCurrent) {
            this.winAmountQuest = 0;
            this.startedShowAnim = false;
        }
    }

    updateQuestData(taskArray) {
        for (let i = 0; i < taskArray.length; i++) {
            let task = this.taskData[i];
            let params = taskArray[i].split(';');
            let progress = params[2].split('/');
            let current = parseInt(progress[0]);
            let total = parseInt(progress[1]);
            task['ProgressStr'] = `${current}/${total}`;
            task['Progress'] = Number(progress[0]) / Number(progress[1]);
            task['Finished'] = (task['Progress'] == 1);
        }
        this.hasUpdate = true;
    }

    getCurrentGame() {
        let result = null;
        if (this.questProcess) {
            this.questProcess.forEach(game => {
                if (game.isValid) {
                    result = game.gameId;
                }
            });
        }
        return result;
    }

    setWinAmount(winAmountQuest, winAmountEvent) {
        if (winAmountQuest) {
            this.winAmountQuest = winAmountQuest;
        }
        if (winAmountEvent) {
            this.winAmountEvent = winAmountEvent;
        }
    }

    resetWinAmount() {
        this.winAmountQuest = 0;
        this.winAmountEvent = 0;
    }

    parseTaskArray(taskArrString: string) : ITaskData[] {
        let result : ITaskData[] = [];
        let taskArray = taskArrString.split(',');
        taskArray.forEach(it => {
            let task = this.parseTask(it);
            result.push(task);
        })
        return result;
    };

    parseTask(taskString: string) : ITaskData {
        let params = taskString.split(';');
        let progressArr = params[2].split('/');
        let current = parseInt(progressArr[0]);
        let total = parseInt(progressArr[1]);
        let progress = Number(progressArr[0]) / Number(progressArr[1]);

        let task: ITaskData;
        task = {
            Id : params[0],
            Des : params[1],
            Current: current,
            Total: total,
            ProgressStr: `${current}/${total}`,
            Progress: progress,
            Finished: (progress == 1)
        }
        return task;
    };

    parseTimePeriod(periodString): any[] {
        let result = [];
        if (periodString != "") {
            let periods = periodString.split("_");
            for (let i=0; i<periods.length; i++) {
                let time = periods[i].split("-");
                let startTime = parseFloat(time[0].replace(":","."));
                let endTime = parseFloat(time[1].replace(":","."));
                result.push([startTime, endTime]);
            }
        }
        return result;
    }

    questComplete(data) {
        let params = data.split(',');
        let questInfo = params[0].split(';');
        this.eventInfo.progressStatus = parseInt(questInfo[6]);
        this.eventInfo.isCurrentGame = (this.eventInfo.progressStatus == 1);
        this.questProcess = this.mapQuestData(params[1].split(';'));
    }

    mapQuestData(data: any): IQuestProgressItem[] {
        let result: IQuestProgressItem[] = [];
        let isFirstQuest = true;
        this.eventInfo.isFinished = true;
        for (let index = 0; index < data.length; index++) {
            const detail = data[index].split('-');
            const isFinished = String(true) === detail[2];
            const isValid = !isFinished && isFirstQuest;
            const betIds = detail[3].split(':');

            if (!isFinished) {
                this.eventInfo.isFinished = false;
            }

            result.push({
                gameId: detail[0],
                winAmount: Number(detail[1]),
                isFinished,
                isValid,
                index,
                betIds
            });
            if (isValid) {
                isFirstQuest = false;
                this.eventInfo.currentIndex = index;
            }
        }
        return result;
    };

    formatTaskData(data) {
        const { eventData: tasks} = data;
        let updatedTask: any = [];
        tasks.forEach(it => {
            updatedTask.push(this.parseTask(it));
        });
        this.taskData = updatedTask;
    };

    reset() {
        this.taskData.forEach(task => {
            task.Finished = false;
            task.Current = 0;
            task.Progress = 0;
            task.ProgressStr = `0/${task.Total}`;
        })
        this.startedShowAnim = false;
        this.finishedShowAnim = false;
    };
}

export {QuestData};