import { _decorator, Button, EventHandler } from 'cc';
import { GameObject } from '../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('ClosePopups')
export class ClosePopups extends GameObject {

    fadeSpeed = 0.2;
    isFading = false;
    onLoad() {
        super.onLoad();
        this.addClickEvent();
    }
    onDisable() {
        this.unscheduleAllCallbacks();
        this.isFading = false;
    }
    addClickEvent() {
        const clickEventHandler = new EventHandler();
        clickEventHandler.target = this.node;
        clickEventHandler.component = 'ClosePopups';
        clickEventHandler.handler = 'closePopups';

        let button = this.node.getComponent(Button);
        if (!button) {
            button = this.node.addComponent(Button)
        }
        button.clickEvents.push(clickEventHandler);
    }
    closePopups() {
        if (this.isFading) return;
        this.isFading = true;
        this.scheduleOnce(() => {
            this.isFading = false;
        }, this.fadeSpeed);
        const gui = this.guiMgr;
        if (!gui) return;
        gui.closePopups();
    }
}

