
import { _decorator, Enum } from 'cc';

export enum NetworkEvent { //send to client

    FAIL_AUTHENTICATION = 0,
    FAIL_CONNECT_SERVER = 1,
    SLOW_CONNECTION = 2,
    SOCKET_LOST_CONNECTION = 3,
    SOCKET_RESUME_CONNECTION = 4,
    RESUME_CONNECTION = 5,
    TIMEOUT_REQUEST_DIE = 6,
    TIMEOUT_REQUEST_WAIT = 7,
    BACKEND_ERROR_CODE = 8,
    WALLET_ERROR_CODE = 9,
    MISMATCH_DATA = 10,
    USER_LOGOUT = 11,
    PROMOTION_ERROR = 12,
    SOCKET_CONNECTED = 13,
}

export enum NetworkState {
    NOT_CONNECT = 0,
    CONNECTED = 1,
    LOST_CONNECTION = 2,
    WAITING_RESPONSE = 3,
    DISCONNECTED = 4
}

Enum(NetworkState);