
import { _decorator, Component, Node, SpriteFrame, Sprite, ProgressBar, tween, Vec3, UITransform } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('ToggleMotion')
export class ToggleMotion extends Component {

    @property(Node)
    progressBar: Node = null;
    @property(Node)
    handle: Node = null;

    @property(SpriteFrame)
    frameOn: SpriteFrame = null;

    @property(SpriteFrame)
    frameOff: SpriteFrame = null;

    status: boolean = false;
    startPos: any;
    endPos: any;
    callBack: any;
    onLoad() {
        if (!this.progressBar.getComponent(UITransform)) {
            this.progressBar.addComponent(UITransform);
        }
        const width = this.progressBar.getComponent(UITransform).width;
        this.startPos = new Vec3((-width / 2) + 25, 0, 0);
        this.endPos = new Vec3((width / 2) - 25, 0, 0)
    }

    setCallBackToggle(callBack: any) {
        this.callBack = callBack;
    }

    onSliderTouchEnd() {
        this.status = !this.status;
        this.progressBar.getComponent(ProgressBar).progress = this.status ? 1 : 0;
        this.status ? this.moveHandle(this.endPos) : this.moveHandle(this.startPos);
        this.callBack && this.callBack();
    }

    moveHandle(pos) {
        tween(this.handle)
            .to(0.18, { position: pos })
            .call(() => {
                this.handle.getComponent(Sprite).spriteFrame = this.status ? this.frameOn : this.frameOff;
            })
            .start();
    }

    setChecked(isChecked = false) {
        this.status = isChecked;
        const pos = this.status ? this.endPos : this.startPos;
        this.handle.getComponent(Sprite).spriteFrame = this.status ? this.frameOn : this.frameOff;
        this.progressBar.getComponent(ProgressBar).progress = this.status ? 1 : 0;
        this.handle.setPosition(pos);
    }

    getChecked() {
        return this.status;
    }

}
