
import { _decorator, Component, Label, v3, tween } from 'cc';
import { setOpacity } from '../../../cc-share/common/utils';
const { ccclass, property } = _decorator;

@ccclass('Toast')
export class Toast extends Component {

    @property({type: Label})
    lblNotify = null;

    @property
    delayTime = 1.5;

    onLoad() {
        this.node.on("SHOW_TOAST_MESSAGE", this.showMessage.bind(this));
        this.node.active = false;
    }

    showMessage(message) {
        this.lblNotify.string = message;
        setOpacity(this.node, 255);
        this.node.active = true;
        this.node.setScale(v3(0.5,0.5,0.5));
        tween(this.node).to(0.2, {scale: v3(1, 1, 1)}).delay(this.delayTime).call(()=>{
            this.node.active = false;
        }).start();
    }

    onDestroy() {
        this.node.off("SHOW_TOAST_MESSAGE", this.showMessage.bind(this));
    }

    hideMessage(){
        this.node.active = false;
    }
}

