import { _decorator, Node, Label } from 'cc';
const { ccclass, property } = _decorator;
import { SlotUIScene } from "./SlotUIScene";

@ccclass('SlotDialog')
export class SlotDialog extends SlotUIScene {
    actionOK: any;
    actionCancel: any;
    
    @property({type: Node})
    gradient = null;

    @property({type: Label})
    message = null;

    @property({type: Node})
    btnOK = null;

    @property({type: Node})
    btnCancel = null;

    @property({type: Node})
    btnHolder = null;

    @property({type: Label})
    titleLabel = null;

    onLoad(): void {
        super.onLoad();
        if (this.titleLabel) {
            this.titleLabel.string = this.config.MESSAGE_DIALOG.NOTICE;
        }
    }

    show() {
        if (!this.node.active)
            super.show();
        const {strText, actionOK = null, actionCancel = null} = this.content;
        this.showMessage(strText, actionOK, actionCancel);
    }

    showMessage(strText: string, actionOK: void, actionCancel: void) {
        if (this.message.string.replace(/\n|\r/g, "") !== strText.replace(/\n|\r/g, "")) {
            this.message.string = strText;
        }
        this.actionOK = actionOK;
        this.actionCancel = actionCancel;
        this.btnOK.active = actionOK != null;
        this.btnCancel.active = actionCancel != null;
        if (this.btnHolder) {
            this.btnHolder.active = this.btnOK.active || this.btnCancel.active;
        }
    }

    isDisplay() {
        return this.node.active;
    }

    onBtnOK() {
        this.playSoundClick();
        this.actionOK();
        this.exit();
    }

    onBtnCancel() {
        this.playSoundClick();
        this.actionCancel();
        this.exit();
    }
}