
import { _decorator, Node, Button, warn } from 'cc';
import { TweenViewComponent } from './TweenViewComponent';
import gameCommonUtils from '../../../../cc-share/common/gameCommonUtils';

const { ccclass, property } = _decorator;


@ccclass('BarMenu')
export class BarMenu extends TweenViewComponent {
    @property(Node)
    exitGameNode : Node = null;

    @property(Node)
    btnSoundOn : Node = null;
    @property(Node)
    btnSoundOff : Node = null;
    @property(Node)
    btnMusicOn : Node = null;
    @property(Node)
    btnMusicOff : Node = null;

    @property(Node)
    overlay : Node = null;

    @property({type : Node})
    borderOverlayList : Node[] = [];

    onLoad() {
        super.onLoad();
        (this.node as any).init = this.init.bind(this);
        this.checkGameInApp();
    }

    init() {
        if(this.soundPlayer){
            if(this.btnSoundOn) 
                this.btnSoundOn.active = this.soundPlayer.isEnableSFX;
            if(this.btnSoundOff)
                this.btnSoundOff.active = !this.soundPlayer.isEnableSFX;
            if(this.btnMusicOn)
                this.btnMusicOn.active = this.soundPlayer.isEnableBGM;
            if(this.btnMusicOff)
                this.btnMusicOff.active = !this.soundPlayer.isEnableBGM;
        }
    }

    show(onStart, onComplete) {
        if(this.overlay) 
            this.overlay.active = true;
        super.show(onStart, ()=>{
            this.activeBorderOverlayList(true);
            this.scheduleOnce(()=>{
                onComplete && onComplete();
            }, 0.5);
            const {isAutoSpin} = this.dataStore;
            const {isFinished} = this.dataStore.playSession;
            if(this.overlay)
                this.overlay.active = (isAutoSpin || (isFinished === false));
        });
    }
    hide(onStart, onComplete) {
        if(this.overlay) 
            this.overlay.active = false;
        super.hide(onStart, ()=>{
            onComplete && onComplete();
            this.activeBorderOverlayList(false);
        });
    }
    clickSoundOn() {
        //turn off sound
        if (this.btnSoundOff) this.btnSoundOff.active = true;
        if (this.btnSoundOn) this.btnSoundOn.active = false;
        if (!this.soundPlayer) {
            warn('[SOUND_EVENT] No sound player found');
            return;
        };
        this.soundPlayer.setEffectEnable(false);
    }
    clickSoundOff() {
        //turn on sound 
        if (this.btnSoundOff) this.btnSoundOff.active = false;
        if (this.btnSoundOn) this.btnSoundOn.active = true;
        if (!this.soundPlayer) {
            warn('[SOUND_EVENT] No sound player found');
            return;
        };
        this.soundPlayer.setEffectEnable(true);
        this.soundPlayer.playSFXClick();
    }
    clickMusicOn() {
        //turn music off
        if (this.btnMusicOff) this.btnMusicOff.active = true;
        if (this.btnMusicOn) this.btnMusicOn.active = false;
        if (!this.soundPlayer) {
            warn('[SOUND_EVENT] No sound player found');
            return;
        }
        this.soundPlayer.setBgmEnable(false);
        this.soundPlayer.playSFXClick();
    }
    clickMusicOff() {
        //turn music on
        if (this.btnMusicOff) this.btnMusicOff.active = false;
        if (this.btnMusicOn) this.btnMusicOn.active = true;
        if (!this.soundPlayer) {
            warn('[SOUND_EVENT] No sound player found');
            return;
        }
        this.soundPlayer.setBgmEnable(true);
        this.soundPlayer.playSFXClick();
    }

    onExitGame(){
        this.soundPlayer && this.soundPlayer.playSFXClick();
        //todo exit game and back to lobby here
    }

    checkGameInApp(){
        if(this.exitGameNode){
            const isEnableBtn = gameCommonUtils.checkConditionCloseGameIframe();
            this.exitGameNode.getComponent(Button).interactable = !!isEnableBtn;
        }
    }

    activeBorderOverlayList(isActive : boolean){
        for(let i = 0; i < this.borderOverlayList.length; i++){
            const borderNode = this.borderOverlayList[i];
            if(borderNode)
                borderNode.active = isActive;
        }
    }
    
}