
import { _decorator, Node, Button, Label, warn } from 'cc';
import serviceRest from '../../../../cc-share/shareServices/serviceRest';
import loadConfigAsync from '../../../../cc-share/shareServices/loadConfigAsync';
import { GameObject } from '../../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('SlotBaseHistory')
export class SlotBaseHistory extends GameObject {

    currentPage = 0;
    totalPage = 0;
    betIds = '';
    gameId = null;
    url = '';
    jpPrefix = '';
    jpList = '';
    token = '';

    @property({ type: Node })
    table = null;

    @property({ type: Node })
    pageIndexView = null;

    @property
    itemPerPage: number = 7;

    @property({ type: Node })
    loading = null;

    @property({ type: Node })
    backBtn = null;

    @property({ type: Node })
    nextBtn = null;

    @property({ type: Button})
    closeButton = null;

    @property({ type: Node })
    errorMessage = null;

    @property
    hasExtraBet = false;

    @property
    isBetHistory = false;

    @property({ type: Node })
    historyTableTitle = null;

    @property({ type: Node })
    historyDetailTitle = null;

    @property({ type: Label })
    pageText = null;

    onLoad() {
        this.initBase();
    }

    initBase() {
        this.currentPage = 1;
        this.totalPage = 1;
        this.stopLoading();
        this.table.getComponent('SlotBaseTableHistory').initCells(this.itemPerPage);
        
        if (this.historyTableTitle) this.historyTableTitle.active = true;
        if (this.historyDetailTitle) this.historyDetailTitle.active = false;
    }

    openPanel() {
        this.node.active = true;
        //TODO set opacity 255
        this.currentPage = 1;
        if (this.currentPage == 1) {
            this.backBtn.getComponent(Button).interactable = false;
            this.nextBtn.getComponent(Button).interactable = false;
        }
        this.pageIndexView.getComponent(Label).string = this.currentPage;
        this.playLoading();
        if (this.isBetHistory) {
            this.requestBetDataPage(this.currentPage, this.itemPerPage, this.onRequestBetResponse.bind(this), this.requestErr.bind(this));
        } else {
            this.requestDataPage(this.currentPage, this.itemPerPage, this.onRequestResponse.bind(this), this.requestErr.bind(this));
        }
    }

    playLoading() {
        this.loading.active = true;
    }

    stopLoading() {
        this.loading.active = false;
    }

    onNextButton() {
        this.playSoundClick();

        this.nextBtn.getComponent(Button).interactable = false;
        this.currentPage += 1;
        this.playLoading();
        if (this.isBetHistory) {
            this.requestBetDataPage(this.currentPage, this.itemPerPage, this.onRequestBetResponse.bind(this), this.requestErr.bind(this));
        } else {
            this.requestDataPage(this.currentPage, this.itemPerPage, this.onRequestResponse.bind(this), this.requestErr.bind(this));
        }
    }

    onPreviousButton() {
        this.playSoundClick();
        if (this.currentPage == 1) return;

        this.backBtn.getComponent(Button).interactable = false;
        this.currentPage -= 1;
        this.playLoading();
        if (this.isBetHistory) {
            this.requestBetDataPage(this.currentPage, this.itemPerPage, this.onRequestBetResponse.bind(this), this.requestErr.bind(this));
        } else {
            this.requestDataPage(this.currentPage, this.itemPerPage, this.onRequestResponse.bind(this), this.requestErr.bind(this));
        }
    }

    playSoundClick() {
        this.soundPlayer.playSFXClick();
    }

    requestDataPage(page, quantity, callback, callbackErr) {
        let from = (page - 1) * quantity;
        let betIds = this.betIds;
        const { IS_SUPPORT_EVENT } = this.config;
        let headers = null;
        if (this.token) {
            headers = Object.create(null);
            headers.Authorization =  this.token;
        }
        let requestParams: Record<string,any> = {
            serviceId: this.jpPrefix + this.gameId,
            from: from,
            size: quantity,
            type: this.jpList,
            betIds: betIds
        }
        if (IS_SUPPORT_EVENT) {
            requestParams.version = "3";
        }
        if (this.errorMessage) this.errorMessage.active = false;
        this.requestHistory(requestParams, callback, callbackErr, headers);
    }

    requestBetDataPage(page, quantity, callback, callbackErr) {
        let from = (page - 1) * quantity;

        let headers = null;
        if (this.token) {
            headers = {
                Authorization: this.token
            };
        }

        let requestParams = {
            serviceId: this.gameId,
            from: from,
            size: quantity
        }
        if (this.errorMessage) this.errorMessage.active = false;
        this.requestHistory(requestParams, callback, callbackErr, headers);
    }

    requestHistory(requestParams = {}, callback, callbackErr, headers = null) {
        const { API_URL } = loadConfigAsync.getConfig();
        if (!this.gameId) {
            warn("GameId has not been set");
            callback({});
            return;
        }
        const currencyCode = this.getCurrencyCode();
        if (currencyCode) {
            requestParams['c'] = currencyCode;
        }
        if (headers) {
            serviceRest.getWithHeader({
                url: this.url,
                params: requestParams,
                callback,
                callbackErr,
                headers,
                apiUrl: API_URL
            })
        }
        else {
            serviceRest.get({
                url: this.url,
                params: requestParams,
                callback,
                callbackErr,
                apiUrl: API_URL
            })
        }
    }

    requestErr() {
        this.stopLoading();
        if (this.errorMessage) {
            this.errorMessage.active = true;
            this.table.emit('CLEAR_DATA');
        }
    }

    onRequestResponse(res) {
        if (res.error) {
            this.requestErr();
            return; //handle error
        }
        if (res.total) {
            this.totalPage = Math.ceil(res.total / this.itemPerPage);
        }
        this.stopLoading();
        if (!res.error && Object.keys(res).length > 0 && res.data && res.data.length > 0) {
            this.nextBtn.getComponent(Button).interactable = true;
            this.backBtn.getComponent(Button).interactable = true;
            this.pageIndexView.getComponent(Label).string = this.currentPage;
            this.table.emit('UPDATE_DATA', res.data)
            if (this.currentPage == 1) {
                this.backBtn.getComponent(Button).interactable = false;
            }
            if (res.total <= this.currentPage * this.itemPerPage || res.data.length < this.itemPerPage) {
                this.nextBtn.getComponent(Button).interactable = false;
                return;
            }
        } else {
            this.table.emit('CLEAR_DATA');
        }
    }

    onRequestBetResponse(res) {
        if (!res || !res.data || res.error) {
            this.requestErr();
            return;
        }

        this.stopLoading();
        if (this._hasBetData(res)) {
            if (res.data.total) {
                this.totalPage = Math.ceil(res.data.total / this.itemPerPage);
            }
            this.nextBtn.getComponent(Button).interactable = true;
            this.backBtn.getComponent(Button).interactable = true;
            this.pageIndexView.getComponent(Label).string = this.currentPage;
            this.table.emit('UPDATE_DATA', res.data.resultList)
            if (this.currentPage == 1) {
                this.backBtn.getComponent(Button).interactable = false;
            }
            if (res.data.total <= this.currentPage * this.itemPerPage || res.data.resultList.length < this.itemPerPage) {
                this.nextBtn.getComponent(Button).interactable = false;
                return;
            }
        } else {
            this.table.emit('CLEAR_DATA');
        }
    }

    protected _hasBetData(res: any) {
        return res.data && res.data.resultList && res.data.resultList.length > 0;
    }

    getCurrencyCode() {
        if (this.dataStore && (this.dataStore as any).currencyCode) {
            return (this.dataStore as any).currencyCode;
        }
        return null;
    }
}
