
import { _decorator, Node, instantiate, v3, Prefab, tween } from 'cc';
import { GameObject } from "../../core/GameObject";
const { ccclass, property } = _decorator;

@ccclass('MiniTable')
export class MiniTable extends GameObject {
    @property
    colNumber: number = 5;
    @property
    rowNumber: number = 3;
    @property
    WIDTH_STEP: number = 250
    @property
    HEIGHT_STEP: number = 250
    @property(Prefab)
    boxPrefab: Prefab;
    @property
    openAllBoxDelay: number = 3;

    listBoxes: Node[] = [];
    listScore: object[] = [];
    startX: number = 0;
    startY: number = 0;

    result: any = null;
    onLoad() {
        super.onLoad();
        this.node.on("INIT_TABLE", this.initBoxes, this);
        this.node.on("OPEN_BOX", this.openBox, this);
        this.node.on("OPEN_ALL_BOXES", this.openAllBoxes, this);
        this.node.on("RESUME_MINI_GAME", this.resumeTable, this);
        this.node.on("RESET_MINI_TABLE", this.resetTable, this);
        this.node.on("AUTO_OPEN_BOX", this.autoClick, this);
        this.startX = -this.colNumber / 2 * this.WIDTH_STEP + this.WIDTH_STEP / 2;
        this.startY = this.rowNumber / 2 * this.HEIGHT_STEP - this.HEIGHT_STEP / 2;
    }

    randRange(min: number, max: number) {
        return Math.floor(Math.random() * (max - min)) + min;
    }

    initBoxes() {
        const maxBox = this.colNumber * this.rowNumber;
        this.listBoxes = [];
        for (let index = 0; index < maxBox; index++) {
            const box = instantiate(this.boxPrefab);
            this.node.addChild(box);
            box.active = true;
            box['boxIndex'] = index;
            this.listBoxes.push(box);
            box.setPosition(this.getPosByIndex(index));
            box.emit('INIT', this.config);
        }
    }

    getPosByIndex(index) {
        let x = this.startX + this.WIDTH_STEP * Math.floor(index / this.rowNumber);
        let y = this.startY - this.HEIGHT_STEP * (index % this.rowNumber);
        return v3(x, y);
    }

    openBox(data, callback?: VoidFunction) {
        const { index, value } = data;
        this.listBoxes[index].emit("BOX_OPEN", value, false, true, callback)
    }

    openAllBoxes(result: any, callback) {
        this.result = result;
        this.updateCurrentMatrix();
        for (let i = 0; i < this.listBoxes.length; i++) {
            if (this.listBoxes[i]["isOpen"] === false) {
                let randValue = this.getRandomValue();
                (this.listBoxes[i] as any).stopAnimClick();
                this.listBoxes[i].emit("BOX_OPEN", randValue, true, false, null);
                this.listBoxes[i].emit("DIM");
            }
        }
        if (callback && typeof callback === 'function') {
            tween(this.node)
                .delay(this.openAllBoxDelay)
                .call(() => {
                    callback();
                })
                .start();
        }
    }

    resumeTable(data: any[], defaultValue: any) {
        if (!data) return;
        for (let i = 0; i < data.length; i++) {
            if (data[i] !== defaultValue) {
                this.listBoxes[i].emit("BOX_OPEN", data[i], true, true);
            }
        }
    }

    resetTable() {
        for (let i = 0; i < this.listBoxes.length; i++) {
            this.listBoxes[i].emit("BOX_RESET");
        }
    }

    autoClick() {
        const index = this.randRange(0, this.listBoxes.length);
        this.listBoxes[index]["isOpen"] ? this.autoClick() : (this.listBoxes[index] as any).onClickItem(null, true);
    }

    updateCurrentMatrix() {
        // todo: implement this func to mini game win Jackpot
        // like BDMN, 4L
    }

    getRandomValue() {
        const boxValue = Object.keys(this.config.BOX_MAP_VALUES);
        return boxValue[Math.floor(Math.random() * boxValue.length)];
    }
}
