
import { _decorator, Node, UITransform, Vec2, Layout, Vec3 } from 'cc';
const { ccclass, property } = _decorator;
import { GameObject } from '../../core/GameObject';
 
@ccclass('TouchPayableSymbol')
export class TouchPayableSymbol extends GameObject {
    @property({ type: Node })
    gradient: Node = null;
    @property({ type: Node })
    infoSymbol: Node = null;
    @property({type: Node})
    touchBlocks: Node [] = [];

    tweenFadeIn = null;

    onLoad() {
        super.onLoad();
        this.node.on("SHOW_INFO_SYMBOL", this.showInfoSymbol, this)
        this.node.on("HIDE_INFO_SYMBOL", this.hideInfoSymbol, this)
        this.infoSymbol.on(Node.EventType.TOUCH_END, this.onInfoSymbolTouchEnded, this);
        this.registerEvent();

        this.gradient['initHeight'] = this.gradient.getComponent(UITransform).height;

        this.node.active = false;
        if (this.touchBlocks && this.touchBlocks.length > 0) {
            for(let i = 0; i < this.touchBlocks.length; i++){
                let touchBlock = this.touchBlocks[i];
                if(touchBlock){
                    touchBlock.on(Node.EventType.TOUCH_START, this.onBlockTouchStarted, this);
                }
            }
        }
    }
    
    registerEvent() {
        if (this.eventManager) {
            this.eventManager.on("SHOW_SYMBOL_PAYTABLE_INFO", this.showInfoSymbol, this);
            this.eventManager.on("HIDE_SYMBOL_PAYTABLE_INFO", this.hideInfoSymbol, this)
        }
    }

    unRegisterAll() {
        if (this.eventManager) {
            this.eventManager.targetOff(this);
        }
    }

    showInfoSymbol(wLocation: Vec3, symbol: Node, spineData, spinneBorder, specialSymbolAnim: string = null) {
        if(this.gradient){
            const pos = this.gradient.parent.getComponent(UITransform).convertToNodeSpaceAR(wLocation);
            const _pos : Vec2 = new Vec2(pos.x, pos.y);
            if(!this.gradient.getComponent(UITransform).getBoundingBox().contains(_pos)){
                return;
            }
        }
        if (this.node.active == false) {
            if (this.node['tweenFade']) {
                this.node['tweenFade'].stop();
                this.node['tweenFade'] = null;
            }
            this.node.active = true;
            this.infoSymbol.active = true;
            this.setOpacity(this.node, 0);
            this.fadeTo(this.node, 0.1, 255, {onComplete: (target)=>{
                if (this.eventManager) {
                    this.eventManager.emit("ON_SHOW_SYMBOL_INFO", true);
                    this.showTouchBlocks();
                }
            }});
        }
        const SYMBOL_SPECIAL = (this.config && this.config.SYMBOL_SPECIAL) || [];
        if (SYMBOL_SPECIAL.indexOf(symbol["symbol"]) !== -1) {
            // this.soundPlayer && this.soundPlayer.playGodTap();
        } else {
            this.soundPlayer && this.soundPlayer.playSFXClick();
        }
        const symbolName = symbol["symbol"];
        const p1 = symbol.parent.getComponent(UITransform).convertToWorldSpaceAR(symbol.getPosition());
        const p2 = this.infoSymbol.parent.getComponent(UITransform).convertToNodeSpaceAR(p1);
        this.infoSymbol.emit("UPDATE_DATA", symbol["symbol"], spineData, spinneBorder, specialSymbolAnim);
        if (symbol["isLeftBorder"]) {
            
            this.infoSymbol.emit("UPDATE_LAYOUT", Layout.HorizontalDirection.LEFT_TO_RIGHT, p2, symbolName);
        } else if (symbol["isRightBorder"]) {
            this.infoSymbol.emit("UPDATE_LAYOUT", Layout.HorizontalDirection.RIGHT_TO_LEFT, p2, symbolName);
        } else if (symbol["isMiddle"]) {
            this.infoSymbol.emit("UPDATE_LAYOUT", Layout.HorizontalDirection.LEFT_TO_RIGHT, p2, symbolName);
        }
        this.gradient.active = true;
    }

    hideInfoSymbol() {
        if (this.node['tweenFade']) {
            this.node['tweenFade'].stop();
            this.node['tweenFade'] = null;
        }
        this.fadeOut(this.node, 0.1, {onComplete: (target)=>{
            if (this.eventManager) {
                this.eventManager.emit("ON_SHOW_SYMBOL_INFO", false)
            }
            this.infoSymbol.emit("RESET_ANIM");
            this.infoSymbol.active = false;
            this.node.active = false;
            this.gradient.active = false;
        }});
    }

    onBlockTouchStarted(evt) {
        this.showTouchBlocks(false);
        this.hideInfoSymbol();
    }

    onInfoSymbolTouchEnded(event) {
        if(event) event.propagationStopped = true;
        this.hideInfoSymbol();
    }

    showTouchBlocks(isOn = true) {
        for(let i = 0; i< this.touchBlocks.length; i++){
            this.touchBlocks[i].active = isOn;
        }
    }

    onDestroy() {
        this.unRegisterAll();
    }
}
