
import {_decorator } from 'cc';
import gameCommonUtils from '../../../cc-share/common/gameCommonUtils';
import loadConfigAsync from '../../../cc-share/shareServices/loadConfigAsync';
import { GameObject } from '../core/GameObject';
const { ccclass, property } = _decorator;

@ccclass('BackToLobby')
export class BackToLobby extends GameObject {    
    isBackToLobby: boolean = false;

    onLoad() {
        super.onLoad();
        const {LOGIN_IFRAME} = loadConfigAsync.getConfig();
        if (LOGIN_IFRAME) {
            const isEnableBtn = gameCommonUtils.checkConditionCloseGameIframe();
            if (!isEnableBtn) {
                const button = this.node.getChildByName('Button');
                if (button) {
                    button.active = false;
                }
                return;
            }
        };
    };

    onEnable() {
        // this.isBackToLobby = false;
    }

    trigger() {
        // if (this.isBackToLobby) return;
        // this.isBackToLobby = true;
        if (this.config.IS_SUPPORT_EVENT && this.mainDirector.totalSlotGame > 1) {
            this.soundPlayer.playSFXClick();
            this.mainDirector.showPopUpCloseGame();
        } else {
            if (this.soundPlayer) {
                this.soundPlayer.playSFXClick();
                this.soundPlayer.stopAllAudio();
            }
            gameCommonUtils.handleCloseGameIframe();
        }
    };
}