
import { tweenMoney } from '../../../cc-share/common/utils';
import { GameObject } from "../core/GameObject"
import { JackpotObject } from './CustomType';
import gameEvent from "../core/CustomEventNames";
import { _decorator, Node, Label, warn } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('Jackpot')
export class Jackpot extends GameObject {
    @property({type: JackpotObject})
    listJackpots: JackpotObject[] = [];
    @property
    isUsingExtraBet: boolean = false;

    jackpots: any = {};
    isInit: boolean = false;
    isPausedJP: boolean = false;
    JP_Prefix: string;
    isJoinGame: boolean = false;
    jackpotData: object = {};

    onLoad() {
        super.onLoad();
        this.node.on("INIT_JACKPOT", this.initJackpot, this); 
        this.node.on("UPDATE_JACKPOT", this.jackpotUpdate, this); 
        this.node.on("CHANGE_JACKPOT", this.changeJackpot, this); // change bet
        this.node.on("PAUSE_JACKPOT", this.pauseRenderJP, this);
        this.node.on("RESUME_JACKPOT", this.resumeRenderJP, this);
        this.node.on("UPDATE_VALUE_JACKPOT", this.updateValueJP, this); // for case win Jackpot

        this.eventManager.on(gameEvent.BET_UPDATE, this.changeJackpot, this);
        this.initData();
    };

    initJackpot(data: object){
        this.isJoinGame = true;
        this.jackpotData = Object.assign({}, data);
        this.renderJackpot(3);
    }

    jackpotUpdate(data: object) {
        Object.assign(this.jackpotData, data);
        this.renderJackpot(3);
    }
    changeJackpot() {
        if(!this.isJoinGame) return;
        this.renderJackpot(.3);
    }
    pauseRenderJP() {
        this.isPausedJP = true;
    };
    resumeRenderJP(data: object = null) {
        if (data) {
            Object.assign(this.jackpotData, data);
        }
        this.isPausedJP = false;
        this.renderJackpot(0.01);
    };
    updateValueJP(jpType: string, jpValue: number) {
        if (this.jackpots[jpType]) {
            const jpObject: JackpotObject = this.jackpots[jpType];
            this.renderJP({ node: jpObject.jpNode, value: jpValue, time: 0.3});
        }else{
            warn("do not have jackpot", jpType);
        }
    };

    onJackpotHistory() {
        this.guiMgr.showJackpotHistory();
    }

    private initData() {
        if (!this.isInit) {
            this.listJackpots.forEach((jpObj: JackpotObject) => {
                this.jackpots[jpObj.jpType] = jpObj;
            })
            this.JP_Prefix = this.config.JP_PREFIX_EVENT;
            this.isInit = true;
        }
    }
    protected renderJackpot(time = 3) {
        if (this.isPausedJP) return;
        const { betKey, extraBetKey } = this.dataStore.betData;
        const betIndex = this.isUsingExtraBet ? extraBetKey : betKey;
        this.listJackpots.forEach((jpObj: JackpotObject) => {
            const value = this.getJackpotValue(jpObj.jpType, betIndex);
            this.renderJP({ node: jpObj.jpNode, value: value, time: time });
        });
    };
    protected getJackpotValue(jpType: string, betIndex: string) {
        let jpId = this.JP_Prefix + betIndex + "_" + jpType;
        const currencyPrefix = this._getCurrencyPrefix();
        if (currencyPrefix) {
            jpId = this.JP_Prefix + betIndex + "_" + currencyPrefix + jpType;
        }

        if (this.jackpotData[jpId]) {
            return this.jackpotData[jpId];
        } else {
            warn("invalid Jackpot Id", jpId, this.jackpotData);
            return null;
        }
    }

    _getCurrencyPrefix() {
        if (!this.dataStore) return;
        const currencyCode = this.dataStore['currencyCode'];
        if (!currencyCode) return;

        let defaultCurrency = this.config.DEFAULT_CURRENCY || 'VND';
        const currencyPrefix = (!currencyCode || defaultCurrency === currencyCode) ? '' : `${currencyCode}_`;
        return currencyPrefix;
    }

    protected renderJP({ node, value, time = 3 }) {
        if (value == null) {
            node.active = false;
            return;
        }
        if (node) {
            let label = node.getComponent(Label);
            if (label){
                tweenMoney(label, time, value);
            }
        }
    }

}