
import { _decorator, Node, Prefab, ParticleSystem2D, Vec3, tween, instantiate, view } from 'cc';
import { setOpacity } from '../../../cc-share/common/utils';
const { ccclass, property, integer } = _decorator;
import { SlotUIScene } from "./../component/SlotUIScene";

@ccclass('CoinsEffect')
export class CoinsEffect extends SlotUIScene {
    duration: number
    moneyPool: Array<any>
    moneyIndex: number
    coinDropperTween: any
    moneyDropperTween: any

    @integer
    public coinSpawnerCount = 3;
    @integer
    public coinPipeDelayTime = 1.3;

    @property({type: Node})
    public coinDropper: Node | null = null;
    @property({type: Node})
    public moneySpawner: Node | null = null;
    @property({type: Prefab})
    public moneyPrefab: Prefab | null = null;
    @property({type: Node})
    public coinPiles: Array<Node> | null = [];

    onLoad() {
        this.initValue();
        this.node.on('START_PARTICLE', this.startParticle, this);
        this.node.on('STOP_PARTICLE', this.stopParticle, this);
        this.node.on('DROP_MONEY', this.randomDropMoney, this);
        this.duration = 10;
    }

    initValue() {
        if(!this.moneyPrefab) return;

        this.moneyPool = [];
        for(let i = 0; i < 20; ++i) {
            const money = instantiate(this.moneyPrefab);
            money.parent = this.moneySpawner;
            setOpacity(money, 0);
            this.moneyPool.push(money);
        }
        setOpacity(this.moneySpawner, 0);
    }

    startParticle() {
        this.coinDropper.getComponent(ParticleSystem2D).resetSystem();
        this.coinPiles.forEach(pile => {
            pile.setPosition(0, -view.getVisibleSize().height / 2 - 20);
            pile.getComponent(ParticleSystem2D).resetSystem();
            const curPos = pile.getPosition();
            tween(pile)
                .to(this.duration, {position: new Vec3(curPos.x + 200, curPos.y + 200, curPos.z)})
                .start();
        });
    }

    stopParticle(){
        this.moneyDropperTween && this.moneyDropperTween.stop();
        this.coinDropper.getComponent(ParticleSystem2D).stopSystem();

        this.coinPiles.forEach(pile => {
            pile.getComponent(ParticleSystem2D).stopSystem();
        });
    }

    randomDropMoney() {
        if (!this.moneyPrefab) return;
        this.moneyIndex = 0;
        setOpacity(this.moneySpawner, 255);

        this.moneyDropperTween = tween(this.moneySpawner).repeatForever(
            tween()
                .delay(0.3)
                .call(() => {
                    this.dropMoney();
                })
        ).start();
    }

    dropMoney(){
        const money = this.moneyPool[this.moneyIndex];
        this.moneyIndex = (this.moneyIndex + 1) % this.moneyPool.length;
        money.x = (Math.random() - 0.5) * view.getVisibleSize().width;
        const randomAnimIdx = Math.random() * 3 | 0 + 1;
        const animName = 'TienRoi' + randomAnimIdx;
        setOpacity(money, 255);
        money.getComponent('sp.Skeleton').setAnimation(0, animName, false);
    }
}
