
import { _decorator, Node, Button, Label } from 'cc';
const { ccclass, property } = _decorator;
import {SlotUIScene} from '../component/SlotUIScene';
import { TutorialTriggerType } from './CustomEnum';
import { setOpacity } from '../../../cc-share/common/utils';

@ccclass('BaseFreespinOption')
export class BaseFreespinOption extends SlotUIScene {
    @property({type: Node})
    public options : Node = null;
    @property({type: Label})
    public countDownText : Label = null;
    @property
    public autoSelectTime : number = 15;
    @property
    public countDownTime : number = 5;

    repeatCountDown: any = null;
    _count: any = null;

    //enable option when open
    enter() {
        this.guiMgr.onIngameEvent(TutorialTriggerType.EnterFreeGameOption,'ON_ENTER_FREEGAME_OPTION');
        this.options.children.forEach((item) => {
            item.getComponent(Button).interactable = true;
            setOpacity(item, 255);
        });
        this.runAutoSelectOption();
        this.soundPlayer && this.soundPlayer.playMusic("BGM_SELECT_OPTION");
    };

    runAutoSelectOption() {
        this._count = this.autoSelectTime;
        if (this.countDownText) {
            this.setOpacity(this.countDownText.node, 255);
            this.countDownText.string = `H thng s t chn sau: ${this._count}s`;
        }
        this.repeatCountDown = () => {
            if (this._count == 0) {
                const randomOption = Math.ceil(Math.random() * this.options.children.length);
                this.optionClick(null, randomOption);
                this.unschedule(this.repeatCountDown);
            }
            this._count--;
            if (this.countDownText) {
                this.countDownText.string = `H thng s t chn sau: ${this._count}s`;
            }
            if (this._count <= this.countDownTime) {
                this.playAudioCountDown();
            }
        };
        this.schedule(this.repeatCountDown, 1);
    };

    //disable option when close
    optionClick(ev, index) {
        this.unschedule(this.repeatCountDown);
        this.soundPlayer && this.soundPlayer.stopAllEffects();
        this.soundPlayer && this.soundPlayer.playSfx("BONUS_COIN_SELECT");
        if (this.countDownText) {
            this.setOpacity(this.countDownText.node, 0);
        }
        this.options.children.forEach((item, i) => {
            item.getComponent(Button).interactable = false;
            if (i + 1 == index) {
                setOpacity(item, 255);
            } else {
                setOpacity(item, 150);

            }
        });
        this.runAnimSelecedOption(index);
    };

    runAnimSelecedOption(option) {
        // to do
        this.guiMgr.onIngameEvent(TutorialTriggerType.FreeGameOptionClick,'FREEGAME_OPTION_SELECT');
        this.mainDirector.freeSpinOptionTrigger(option);
    };

    playAudioCountDown() {
        this.soundPlayer && this.soundPlayer.playSfx("COUNT_DOWN_30S");
    };
}