import { Node, tween, ITweenOption, ImageAsset, SpriteFrame, Texture2D } from "cc";
export function convertSlotMatrix(matrix: string[], format: number[]) {
    let cloneMatrix = matrix.slice();
    let newMatrix = [];
    for (let col = 0; col < format.length; col++) {
        let numRow = format[col];
        let reelResult = cloneMatrix.splice(0, numRow);
        newMatrix.push(reelResult);
    }
    return newMatrix;
}

export function convertPayLine(payLines: any[]): any[] {
    if(!payLines) return null;
    const listNewPL = [];
    for (let i = 0; i < payLines.length; i++) {
        const dataSplit = payLines[i].split(';');
        if (dataSplit.length >= 3) {
            listNewPL.push({
                payLineID: dataSplit[0],
                payLineWinNumbers: parseInt(dataSplit[1]),
                payLineWinAmount: dataSplit[2],
                symbolName: dataSplit[3],
                multiplier: dataSplit[4] ? dataSplit[4] : 1
            });
        }
    }
    return listNewPL;
};

export function convertPayLineAlways(payLines: any[]): any[] {
    if(!payLines) return null;
    const listNewPL = [];
    for (let i = 0; i < payLines.length; i++) {
        const dataSplit = payLines[i].split(';');
        if (dataSplit.length !== 0) {
            listNewPL.push({
                symbolName: dataSplit[0],
                totalWinAmount: dataSplit[1],
                symbolCount: dataSplit[2],
                combination: dataSplit[3],
                payableSymbol: dataSplit[4],
                multiplier: dataSplit[5],
            });
        }
    }
    return listNewPL;
};

/**
 * @only for web
 * 
 * @example
 * exportDataFile({"speed":10}, config.json);
 */
export function exportDataFile(dataStr, fileName: string) {
    let type = fileName.split(".").pop();
    const dataUri = `data:application/${type};charset=utf-8,${encodeURIComponent(dataStr)}`;
    const linkElement = document.createElement('a');
    linkElement.setAttribute('href', dataUri);
    linkElement.setAttribute('download', fileName);
    linkElement.click();
}

/**
 * @only for web
 * 
 * @example
 * importDataFile((result)=> {console.log(result)});
 */
export function importDataFile(callback: (result) => void) {
    const input = window.document.createElement("input");
    input.type = "file";
    document.body.appendChild(input);
    setTimeout(() => {
        input.click();
    }, 1000);

    input.onchange = () => {
        const selectedFile = input.files[0];
        const reader = new FileReader();
        reader.onload = (event: any) => {
            callback(event.target.result);
        };
        reader.readAsText(selectedFile);
    }
}

export function loadSpriteFrameFile(callback: (result) => void) {
    const input = window.document.createElement("input");
    input.type = "file";
    document.body.appendChild(input);
    setTimeout(() => {
        input.click();
    }, 100);

    input.onchange = () => {
        const selectedFile = input.files[0];
        const reader = new FileReader();
        reader.onload = (event: any) => {
            const img = new Image();
            img.src = event.target.result;

            img.onload = () => {
                const spriteFrame = new SpriteFrame();
                const texture = new Texture2D();
                const imageAsset = new ImageAsset(img);
                texture.image = imageAsset;
                spriteFrame.texture = texture;
                spriteFrame.name = selectedFile.name.replace(/\.png$/, "");
                callback && callback(spriteFrame);
            }
        };
        reader.readAsDataURL(selectedFile);
    }
}


const _factoryCache = {};

export function factorial(n) {
    if (_factoryCache[n] !== void 0) return _factoryCache[n];
    _factoryCache[n] = n < 2 ? 1 : n * factorial(n - 1);
    return _factoryCache[n];
}

export function combination(n: number, k: number): number {
    return factorial(n) / factorial(k) / factorial(n - k);
}

export function bezier(pos, t) {
    const n = pos.length - 1, t1 = 1 - t;
    let _val = 0;
    for (let i = 0; i <= n; i++) {
        // Pi * Combine(n,i) * t^i * (1-t)^(n-i)
        _val += pos[i] * combination(n, i) * Math.pow(t, i) * Math.pow(t1, (n - i));
    }
    return _val;
}

export function bezierTo(node: Node, duration, positions, option?: ITweenOption) {
    const _target = { t: 0 };
    let bezierX, bezierY;
    const _options = Object.assign({}, option);
    _options.onStart = () => {
        _target.t = 0;
        const { x, y } = node.position;
        bezierX = [x, ...positions.map(p => p.x)];
        bezierY = [y, ...positions.map(p => p.y)];
        option?.onStart && option?.onStart();
    }
    _options.onUpdate = () => {
        const { t } = _target;
        const x = bezier(bezierX, t);
        const y = bezier(bezierY, t);
        node.setPosition(x, y);
        option?.onUpdate && option?.onUpdate();
    }
    return tween(_target)
        .to(duration, { t: 1 }, _options)
        .start();
}

export function bezierBy(node: Node, duration, positions, option?: ITweenOption) {
    const _target = { t: 0 };
    let bezierX, bezierY;
    const _options = Object.assign(option)
    _options.onStart = () => {
        _target.t = 0;
        const { x, y } = node.position;
        let _x = x, _y = y;
        bezierX = [x, ...positions.map(p => _x += p.x)];
        bezierY = [y, ...positions.map(p => _y += p.y)];
        option?.onStart && option?.onStart();
    }
    _options.onUpdate = () => {
        const { t } = _target;
        const x = bezier(bezierX, t);
        const y = bezier(bezierY, t);
        node.setPosition(x, y);
        option?.onUpdate && option?.onUpdate();
    }
    return tween(_target)
        .to(duration, { t: 1 }, _options)
        .start();
}

    