
import { _decorator, v3 } from 'cc';
const { ccclass, property } = _decorator;


@ccclass('TweenViewConfig')
export class TweenViewConfig {
    // _name: 'TweenViewConfig';
    @property({ displayName: 'fromOpacity', visible: true })
    fromOpacity = 0;
    @property({ displayName: 'toOpacity', visible: true })
    toOpacity = 255;

    @property({ displayName: 'fadeDuration', visible: true })
    fadeDuration = 1;
    @property({ displayName: 'fadeEasing', visible: true })
    fadeEasing = "sineIn";

    @property({ displayName: 'fromPos', visible: true })
    fromPos = v3(0, 0, 0)
    @property({ displayName: 'toPos', visible: true })
    toPos = v3(0, 0, 0);
    @property({ displayName: 'moveDuration', visible: true })
    moveDuration = 1;
    @property({ displayName: 'moveEasing', visible: true })
    moveEasing = "sineIn";
    @property({ displayName: 'fromScale', visible: true })
    fromScale = 1;
    @property({ displayName: 'toScale', visible: true })
    toScale = 2;
    @property({ displayName: 'scaleDuration', visible: true })
    scaleDuration = 1;
    @property({ displayName: 'scaleEasing', visible: true })
    scaleEasing = "sineIn";
    @property({ displayName: 'fromAngle', visible: true })
    fromAngle = 0;
    @property({ displayName: 'toAngle', visible: true })
    toAngle = 180;
    @property({ displayName: 'rotateDuration', visible: true })
    rotateDuration = 1;
    @property({ displayName: 'rotateEasing', visible: true })
    rotateEasing = "sineIn";
    @property({ displayName: 'delayStart', visible: true })
    delayStart = 0;
}
