
import { _decorator, Component, game, view, ResolutionPolicy, Size, sys , settings, Settings} from 'cc';
import { CanvasScaleByOrientation } from './CanvasScaleByOrientation';
const { ccclass, property } = _decorator;
 
@ccclass('ScreenOrientationControl')
export class ScreenOrientationControl extends Component {
   
    @property
    portraitDesignResolution: Size = new Size(720, 1280);
    @property
    landscapeDesignResolution: Size = new Size(1560, 720);
    
    private _canvasScaleComp : CanvasScaleByOrientation;

    start(){
        this._canvasScaleComp = this.getComponent(CanvasScaleByOrientation);
        if(this._canvasScaleComp && game.config && sys.isMobile){
            const orientation = settings.querySettings(Settings.Category.SCREEN, 'orientation') ?? 'auto';
            if(orientation == "portrait"){
                this._canvasScaleComp.isPortrait = true;
                this.setPortraitDS();
            }else{
                this._canvasScaleComp.isPortrait = false;
                this.setLandscapeDS();
            }
            
            this._canvasScaleComp.scaleCanvasByOrientation();
        }
    }

    setPortraitDS(){
        view.setDesignResolutionSize(this.portraitDesignResolution.width, this.portraitDesignResolution.height, ResolutionPolicy.FIXED_WIDTH);
    }

    setLandscapeDS(){
        view.setDesignResolutionSize(this.landscapeDesignResolution.width, this.landscapeDesignResolution.height, ResolutionPolicy.FIXED_HEIGHT);
    }
}


