import { _decorator,  Node, Prefab, instantiate } from 'cc';
import EventNames from '../../scripts/core/EventNames';
import { GameObject } from '../core/GameObject';
const { ccclass, property } = _decorator;
@ccclass('SlotTable')
export class SlotTable extends GameObject {
    @property({type: Node})
    table: Node;
    @property({ type: Prefab })
    reelPrefab;
    @property({ type: Prefab })
    symbolPrefab;
    @property({type: Node})
    stickyWild: Node;
    @property
    isFreeMode: boolean = false;

    tableFormat: any = [];
    matrix: any = [];
    isFastToResult: boolean = false;
    reels: Node[] = [];
    isTurbo: boolean = false;
    stopSpinningCallbackCount: number = 0;
    _tableStopCB = null;
    _tableFastStopCB = null;
    _isSpinning = false;

    // @property({type: Node})
    // private spinBtn: any;
    // @property({type: Node})
    // private fastToResultBtn: any;

    onLoad() {
        super.onLoad();
        this.node.on("INIT",this.init,this);
        this.node.on("START_SPINNING",this.startSpinning,this);
        this.node.on("STOP_SPINNING",this.stopSpinning,this);
        this.node.on("FAST_TO_RESULT",this.fastToResult,this);
        this.node.on("CHANGE_MATRIX",this.changeMatrix,this);
        this.node.on("RANDOM_MATRIX",this.randomMatrix,this);

        this.node.on("STOP_REEL_WITH_RANDOM_MATRIX",this.stopReelWithRandomMatrix,this);
        this.node.on("SHOW_STATIC_SYMBOL",this.showStaticSymbol,this);
        this.node.on("GAME_EXIT", this.gameExit, this);
        this.node.on("GAME_HIDE", this.onGameHide, this);
        this.node.on("GAME_SHOW", this.onGameShow, this);
        this.node['getMatrixResult'] = this.getMatrixResult.bind(this);
    };

    init() {
        const { SYMBOL_WIDTH, NORMAL_TABLE_FORMAT, FREE_TABLE_FORMAT } = this.config;
        this.tableFormat = this.isFreeMode ? FREE_TABLE_FORMAT : NORMAL_TABLE_FORMAT;
        (this.node as any).isFreeMode = this.isFreeMode;
        (this.node as any).tableFormat = this.tableFormat;
        this.isFastToResult = false;
        let startX = -(this.tableFormat.length / 2 - 0.5) * SYMBOL_WIDTH;
        for (let index = 0; index < this.tableFormat.length; index++) {
            const reel = instantiate(this.reelPrefab);
            reel.name = "Reel_" + index;
            this.table.addChild(reel);
            reel.setPosition(startX + index * SYMBOL_WIDTH, 0);
            reel.emit('INIT', this.tableFormat, this.config, index, this.symbolPrefab, this.isFreeMode);
            this.reels.push(reel);
        }
        this.node['reels'] = this.reels;
        if(this.stickyWild) {
            this.stickyWild.emit("INIT", this);
        }
    };

    onGameShow(){
        if(this.table) this.table.active = true;
    }

    onGameHide(){
        if(this.table) this.table.active = false;
    }

    randomMatrix() {
        for (let col = 0; col < this.tableFormat.length; ++col) {
            this.reels[col].emit('RANDOM_SYMBOLS');
        }
    };
    
    changeMatrix({matrix}) {
        this.matrix = matrix;
        for (let col = 0; col < this.tableFormat.length; ++col) {
            this.reels[col].emit('UPDATE_SYMBOLS', this.matrix[col]);
        }
        if(this.stickyWild) {
            this.stickyWild.emit("CHANGE_MATRIX", matrix);
        }
    };
 
    startSpinning() {
        this._isSpinning = true;
        this.isTurbo = (this.dataStore && this.dataStore.modeTurbo) || false;
        this.isFastToResult = false;
        const currentConfig = this.config[this.isTurbo ? 'TABLE_TURBO' : 'TABLE_NORMAL'];
        this.reels.forEach((reel, index) => {
            reel.emit('START_SPIN', index, currentConfig);
        });
        this.node.emit('TABLE_START_SOUND');
        this.node.emit('RESET_NEAR_WIN');
        this.eventManager.emit(EventNames.TABLE_START_SPIN);
    }

    stopSpinning(matrix =  [], callback = null) {
        this.matrix = matrix;
        this._tableStopCB = callback;
        this.node.emit('REEL_DELAY_NEAR_WIN', {reels: this.reels, matrix, context: this });
        this.node.emit('REEL_ABOUT_TO_STOP_SOUND', {reels: this.reels, matrix, context: this });
        this.stopSpinningCallbackCount = 0;
        this.reels.forEach((reel, col) => {
            const revMatrix = [...matrix[col]].reverse();
            reel.emit('STOP_SPIN', revMatrix, this.checkStopSpinningCallback.bind(this, matrix[col]));
        });
    };

    onTableStopped() {
        this.eventManager.emit(EventNames.TABLE_STOP_SPIN);
        if (this._tableStopCB) {
            this._tableStopCB();
            this._tableStopCB = null;
        }
        if (this.isFastToResult) {
            this.eventManager.emit(EventNames.FAST_TO_RESULT_FINISH);
        }
        this._isSpinning = false;
    }

    isSpinning() {
        return this._isSpinning = true;
    }

    checkStopSpinningCallback(matrix = []) {
        this.stopSpinningCallbackCount++;
        const count = this.stopSpinningCallbackCount;
        if (count >= this.reels.length) {
            this.onTableStopped();
        }
        this.node.emit('REEL_STOP_NEARWIN', {matrix, count, context: this});
        this.node.emit('REEL_STOP_SOUND', {matrix, count, context: this});
        if(this.stickyWild) {
            this.stickyWild.emit("SHOW_STICKY_WILD", count - 1);
        }
    };

    stopReelWithRandomMatrix(callback = null) {
        const matrixRandom = [];
        for (let col = 0; col < this.tableFormat.length; ++col) {
            matrixRandom[col] = [];
            for (let row = this.tableFormat[col] - 1; row >=0 ; --row) {
                const reel: any = this.reels[col].getComponent('SlotReel');
                if (reel) {
                    matrixRandom[col][row] = reel.getRandomSymbolNameWithExceptions(['A', 'R', 'K']);
                } else {
                    matrixRandom[col][row] = "3";
                }
            }
        }
        this.stopSpinning(matrixRandom, callback);
    };

    fastToResult() {
        if (!this._isSpinning) return;
        // if (this.isFastToResult) return;
        this.isFastToResult = true;
        this.reels.forEach((reel) => reel.emit('FAST_STOP'));
    }

    showStaticSymbol(col, row, symbol, isShow) {
        if (!this.reels || !this.reels[col]) return;
        this.reels[col].emit('HIDE_SYMBOL', row, symbol, isShow);
    };

    gameExit() {
        if (this.stickyWild) {
            this.stickyWild.emit("RESET");
        }
    };

    getMatrixResult() {
        return this.matrix;
    }
}