
import { _decorator, Node, instantiate, v3, Prefab, tween } from 'cc';
import { fadeTo } from '../../../../cc-share/common/utils';
import { MiniTable } from "./MiniTable";
const { ccclass, property, integer } = _decorator;

@ccclass('ExtendMiniTable')
export class ExtendMiniTable extends MiniTable {

    @property(Prefab)
    extendBoxPrefab: Prefab;
    @property(Node)
    extendBoxHolder: Node;
    @property
    maxExtendBox: number = 3;
    @integer
    extendWidthStep = 150;

    extendBoxes: any[] = [];
    extendValues: number[];
    maxMiniBox: number;
    currentPhase: number = 1;

    onLoad() {
        super.onLoad();
        this.node.on("SHOW_MINI_BOXES", this.showMiniBoxes, this);
        this.node.on("HIDE_MINI_BOXES", this.hideMiniBoxes, this);
        this.node.on("SHOW_EXTEND_BOXES", this.showExtendBoxes, this);
        this.node.on("HIDE_EXTEND_BOXES", this.hideExtendBoxes, this);
    }

    initBoxes() {
        super.initBoxes();
        this.initExtendBoxes();
        this.hideExtendBoxes();
    }

    initExtendBoxes() {
        const maxMiniBox = this.colNumber * this.rowNumber;
        for (let index = 0; index < this.maxExtendBox; index++) {
            const eBox: Node = instantiate(this.extendBoxPrefab);
            this.extendBoxHolder.addChild(eBox);
            eBox.active = true;
            eBox['boxIndex'] = maxMiniBox + index;
            this.extendBoxes.push(eBox);
            eBox.setPosition(this.getPosExtendBox(index));
            eBox.emit('INIT');
        }
    }

    getPosExtendBox(index: number) {
        let x = ((1 - this.maxExtendBox) / 2 + index) * this.extendWidthStep;
        return v3(x, 0);
    }

    showMiniBoxes(callBack: Function = null, time: number = 0) {
        this.currentPhase = 1;
        this.listBoxes.forEach(box => box.emit("SHOW"));
        if (time > 0 && callBack) {
            this.scheduleOnce(callBack, time);
        } else {
            callBack && callBack();
        }
    }
    hideMiniBoxes(callBack: Function = null, time: number = 0) {
        this.listBoxes.forEach(box => box.emit("HIDE"));
        if (time > 0 && callBack) {
            this.scheduleOnce(callBack, time);
        } else {
            callBack && callBack();
        }
    }

    showExtendBoxes(callBack: Function = null, time: number = 0) {
        this.currentPhase = 2;
        this.extendBoxHolder.active = true;
        this.extendBoxes.forEach(eBox => eBox.emit("SHOW"));
        if (time > 0 && callBack) {
            this.scheduleOnce(callBack, time);
        } else {
            callBack && callBack();
        }
    }

    hideExtendBoxes(callBack: Function = null, time: number = 0) {
        this.extendBoxHolder.active = false;
        this.extendBoxes.forEach(eBox => eBox.emit("HIDE"));
        if (time > 0 && callBack) {
            this.scheduleOnce(callBack, time);
        } else {
            callBack && callBack();
        }
    }

    resumeTable(data: number[], defaultValue: number) {
        if(!data) return;
        for (let i = 0; i < this.listBoxes.length; i++) {
            if (data[i] !== defaultValue) {
                this.listBoxes[i].emit("BOX_OPEN", data[i], true, true);
            }
        }
    }

    openBox(data, callback?: VoidFunction) {
        let { index, value } = data;
        if (index < this.listBoxes.length) {
            this.listBoxes[index].emit("BOX_OPEN", value, false, true, callback)
        } else {
            index -= this.listBoxes.length;
            this.extendBoxes[index].emit("BOX_OPEN", value, false, true, callback);
        }
    }

    openAllBoxes(result: any, callback) {
        if (this.currentPhase === 1) {
            super.openAllBoxes(result, callback);
        } else {
            this.result = result;
            for (let i = 0; i < this.extendBoxes.length; i++) {
                const eBox: any = this.extendBoxes[i];
                if (eBox.isOpen === false) {
                    let randValue = this.getRandomValue();
                    eBox.stopAnimClick();
                    eBox.emit("BOX_OPEN", randValue, true, false, null);
                    fadeTo(eBox, 0.5, 0.5);
                }
            }
            if (callback && typeof callback === 'function') {
                tween(this.node)
                    .delay(this.openAllBoxDelay)
                    .call(() => {
                        callback();
                    })
                    .start();
            }
        }
    }

    autoClickExtend() {
        const index = this.randRange(0, this.extendBoxes.length);
        (this.listBoxes[index] as any).onClickItem(null, true)
    }

    autoClick() {
        if (this.currentPhase === 2) {
            const index = this.randRange(0, this.extendBoxes.length);
            this.extendBoxes[index].onClickItem(null, true)
        } else {
            const index = this.randRange(0, this.listBoxes.length);
            this.listBoxes[index]["isOpen"] ? this.autoClick() : (this.listBoxes[index] as any).onClickItem(null, true);
        }
    }

    resetTable() {
        this.listBoxes.forEach(box => box.emit("BOX_RESET"));
        this.extendBoxes.forEach(eBox => eBox.emit("BOX_RESET"));
        this.hideExtendBoxes();
        this.currentPhase = 1;
    }


}
