
import { _decorator, Node, Label, Prefab, NodePool, Vec3, instantiate, CCString, CCInteger, Event } from 'cc';
import { GameObject } from '../core/GameObject';
import { formatMoney } from '../../../cc-share/common/utils';
import { SlotGameMode } from './CustomEnum';

const { ccclass, property } = _decorator;

@ccclass('BaseTableHistoryDetail')
export class BaseTableHistoryDetail extends GameObject {
    @property({ type: Node })
    slotTable: Node = null;
    @property({ type: Node })
    enableHighlightHolder: Node = null;
    @property({ type: Node })
    dimBackground: Node = null;
    @property({ type: Prefab })
    symbolPrefab: Prefab = null;
    @property({ type: Label })
    totalWinAmount: Label = null;
    @property({ type: Node })
    prevPageBtn: Node = null;
    @property({ type: Node })
    nextPageBtn: Node = null;
    @property({ type: SlotGameMode })
    sceneType = SlotGameMode.NormalGame;
    @property
    customSymbolWidth: number = 0;
    @property
    customSymbolHeight: number = 0;
    @property
    symbolScaleX: number = 1;
    @property
    symbolScaleY: number = 1;
    @property
    hasSubSymbol: boolean = false;
    @property
    hasPayline: boolean = false;
    @property
    hasBigWild: boolean = false;
    @property({type:CCInteger, tooltip: "Array of BigW ild Collumn Index on Table from 0",visible: function (this:BaseTableHistoryDetail){
        return this.hasBigWild;
    }})
    bigWildCols:number[] = [];
    @property({type:CCString, tooltip: "Big wild symbol name per col, should be an array of 5 cols", visible: function (this:BaseTableHistoryDetail){
        return this.hasBigWild;
    }})
    bigWildSymbols:string[] = [];// big wild symbol name per col, should be an array of 5 col

    protected symbolPool: any = null;
    protected data: any;
    protected pages: number[] = [];
    protected currentFreeGamePage: number = 0;
    protected currentJackpotPage: number = 0;
    protected currentBonusGamePage: number = 0;
    protected currentTopUpGamePage: number = 0;
    protected currentNormalGamePage: number = 0;
    symbols = [];

    onLoad() {
        super.onLoad();
        this.node.on('UPDATE_DETAIL_VIEW', this.updateDetailView, this);
        this.node.on('RESET_ALL', this.resetAll, this);
        this.node.on('CLEAR_TABLE', this.clearTable, this);
        this.symbolPool = new NodePool('SymbolPool');
        if (this.prevPageBtn) this.prevPageBtn.active = false;
        if (this.nextPageBtn) this.nextPageBtn.active = false;
    }

    updateDetailView(data, pages) {
        this.data = data;
        this.pages = pages;
        this.clearTable();
        this.updateTotalWinAmount(data);
    }

    updateTotalWinAmount(data: any) {
        this.totalWinAmount.string = '0';
        if (this.sceneType === SlotGameMode.JackpotGame) {
            if (data.latestWinJackpotInfo && data.latestWinJackpotInfo.length) {
                this.totalWinAmount.string = formatMoney(parseFloat(data.latestWinJackpotInfo[0].jackpotAmount));
            }
        } else if(this.sceneType === SlotGameMode.FreeGame && data.totalFreeWinAmount!==undefined) {
            this.totalWinAmount.string = formatMoney(parseFloat(data.winAmount));
        } else if(this.sceneType === SlotGameMode.BonusGame && data.totalBonusWinAmount!==undefined) {
            this.totalWinAmount.string = formatMoney(parseFloat(data.winAmount));
        }else if(this.sceneType === SlotGameMode.TopUpGame && data.totalTopUpWinAmount) {
            this.totalWinAmount.string = formatMoney(parseFloat(data.winAmount));
        }else if(this.sceneType === SlotGameMode.NormalGame && data.winAmount!==undefined) {
            this.totalWinAmount.string = formatMoney(parseFloat(data.winAmount));
        }
    }

    renderTable(matrix, format, subSym) {
        if (!matrix) return;
        if(this.dimBackground) this.dimBackground.active = false;
        let symbolWidth = this.sceneType === SlotGameMode.FreeGame && this.config.SYMBOL_WIDTH_FREE_GAME ? this.config.SYMBOL_WIDTH_FREE_GAME : this.config.SYMBOL_WIDTH;
        let symbolHeight = this.sceneType === SlotGameMode.FreeGame && this.config.SYMBOL_HEIGHT_FREE_GAME ? this.config.SYMBOL_HEIGHT_FREE_GAME : this.config.SYMBOL_HEIGHT;
        symbolWidth = this.customSymbolWidth || symbolWidth;
        symbolHeight = this.customSymbolHeight || symbolHeight;

        let startX = (-format.length / 2 + 0.5) * symbolWidth;
        let count = 0;
        for (let col = 0; col < format.length; col++) {
            this.symbols[col] = [];
            let startY = (format[col] / 2 - 0.5) * symbolHeight;
            for (let row = 0; row < format[col]; row++) {
                let symbol = this.getSymbol();
                symbol.parent = this.slotTable;
                symbol.setScale(new Vec3(this.symbolScaleX, this.symbolScaleY, 1));
                symbol.setPosition(startX + col * symbolWidth, startY - row * symbolHeight);
                symbol.emit("INIT_FOR_PAYLINE", this.enableHighlightHolder, null, false);
                symbol.col = col;
                symbol.row = row;
                let symbolValue = matrix[count];
                if(this.hasBigWild){
                    let res = this.bigWildCols.indexOf(col);
                    if(res>=0 && symbolValue === "K"){
                        symbolValue = "";
                        if(row === 1){
                            symbolValue = this.bigWildSymbols[col];
                            symbol.emit('CHANGE_TO_BIG_WILD', symbolValue);
                        }
                    }
                }
                if(symbolValue === '' || symbolValue ===matrix[count]){
                    symbol.emit('CHANGE_TO_SYMBOL',symbolValue);
                }
                symbol.symbolName = matrix[count];
                if (subSym && subSym.length && subSym.indexOf(count) > -1) {
                    this.showSubSymbol(symbol, true);
                }
                this.symbols[col][row] = symbol;
                count++;
            }
        }

        this.showPaylines();
    }

    showPaylines() {
        if (this.hasPayline && this.data.paylines && this.data.paylines.length && this.data.paylines !== '[]') {
            const parsePaylines = this.data.paylines.replace('[', '').replace(']','');
            const paylines = this.dataStore.convertPayLine(parsePaylines.split(','));
            this.showTablePaylines(paylines);
        } else {
            if(this.dimBackground) this.dimBackground.active = false;
        }
    }

    showTablePaylines(paylines: any) {
        if (!paylines|| paylines.length == 0) return;
        this.disableHighlight();
        paylines.forEach(payline => {
            this.showNextPayline(payline);
        });
    }

    disableHighlight() {
        if(this.dimBackground) this.dimBackground.active = true;
    }

    showNextPayline(paylineInfo:any) {
        if(this.config.PAY_LINE_ALLWAYS){
            const{symbolName, symbolCount} = paylineInfo;
            this.slotTable.children.forEach((symbol) => {
                if (((symbol as any).symbolName === symbolName.trim() || (symbol as any).symbolName === "K") && (symbol as any).col < symbolCount) {
                    this.showSymbolHightlight(symbol);
                }
            });
        }else{
            const{payLineID, payLineWinNumbers, symbolName} = paylineInfo;
            let payline = this.config.PAY_LINE_MATRIX[payLineID.trim()];
            if (payline && payline.length && this.symbols.length > 0){
                for (let paylinePos = 0; paylinePos < payLineWinNumbers; ++paylinePos){
                    const row = payline[paylinePos];
                    const col = paylinePos;
                    const symbol = this.symbols[col][row];
                    if(symbol && (symbol.symbolName === symbolName||symbol.symbolName === "K")) this.showSymbolHightlight(symbol);
                }
            }
        }
    }

    showSubSymbol(symbol: any, isActive: boolean) {
        // Custom event show subSymbol
        if (!symbol) return;
        symbol.emit('SHOW_SUB_SYMBOL_STATIC', isActive);
    }

    showSymbolHightlight(symbol: any) {
        if (!symbol) return;
        symbol.emit('ENABLE_HIGHLIGHT', true);
    }

    onPrevPage() {
        let currentPage = 0;
        if (this.sceneType === SlotGameMode.FreeGame) {
            currentPage = --this.currentFreeGamePage;
        } else if (this.sceneType === SlotGameMode.JackpotGame) {
            currentPage = --this.currentJackpotPage;
        }else if (this.sceneType === SlotGameMode.BonusGame) {
            currentPage = --this.currentBonusGamePage;
        }else if (this.sceneType === SlotGameMode.TopUpGame) {
            currentPage = --this.currentTopUpGamePage;
        } else if (this.sceneType === SlotGameMode.NormalGame) {
            currentPage = --this.currentNormalGamePage;
        }
        if (!currentPage) this.prevPageBtn.active = false;
        this.nextPageBtn.active = true;
        this.clearTable();
        this.playClickSound();
        let event = new Event('UPDATE_GAME_DETAIL', true);
        event['userData'] = {currentPage, gameMode: this.sceneType};
        this.node.dispatchEvent(event);
        //if (this.eventManager) this.eventManager.emit('UPDATE_GAME_DETAIL', currentPage, this.sceneType);
    }

    onNextPage() {
        let currentPage = 0;
        if (this.sceneType === SlotGameMode.FreeGame) {
            currentPage = ++this.currentFreeGamePage;
        } else if (this.sceneType === SlotGameMode.JackpotGame) {
            currentPage = ++this.currentJackpotPage;
        }else if (this.sceneType === SlotGameMode.BonusGame) {
            currentPage = ++this.currentBonusGamePage;
        }else if (this.sceneType === SlotGameMode.TopUpGame) {
            currentPage = ++this.currentTopUpGamePage;
        } else if (this.sceneType === SlotGameMode.NormalGame) {
            currentPage = ++this.currentNormalGamePage;
        }
        this.prevPageBtn.active = true;
        if (currentPage === this.pages.length - 1) this.nextPageBtn.active = false;
        this.clearTable();
        this.playClickSound();
        let event = new Event('UPDATE_GAME_DETAIL', true);
        event['userData'] = {currentPage, gameMode: this.sceneType};
        this.node.dispatchEvent(event);
        //if (this.eventManager) this.eventManager.emit('UPDATE_GAME_DETAIL', currentPage, this.sceneType);
    }

    resetUI() {
        if (this.prevPageBtn) this.prevPageBtn.active = false;
        if (this.nextPageBtn) this.nextPageBtn.active = false;
        if(this.totalWinAmount) this.totalWinAmount.string = '0';
        this.clearTable();
    }

    resetAll() {
        this.data = '';
        this.pages = [];
        this.currentFreeGamePage = 0;
        this.currentJackpotPage = 0;
        this.currentBonusGamePage = 0;
        this.currentTopUpGamePage = 0;
        this.currentNormalGamePage = 0;
        this.resetUI();
    }

    clearTable() {
        for(let col = 0; col<this.symbols.length; col++){
            for(let row = 0; row<this.symbols[col].length; row++){
                const symbol = this.symbols[col][row];
                if(symbol){
                    this.resetSymbol(symbol);
                    this.symbolPool.put(symbol);
                }
            }
        }
        this.symbols = [];
    }

    playClickSound(){
        this.soundPlayer && this.soundPlayer.playSFXClick();
    }

    resetSymbol(symbol) {
        symbol.emit('RESET');
    }

    getSymbol() {
        let element = this.symbolPool.get();
        if (!element) element = instantiate(this.symbolPrefab);
        return element;
    }

    onDestroy() {
        super.onDestroy();
        this.symbolPool.clear();
    }
}
