
import { _decorator, Component, Node, Layout, SpriteFrame, sp, Vec3, UITransform, Sprite, CCString } from 'cc';
import { setOpacity } from '../../../../cc-share/common/utils';
import {ExpandingConfig} from '../../component/CustomType';
import {HorizontalExpandDirection, VerticalExpandDirection} from "../../component/CustomEnum";


const { ccclass, property } = _decorator;


@ccclass('InfoPayableSymbol')
export class InfoPayableSymbol extends Component {
    @property({ type: Node })
    frameBg: Node = null;
    @property({ type: Layout })
    layout: Layout = null;
    @property({ type: Node })
    description: Node = null;
    @property({ type: Node })
    payable: Node = null;
    @property({ type: Node })
    spine: Node = null;
    @property({ type: Node })
    border: Node = null;
    @property({ type: SpriteFrame })
    listPayables: SpriteFrame[] = [];
    @property({type: CCString})
    listSymbols: string[] = [];
    @property({type: CCString})
    listSymbolAnimations: string[] = [];
    @property
    borderAnimation: string = "Touch";

    @property
    bigNodeWidth: number = 420;
    @property
    bigDescriptionWidth: number = 240;
    @property
    bigBackgroundWidth: number = 420;
    @property
    bigBackgroundHeight: number = 420;

    @property
    normalNodeWidth: number = 282;
    @property
    normalDescriptionWidth: number = 95;
    @property
    normalBackgroundWidth: number = 300;
    @property
    normalBackgroundHeight: number = 300;
    @property
    normalBackgroundOffsetX: number = 6;

    @property
    normalWidthOffsetRatio: number = 0.2;
    @property
    expandWidthOffsetRatio: number = 0.31;
    @property
    expandHeightOffsetRatio: number = 0.5;


    spineAnim: sp.Skeleton = null;
    borderAnim: sp.Skeleton = null;
    tweenScale = null;
    tweenForever = null;

    onLoad() {
        this.node.on("UPDATE_LAYOUT", this.updateLayout, this);
        this.node.on("UPDATE_DATA", this.updateData, this);
        this.node.on("RESET_ANIM", this.resetAnim, this);
        if(this.spine){
            this.spineAnim = this.spine.getComponent(sp.Skeleton);
        }
        if(this.border){
            this.borderAnim = this.border.getComponent(sp.Skeleton);
        }
    }
    updateLayout(direction = Layout.HorizontalDirection.LEFT_TO_RIGHT, newPos: Vec3, symbolName: string, expandConfig: ExpandingConfig = null) {
        let newX = newPos.x;
        let newY = newPos.y
        setOpacity(this.node, 0);
        this.node.getComponent(UITransform).width = this.normalNodeWidth;
        this.description.getComponent(UITransform).width = this.normalDescriptionWidth;
        this.frameBg.getComponent(UITransform).width = this.normalBackgroundWidth;
        this.frameBg.getComponent(UITransform).height = this.normalBackgroundHeight;
        let _x = direction === Layout.HorizontalDirection.LEFT_TO_RIGHT ? this.normalBackgroundOffsetX : -this.normalBackgroundOffsetX;
        this.frameBg.setPosition(_x, this.frameBg.getPosition().y);

        let widthNode = this.node.getComponent(UITransform).width
        newX = direction === Layout.HorizontalDirection.LEFT_TO_RIGHT ?
            newPos.x + widthNode * this.normalWidthOffsetRatio :
            newPos.x - widthNode * this.normalWidthOffsetRatio;
        if(expandConfig){
            if (expandConfig.expandWidth > 1) {
                this.node.getComponent(UITransform).width = this.bigNodeWidth;
                this.description.getComponent(UITransform).width = this.bigDescriptionWidth;
                this.frameBg.getComponent(UITransform).width = this.bigBackgroundWidth;
                this.frameBg.setPosition(0, this.frameBg.getPosition().y);
                let nodeWidth = this.node.getComponent(UITransform).width;
                if(expandConfig.expandDirectionHorizontal === HorizontalExpandDirection.LeftToRight){
                    newX = newPos.x + nodeWidth * this.expandWidthOffsetRatio;
                }else if(expandConfig.expandDirectionHorizontal === HorizontalExpandDirection.RightToLeft){
                    newX = newPos.x - nodeWidth * this.expandWidthOffsetRatio;
                }
            } 
            if(expandConfig.expandHeight > 1){
                this.frameBg.getComponent(UITransform).height = this.bigBackgroundHeight;
                this.frameBg.setPosition(0, this.frameBg.getPosition().y);
                let nodeHeight = this.node.getComponent(UITransform).height;
                if(expandConfig.expandDirectionVertical === VerticalExpandDirection.BottomToTop){
                    newY = newPos.y + nodeHeight * this.expandHeightOffsetRatio;
                }else if(expandConfig.expandDirectionVertical === VerticalExpandDirection.TopToBottom){
                    newY = newPos.y - nodeHeight * this.expandHeightOffsetRatio;
                }
            }
        }
        

        this.node.setPosition(newX, newY, 0);
        this.layout.horizontalDirection = direction;
        this.layout.updateLayout();
        this.scheduleOnce(() => {
            setOpacity(this.node, 255);
        }, 0.03);
    }
    updateData(symbolName:string = null, spineData = null, spineBorder = null, specialSymbolAnim: string = null) {
        if (!symbolName) {
            return;
        }
        const index = this.listSymbols.indexOf(symbolName);
        let animationName: string = 'animation';
        if (index > -1) {
            this.payable.getComponent(Sprite).spriteFrame = this.listPayables[index];
            animationName = this.listSymbolAnimations[index];
        }

        if(spineData){
            this.spineAnim.skeletonData = spineData;
            if(specialSymbolAnim && specialSymbolAnim !== ""){
                animationName = specialSymbolAnim;
            }
            if (this.spineAnim.findAnimation(animationName)) {
                this.spineAnim.setAnimation(0, animationName, true);
                this.spineAnim.timeScale = 0.5;
            }
        }
        
        if (spineBorder && this.borderAnim) {
            this.borderAnim.skeletonData = spineBorder;
            if (this.borderAnim.findAnimation(this.borderAnimation)) {
                this.borderAnim.setAnimation(0, this.borderAnimation, true);
            }
        }
    }
    resetAnim() {
        if(this.spineAnim)
            this.spineAnim.skeletonData = null;
        if(this.borderAnim)
            this.borderAnim.skeletonData = null;
    }
}